<template>
	<div class="myOrder_view">
		<mt-header class="head" fixed title="我的订单">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<ul class="top_tab">
			<li class="all_Order" @click="selTab"><router-link to="/order?active=0" replace>全部<i></i></router-link></li>
			<li class="no_Payment" @click="selTab"><router-link to="/order?active=1" replace>待付款<i></i></router-link></li>
			<li class="no_Delivery" @click="selTab"><router-link to="/order?active=2" replace>待发货<i></i></router-link></li>
			<li class="no_Goods" @click="selTab"><router-link to="/order?active=3" replace>待收货<i></i></router-link></li>
			<li class="completes" @click="selTab"><router-link to="/order?active=4" replace>已完成<i></i></router-link></li>
		</ul>
		<div v-show="is_show">
			<div class="Order_view" v-show="list.length>0">
				<div class="order_item" v-for="(item,index) in list">
					<p class="create_time">
						创建时间: <span v-text="item.Date"></span>
						<span v-if="item.order_status==2" style="float: right;color: #9d9d9d;font-size: 1.6rem;">待支付</span>
						<span v-if="item.order_status==3" style="float: right;color: #9d9d9d;font-size: 1.6rem;">待发货</span>
						<span v-if="item.order_status==4" style="float: right;color: #9d9d9d;font-size: 1.6rem;">待收货</span>
						<span v-if="item.order_status==5" style="float: right;color: #9d9d9d;font-size: 1.6rem;">已完成</span>
						<span v-if="item.order_status==6" style="float: right;color: #9d9d9d;font-size: 1.6rem;">已取消</span>
						<span v-if="item.order_status==7" style="float: right;color: #9d9d9d;font-size: 1.6rem;">已退款</span>
					</p>
					<div class="goods_content">
						<router-link style="color: #3C3C3C;" :to="{path: 'orderDetails', query: {id: item.order_id}}" v-for="(vo,i) in item.goodList" :key="i">
							<div class="sp_msg">
								<div class="img_box"><img :src="url+vo.goods_img"/></div>
								<div class="text_box">
									<p class="title" v-text="vo.goods_name"></p>
									<p class="argument">{{item.type}}&nbsp;{{vo.spec_key_name}}</p>
									<p class="price"><span>¥{{vo.goods_price}}</span><span>×{{vo.goods_num}}</span></p>
								</div>
							</div>
						</router-link>
						<div class="sp_total">
							共{{item.num}}件商品&nbsp;合计:<span>¥{{item.total_price}}</span>(含运费:¥0)
						</div>
					</div>
					<!--<div class="goods_btn" v-if="item.order_status==7">
						<a href="" style="float: left;">已退款</a>
					</div>-->
					<div class="goods_btn" v-if="item.order_status==6">
						<!--<a href="" style="float: left;">已取消</a>-->
						<a class="close" href="javascript:void(0);" @click="del(index,item.order_id)">删除</a>
					</div>
					<div class="goods_btn" v-if="item.order_status==2">
						<!--<a href="" style="float: left;">待支付</a>-->
						<a class="close" @click="close(index,item.order_id)" href="javascript:void(0);">取消订单</a>
						<a class="pay" @click="submit(item.order_id)" href="javascript:void(0);">支付</a>
					</div>
					<!--<div class="goods_btn" v-if="item.order_status==3">
						<a href="" style="float: left;">待发货</a>
					</div>-->
					<div class="goods_btn" v-if="item.order_status==4">
						<a href="" style="float: left; color: #3C3C3C;">{{item.surplus_days != '已确认收货' ? '剩余'+item.surplus_days+'天自动收货' : '已确认收货'}}</a>
						<router-link class="logistics_two" :to="{path: 'logistics', query: {id: item.order_id}}">查看物流</router-link>
						<a class="the_goods" @click="receiptOrder(index,item.order_id)" href="javascript:void(0);">收货</a>
					</div>
					<div class="goods_btn" v-if="item.order_status==5">
						<router-link class="logistics_two" :to="{path: 'logistics', query: {id: item.order_id}}">查看物流</router-link>
						<a v-if="item.is_service==0" class="after-sales" @click="service(item)">{{item.days?'退车':'还车'}}</a>
						<router-link v-if="item.is_service==1" class="after-sales" :to="{path: 'subsuc', query: {id: item.order_id}}">{{item.days?'退车':'还车'}}</router-link>
						<a v-if="item.iscomment==0" class="evaluation" @click="evaluate(item)">评价</a>
						<a v-else class="evaluation" @click="evaluateOK(item)">已评价</a>
						<!--<router-link v-if="item.iscomment==1" class="evaluation" to="/evaluation">已评价</router-link>-->
					</div>
				</div>
			</div>
			<p class="empty" v-show="list.length==0">您还没有相关的订单</p>

		</div>
	</div>
</template>
<script src="http://res.wx.qq.com/open/js/jweixin-1.0.0.js"></script>
<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {step,unOrder,delOrder,continueOrder,receipt} from '../../api/api';
	export default {
		data(){
			return{
				is_show:false,
				active:0,
				url:'',
				list:[]
			}
		},
		methods:{
			close(i,order_id){
				MessageBox({
					title: '是否删除',
					message: '是否取消该订单？',
					showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						unOrder({order_id:order_id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.list.splice(i,1);
			            	}
		                })
					} 					
				});
			},
			del(i,order_id){
	            MessageBox({
					title: '是否删除',
					message: '是否删除该订单？',
					showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						delOrder({order_id:order_id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.list.splice(i,1);
			            	}
		                })
					} 					
				});

			},
			receiptOrder(i,order_id){
				MessageBox({
					title: '是否删除',
					message: '是否收到宝贝了么？',
					showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						receipt({id:order_id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.list.splice(i,1);
			            	}
		                })
					} 					
				});
			},
			init(){
				this.active = this.$route.query.active;
				$(".top_tab li").eq(this.active).addClass("active");
			},
			selTab(){
				this.active = this.$route.query.active;
				$(".top_tab li").eq(this.active).addClass("active").siblings().removeClass("active");
				this.getList(this.active)	
			},
			getList(status){
				Indicator.open();
				this.is_show=false
	            step({id:status,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.list=[];
	            		this.list=res.data;
	            		this.list.forEach((i)=>{
	            			if(i.type_id == 5){
	            				i.type = '全新车';
	            			}else if(i.type_id == 6){
	            				i.type = '循环车A类';
	            			}else{
	            				i.type = '循环车B类';
	            			}
	            			var time = new Date(i.create_time*1000);
	            			var year = time.getFullYear();
	            			var month = time.getMonth()+1;
	            			var day = time.getDate();
	            			var h = time.getHours();
	            			var m = time.getMinutes();
	            			var s = time.getSeconds();
	            			i.Date = year+'年'+month+'月'+day+'日'+h+':'+m+':'+s;
	            		});
	            	}
	            	this.is_show=true;
                })
			},
			submit(order_id){
				var that=this
	            continueOrder({id:order_id,token:this.getToken()}).then((res)=>{
	            	if(res.code==200){
	            		WeixinJSBridge.invoke(
					       'getBrandWCPayRequest', {
					           "appId":res.data.appId,     //公众号名称，由商户传入     
					           "timeStamp":res.data.timeStamp,         //时间戳，自1970年以来的秒数     
					           "nonceStr":res.data.nonceStr, //随机串     
					           "package":res.data.package,     
					           "signType":res.data.signType,         //微信签名方式：     
					           "paySign":res.data.paySign //微信签名 
					       	},
					       	function(res){ 
					       		console.log(res);
					            if(res.err_msg == "get_brand_wcpay_request:ok" ) {
					            	Toast({message:'支付成功',position:'top'});
				                    that.$router.push({
										path: '/payment',
										query: { 
							                id: order_id, 
							            }
									});
					            } 
					            if(res.err_msg == "get_brand_wcpay_request:cancel" ) {
					            	MessageBox({
										message: '确定取消支付吗？',
										showCancelButton: true,
										closeOnClickModal:false,
									}).then(action => {
										if(action=='confirm'){
											Toast({message:'支付已取消',position:'top'});
							            	that.$router.push({
												path: '/order',
												query: { 
									                active: 1, 
									            }
											});
										} 					
									});
					            	
					            } 
					            if(res.err_msg == "get_brand_wcpay_request:fail" ) {
					                Toast({message:'支付失败',position:'top'});
					                that.$router.push({
										path: '/order',
										query: { 
							                active: 1, 
							            }
									});
					            } 
					       	}
					    )

	            	}
                })
			},
			service(item){
				this.$router.push({
					path: '/service',
					query: { 
		                order_id:item.order_id
		            }
				});

			},
			evaluate(item){
				var title = item.goodList[0].goods_name;
				var spec_key_name = item.type +'/'+ item.goodList[0].spec_key_name;
				var goods_img = item.goodList[0].goods_img;
				var gid = item.goodList[0].gid;
				var order_id = item.order_id;
				this.$router.push({
					path: '/goodsEval',
					query: { 
		                title : title, 
		                spec_key_name : spec_key_name,
		                goods_img : goods_img,
		                gid : gid,
		                order_id : order_id
		            }
				});
			},
            evaluateOK(item){
                var order_id = item.order_id;
                this.$router.push({
                    path: '/evaluation',
                    query: {
                        order_id: order_id
                    }
                });
			}
		},
		mounted(){
			this.init();

		},
		created(){
	    	this.url=this.host()
	    	this.active = this.$route.query.active;
			this.getList(this.active)
		},	
	}
</script>

<style scoped="scoped">
	.myOrder_view{
		padding-top: 9rem;
		padding-bottom: 2rem;
	}
	.top_tab{
		height: 4rem;
		background: #FFFFFF;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		position: fixed;
		top: 5rem;
		left: 0;
		right: 0;
	}
	.top_tab li{
		height: 100%;
	}
	.top_tab li a{
		display: inline-block;
		line-height: 4rem;
		height: 100%;
		color: #3C3C3C;
		font-size: 1.5rem;
		position: relative;
	}
	.top_tab li.active a i{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 0.25rem;
		background: #FF6784;
		border-radius: 0.1rem;
	}
</style>


