<template>
	<div class="reg_view">
		<div class="head_tab">
			<router-link to="/login">登录</router-link>
			<router-link class="active" to="/register">注册</router-link>
		</div>
		<ul class="reg_input">
			<li class="phone_input">
				<i></i><input type="text" v-model="phone" maxlength="11" value="" placeholder="输入手机号码" />
			</li>
			<li class="verifyCode_input">
				<i></i><input class="v_code" v-model="code" maxlength="6" type="text" value="" placeholder="输入短信验证码" /><input @click="send()" type="button" value="获取验证码" v-if="!show"/><button class="custom" v-if="show">重新发送({{time}})秒</button>
			</li>
			<li class="login_pwd_input">
				<i></i><input type="password" v-model="password" value="" placeholder="设置登录密码" />
			</li>
		</ul>
		<button class="reg_btn" @click="regist">立即注册</button>
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {userRegist,sendCode} from '../api/api';
	export default {
		data(){
			return{
				phone:'13512345678',
				code:'',
				password:'',
				time:60,
				show:false,
			}
		},
		methods:{
			send(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				Indicator.open();
				sendCode({phone:this.phone,type:1}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.show=true;
	            		this.custom();
	            	}
                    Toast({message:res.msg,position:'top'});
                })
			},
			custom(){
				var closeTime =""
				closeTime=setInterval(() => {
                	this.time--
                	if(this.time<=0){
						this.time=60;
						this.show=false;
						clearInterval(closeTime);
					}
				}, 1000);
				
			},
			regist(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(this.code==""){
					Toast({message:'请填写验证码',position:'top'});
					$('.verifyCode_input').addClass("on");
					return;

				}
				if(!this.regex('aaa',this.code,/^\d{6}$/)){
					Toast({message:'验证码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(this.password==""){
					Toast({message:'密码不能为空',position:'top'});
					$('.login_pwd_input').addClass("on");
					return;
				}
				Indicator.open();
	            userRegist({phone:this.phone,code:this.code,password:this.password}).then((res)=>{
	            	Indicator.close();
                    if(res.code==200){
                        Toast({message:res.msg,position:'top'});
                        setTimeout(() => {
                        	this.$router.push({
								path: '/'
							});
						}, 2000);
                    }else{
                    	Toast({message:res.msg,position:'top'});
                    }
                })
			}
		},
		mounted() {
			$(function(){
				$("input").focus(function(){
					$(this).parents("li").addClass("on").siblings().removeClass("on");
				});
				$("input").blur(function(){
					$(this).parents("li").removeClass("on");
				});
			})
		}
	}
</script>

<style>
	html,body{
		width: 100%;
		height: 100%;
	}
	/*登录*/
	.reg_view{
		width: 100%;
		height: 100%;
		overflow: hidden;
		background:#FFFFFF url(../assets/img/login_bg@2x.png) left bottom no-repeat;
		background-size: 100% 30%;
	}
	.head_tab{
		margin: 4rem auto 5rem;
		font-size: 1.7rem;
		line-height: 2.2rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
	.head_tab a{
		color: #3C3C3C;
		position: relative;
	}
	.head_tab a.active{
		color: #FF6784;
	}
	.head_tab a.active:after{
		content: "";
		position: absolute;
		width: 1.9rem;
		height: 0.3rem;
		border-radius: 3px;
		background: #FF6784;
		left: 50%;
		transform: translateX(-50%);
		bottom: -0.6rem;
	}
	.reg_input{
		width: 90%;
		margin: 0 auto;	
	}
	.reg_input li{
		border: 1px solid transparent;
		padding: 1rem 0;	
		margin-bottom: 2rem;
		border-radius: 2rem;
	}
	.reg_input li.on{
		border-color: #E8E8E8;
	}
	.reg_input .phone_input,
	.reg_input .login_pwd_input,
	.reg_input .verifyCode_input{
		position: relative;
		height: 2.2rem;
		line-height: 2.2rem;
		padding-left: 4rem;
	}
	.phone_input i,
	.login_pwd_input i,
	.verifyCode_input i{
		position: absolute;
		left: 1rem;
		top: 1rem;
		width: 2.2rem;
		height: 2.2rem;
		background: url(../assets/img/phone_number@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.login_pwd_input i{
		background: url(../assets/img/password@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.verifyCode_input i{
		background: url(../assets/img/verification_code@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.phone_input input,
	.login_pwd_input input,
	.verifyCode_input .v_code{
		width: 98%;
		height: 2.2rem;
		font-size: 1.6rem;
		border: none;
	}
	.verifyCode_input{
		padding-right: 8.5rem;
	}
	.verifyCode_input input[type="button"], .verifyCode_input .custom{
		position: absolute;
		top: 1rem;
		right: 1rem;
		height: 2.2rem;
		color: #FF6784;
		border: none;
		background: transparent;
	}
	.reg_btn{
		display: block;
		width: 86%;
		height: 5rem;
		margin: 0 auto 2.1rem;
		color: #FFFFFF;
		font-size: 1.8rem;
		border: none;
		background-color: rgb(255,87,123);
		border-radius: 2.5rem;
		box-shadow: 0 0.5rem 1rem 0.25rem rgba(255,87,123,0.2);
	}
</style>


