<template>
	<div class="options_view">
		<mt-header class="head" fixed title="设置">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div class="options_list">
			<router-link to="/pwdreset"><div class="options_pw">修改登录密码 <i></i></div></router-link>
			<router-link to="/bindingnum" slot="right" v-if="!phone"><div class="options_pn">绑定手机号<i></i></div></router-link>
			<router-link :to="{path: 'bindingsuc', query: {phone: phone}}" slot="right" v-if="phone"><div class="options_pn">修改手机号<i></i></div></router-link>
			<div class="options_pn" @click="userLogout()">退出登录</div>
		</div>
	</div>
</template>

<script>
	import { Toast,Indicator,MessageBox } from 'mint-ui';
	import {logout} from '../../api/api';
	export default {
		data(){
			return{
				phone :false,
			}
		},
		methods:{
			userLogout(){
				MessageBox({
				  	title: '是否删除',
				  	message: '确定退出吗？',
				  	showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						Indicator.open();
						logout({token:this.getToken()}).then((res)=>{
			            	Indicator.close();
			            	Toast({message:res.msg,position:'top'});
		                    if(res.code==200){
		                    	Lockr.flush();
		                    	setTimeout(() => {
		                        	this.$router.replace({
										path: '/'
									});
								}, 2000);
		                    }
		                })
					} 					
				});
				
			}
		},

		created(){
			this.isLogin()
			this.phone=Lockr.get('phone');
			
		},	
	}
</script>

<style>

	.mint-msgbox{
		border-radius: 1rem
	}
	.mint-msgbox-message{
		color: #f88
	}
	.options_view{
		padding-top: 6rem;
		background: #F7F7F7;
	}
	.options_list{
		background: #FFFFFF;
	}
	.options_list div{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.options_list div{
		color: #3C3C3C;
	}
	.options_list div i{
		position: absolute;
		width: 0.7rem;
		height: 1.3rem;
		top: 50%;
		right: 1.2rem;
		transform: translateY(-50%);
		background: url(../../assets/img/next@2x.png);
		background-size: 100% 100%;
	}
</style>


