<template>
	<div class="authentication_view">
		<mt-header class="head" fixed title="身份验证">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span v-if="status!=3&&status!=1" slot="right" @click="submit()">提交</span>
		</mt-header>
		<div class="authentication_explain">
      		<p v-if="status==4" class="hint">请上传本人身份证照片，进行人工审核</p>
      		<p v-else-if="status==3" class="hint">实名信息审核中</p>
			<p v-else-if="status==1" class="hint">恭喜您，您已经通过实名认证</p>
			<p v-else-if="status==2" class="hint">抱歉，您的实名认证没有通过！请重新申请</p>
      		<p v-else></p>
			<div class="photo_box frontimg">
				<label v-if="!front">
					<input  style="display: none;" type="file" id="file" accept="image/*" />
					<img src="../../assets/img/photo_icon@2x.png" />
					<p>+手持身份证正面照</p>
				</label>
				<label v-else style='width:100%;height:100%;display:block;'>
					<input v-if="status!=1&&status!=3" style='display: none;' type='file' accept="image/*" />
					<img style='width:100%;height:100%;margin:0' :src="url+'/v1/thumb?src='+url+front+'&w=200&h=200'" />
				</label>
			</div>
			<div class="photo_box backimg">
				<label v-if="!backimg">
					<input style="display: none;" type="file" id="file" accept="image/*"/>
					<img src="../../assets/img/photo_icon@2x.png" />
					<p>+手持身份证反面照</p>
				</label>
				<label v-else style='width:100%;height:100%;display:block;'>
					<input v-if="status!=3&&status!=1" accept="image/*" style='display: none;' type='file' />
					<img style='width:100%;height:100%;margin:0' :src="url+'/v1/thumb?src='+url+backimg+'&w=200&h=200'" />
				</label>
			</div>
		</div>
		<div class="afterservice_list">
			<div class="afterservice_money">真实姓名：<input type="text" :readonly="status==3||status==1?true:false" v-model="realname" value="" maxlength="10" placeholder="请输入真实姓名"/></div>
			<div class="afterservice_money">身份证号：<input type="text" :readonly="status==3||status==1?true:false" v-model="id_card" maxlength="18" value="" placeholder="请输入身份证号" oninput="if(value.length>18)value=value.slice(0,18)" /></div>
		</div>
	</div>
</template>
<script>
	import EXIF from 'exif-js'; 
	import { Toast,Indicator } from 'mint-ui';
	import {identity,identityInfo} from '../../api/api';
	export default {
		data(){
			return{
				realname:'',
				id_card:'',
				front:'',
				backimg:'',
				status:4,//未申请
				url:'',
			}
		},
		methods:{
			onUploadFront(e){
				Indicator.open('上传中...');
				this.imageLoad(e,1);

			},
			onUploadBack(e){
				Indicator.open('上传中...');
				this.imageLoad(e,0);
			},
			imageLoad(e,i){
				var th = this;
				var url=this.host()+'/v1/base64ToImg';
				var images = new Image();
				var canvas = document.createElement('canvas');
				var context = canvas.getContext('2d');
		        var reader = new FileReader();
		        var file = event.target.files[0];
		        var orientation='';
		        var degree=0;
		        EXIF.getData(file, function() {    
                   	orientation = EXIF.getTag(this, 'Orientation');  
                }); 
		        var images = new Image();
		        reader.readAsDataURL(file);
		        reader.onload = function() {
				    images.src = event.target.result;
				};
				images.onload = function () {
				    // 图片原始尺寸
				    var originWidth = this.width;
				    var originHeight = this.height;
				    // 最大尺寸限制
				    var maxWidth = 800, maxHeight = 800;
				    // 目标尺寸
				    var targetWidth = originWidth, targetHeight = originHeight;
				    // 图片尺寸超过1200x1200的限制
				    if (originWidth > maxWidth || originHeight > maxHeight) {
				        if (originWidth / originHeight > maxWidth / maxHeight) {
				            // 更宽，按照宽度限定尺寸
				            targetWidth = maxWidth;
				            targetHeight = Math.round(maxWidth * (originHeight / originWidth));
				        } else {
				            targetHeight = maxHeight;
				            targetWidth = Math.round(maxHeight * (originWidth / originHeight));
				        }
				    }
				    var width='';
				    var height='';
				    canvas.width =width= targetWidth;
				    canvas.height =height= targetHeight;
				    // 清除画布
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    //判断图片方向，重置canvas大小，确定旋转角度，iphone默认的是home键在右方的横屏拍摄方式
				    var degree=0;
					switch(orientation){
						//iphone横屏拍摄，此时home键在左侧
						case 3:
							degree=180;
							targetWidth=-width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在下方(正常拿手机的方向)
						case 6:
							canvas.width=height;
							canvas.height=width;
							degree=90;
							targetWidth=width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在上方
						case 8:
							canvas.width=height;
							canvas.height=width;
							degree=270;
							targetWidth=-width;
							targetHeight=height;
							break;
					}
					//使用canvas旋转校正
					context.rotate(degree*Math.PI/180);

				    // 图片压缩
				    context.drawImage(images, 0, 0, targetWidth, targetHeight);
				    var base = canvas.toDataURL();
				    $.ajax({
		                url: url,//这里是后台接口需要换掉
		                type: 'POST',
		                dataType: 'json',
		                cache: false,
		                async: false,
		                data: {base64:base},
		                success: function(res){
							if (res.code === 200) {
								if(i == 1){
									th.front=res.data
								}else if(i == 0){
									th.backimg=res.data;
								}
		                   }
		                },
		                complete: function () {
				            setTimeout(() => Indicator.close(), 500); 	
				        },
				        error: function (data) {
				        	Toast({message:'上传失败',position:'top'});
				        },
		            })
				}
			},
			submit(){
				if(!this.front){
					Toast({message:'请上传身份证正面',position:'top'});
					return;
				}
				if(!this.backimg){
					Toast({message:'请上传身份证反面',position:'top'});
					return;
				}
				if(!this.realname){
					Toast({message:'请填写真实姓名',position:'top'});
					return;
				}
				if(!this.id_card){
					Toast({message:'请填写身份证号',position:'top'});
					return;
				}
				if(!this.regex('id_card',this.id_card)){
					Toast({message:'身份证号格式错误',position:'top'});
					return;
				}
				Indicator.open();
				identity({front:this.front,back:this.backimg,card:this.id_card,realname:this.realname,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
                    if(res.code==200){
                    	this.status=3
                    }
                })
			},
			imgWH(){
				$.each($(".photo_box"), function(i,item) {
					var w=0,h=0;
					w=$(item).width();
					h=w;
					$(item).css("width",w+"px").css("height",h+"px");
				});
			}
		},
		mounted() {
			var that = this;
			this.imgWH()
			$(window).resize(function(){
				that.imgWH();
			});
			$(".authentication_view").on("change",".frontimg input",function(e){
				that.onUploadFront(e)
			});
			$(".authentication_view").on("change",".backimg input",function(e){
				that.onUploadBack(e)
			})
	    },
	    created(){
	    	this.url=this.host()
			var re=this.isLogin()
			var that = this;
			if(re){
				identityInfo({token:this.getToken()}).then((res)=>{
	            	Indicator.close();
                    if(res.code==200){
                    	this.realname=res.data.real_name;
						this.id_card=res.data.id_card;
						this.front=res.data.id_img_front;
						this.backimg=res.data.id_img_back;
						this.status=res.data.status;
                    }

                })
			}
		},
	}
</script>

<style scoped="scoped">
	.authentication_view{
		padding-top: 5rem;
		box-sizing: border-box;
	}
	.authentication_explain{
		width: 100%;
		margin: 0 auto;
		overflow: hidden;
	}
	.authentication_explain p{
		line-height: 1.4rem;
		font-size: 1.4rem;
		margin: 1.5rem 1.2rem;
		color: #9C9C9C;
	}
	.authentication_explain .hint{
		font-size: 1.6rem ;
		line-height: 1.6rem;
		text-align: center;
		color: #000000;
	}
	.photo_box{
		width: 44%;
		border:0.25rem dashed #E6E6E6;
		margin-left: 4%;
		float: left;
		overflow: hidden;
		box-sizing: border-box;
	}
	
	.photo_box img{
		width: 30%;
		height: 24%;
		margin: 28% 34% 13% 34%;
	}
	.photo_box p{
		color: #BFBFBF;
		font-size: 1.4rem;
		text-align: center;
		margin: 0 0 2rem 0;
	}
	.afterservice_list{
		margin-top: 2rem;
		background: #FFFFFF;
	}
	.afterservice_list div{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.afterservice_list div{
		color: #3C3C3C;
	}
	.afterservice_list .afterservice_money input{
		border: none;
		font-size: 1.5rem;
		text-align: right;
		height: 3rem;
		float: right;
	}
	.afterservice_list p{
		font-size: 1.5rem;
		height: 3rem;
		float: right;
		margin-right: 1.5rem;
		color: #909090;
	}

</style>



