<template>
	<div class="invoice_view">
		<mt-header class="address_head" title="新增发票信息">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<ul class="invoice_list">
			<li class="invoice_type">类型<button class="invoice_company" @click="company" id="companyBtn">单位</button><button class="invoice_personal" @click="personal" id="personalBtn">个人</button></li>
			<li class="invoice_title">抬头<input type="text" value="" placeholder="请填写准确的抬头名称" /></li>
			<li v-show="type" class="invoice_dutynum" id="dutynum">税号<input type="tel" maxlength="20" value="" placeholder="请填写「税号」或「社会信用代码」" /></li>
		</ul>
		<p v-show="type" id="tiptext">可咨询公司财务部门获得「税号」或「社会信用代码」</p>
		<center><button class="saveBtn">保存</button></center>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				type:true,
			}
		},
		methods:{
			company(){
				document.getElementById("companyBtn").setAttribute("class","chooseCss");
				document.getElementById("personalBtn").setAttribute("class","defaultCss");
				this.type = true;
			},
			personal(){
				document.getElementById("personalBtn").setAttribute("class","chooseCss");
				document.getElementById("companyBtn").setAttribute("class","defaultCss");
				this.type = false;
			}
		}
	}
</script>

<style scoped="scoped">
	.invoice_view{
		width: 100%;
	}
	.invoice_list{
		background: #FFFFFF;
	}
	.invoice_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.invoice_list .invoice_title input{
		border: none;
		width: 10rem;
		font-size: 1.5rem;
		height: 3rem;
		text-align: left;
		padding-left: 1rem;
	}
	.invoice_list .invoice_dutynum input{
		border: none;
		width: 20rem;
		font-size: 1.5rem;
		height: 3rem;
		text-align: left;
		padding-left: 1rem;
	}
	.invoice_personal,.defaultCss{
		background-color: white;
		width: 6rem;
		height: 2.5rem;
		border-radius: 0.3rem;
		border-width: 0.1rem;
		border-color: #E8E8E8;
		border-style:solid;
		margin-left: 1rem;
	}
	.invoice_company,.chooseCss{
		background-color: white;
		width: 6rem;
		height: 2.5rem;
		border-radius: 0.3rem;
		border-width: 0.1rem;
		border-color: #5CACEE;
		color: #5CACEE;
		border-style:solid;
		margin-left: 1rem;
	}
	.invoice_view p{
		color: gray;
		padding: 0.8rem;
		line-height: 1.7rem;
	}
	.saveBtn{
		background-color: #5CACEE;
		height: 4rem;
		border-radius: 0.3rem;
		border-width: 0;
		color: white;
		border-style:solid;
		width: 80%;
		font-size: 1.7rem;
		margin-top: 1rem;
	}
</style>


