<template>
	<div class="pay_success_view">
		<mt-header class="head" fixed title="支付成功">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div v-show="is_show">
			<div class="pay_success_prompt">
				<div>
					<p>卖家已付款~</p>
					<p>您的包裹已蓄势待发</p>
				</div>
				<img src="../../assets/img/payment_success_icon@2x.png"/>
			</div>
			<div class="pay_success_amount ">
				<span class="amount"><i>¥</i>{{amount}}</span>
				<div>
					<router-link to="/order?active=2">查看订单</router-link>
					<span class="hr"></span>
					<router-link to="/index">返回首页</router-link>
				</div>
			</div>
			<div class="recommended">
		    	<div class="recommended_title">
		    		<p class="f-m"><i class="f-y"></i>商品推荐</p>
		    	</div>
		    	<ul class="page-loadmore-list">
	            	<li v-for="item in list" :key="item.id" class="page-loadmore-listitem">
	            		<router-link :to="{path: 'goodsdet', query: {id: item.id}}">
		            		<div class="img_box"><img :src="url+item.img_thumb"/></div>
		            		<h4 v-text="item.title"></h4>
		            		<p><span class="new_price">¥{{item.price}}</span><span class="original_price">¥{{item.original_price}}</span></p>
	            		</router-link>
	            	</li>
	            </ul>
		    </div>
		    <p class="to_load">已经到底了~</p>
	    </div>
	</div>
</template>

<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {order} from '../../api/api';
	export default {
		data(){
			return{
				url:'',
				amount:'',
				is_show: false,
				order_id:'',
	            list:[]
			}
		},
		methods:{
			getInfo(){
				Indicator.open();
				order({order_id:this.order_id,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.amount=res.data.info.total_price;
	            		this.list=res.data.suggest;
	            	}
	            	this.is_show=true;
                })
			}
		},
		created(){
	    	this.url=this.host()
	    	this.order_id = this.$route.query.id;
	    	this.getInfo();
		},	
	}
</script>
<style scoped="scoped">
	.pay_success_view{
		padding-top: 5rem;
	}
	.pay_success_prompt{
		height: 12rem;
		background: url(../../assets/img/payment_success_bg@2x.png) no-repeat;
		background-size: 100% 100%;
		position: relative;
	}
	.pay_success_prompt div{
		position: absolute;
		top: 50%;
		left: 2rem;
		transform: translateY(-50%);
		color: #FFFFFF;
		font-size: 1.6rem;
		line-height: 1.8rem;
	}
	.pay_success_prompt img{
		width: 6.5rem;
		height: 6.2rem;
		position: absolute;
		top: 40%;
		right: 3rem;
		transform: translateY(-50%);
	}
	.pay_success_amount{
		height: 16.7rem;
		background: url(../../assets/img/money_bg@2x.png) no-repeat;
		background-size: 100% 100%;
		position: relative;
		margin-top: -4rem;
		z-index: 10;
		margin-bottom: 1rem;
	}
	.pay_success_amount .amount{
		position: absolute;
		left: 50%;
		top: 4rem;
		transform: translateX(-50%);
		color: #FF3D3D;
		font-size: 2.6rem;
	}
	.pay_success_amount .amount i{
		font-size: 1.6rem;
		margin-right: 0.3rem;
	}
	.pay_success_amount div{
		width: 100%;
		height: 2.5rem;
		position: absolute;
		left: 0;
		bottom: 3.5rem;
		font-size: 1.5rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-content: center;
		line-height: 2.5rem;
	}
	.pay_success_amount div a{
		color: #3C3C3C;
		padding: 0 3rem;
	}
	.pay_success_amount div .hr{
		display: inline-block;
		width: 1px;
		height: 2.5rem;
		background: #E8E8E8;
	}
	/*商品推荐*/
	.recommended{
		background: #FFFFFF;
	}
	.recommended_title{
		height: 4rem;
		text-align: center;
		position: relative;
	}
	.recommended_title p{
		padding-left: 2.25rem;
		font-size: 1.4rem;
	}
	.recommended_title p i{
		width: 1.5rem;
		height: 1.6rem;
		background: url(../../assets/img/commodity@2x.png) no-repeat;
		background-size: 100% 100%;	
	}
	.recommended_title p:before{
		content: "";
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: -11rem;
		width: 10rem;
		height: 2px;
		background: url(../../assets/img/segmenting_line1@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.recommended_title p:after{
		content: "";
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: -11rem;
		width: 10rem;
		height: 2px;
		background: url(../../assets/img/segmenting_line2@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.recommended .page-loadmore-list{
		overflow: hidden;
	}
	.recommended .page-loadmore-list li{
		width: 50%;
		box-sizing: border-box;
		float: left;
		padding: 0 1.2rem 1rem;
		border-right: 1px solid #E8E8E8;
		border-bottom: 1px solid #E8E8E8;
		overflow: hidden;
		font-size: 1.4rem;
	}
	.recommended .page-loadmore-list li:nth-child(even){	
		border-right-color: transparent;
	}
	.recommended .page-loadmore-list li div{
		width: 100%;
	}
	.recommended .page-loadmore-list li div img{
		width: 100%;
	}
	.recommended .page-loadmore-list li h4{
		font-weight: normal;
		color: #3C3C3C;
		margin-bottom: 0.5rem;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.recommended .page-loadmore-list li .new_price{
		color: #FF3D3D;
		margin-right: 0.6rem;
	}
	.recommended .page-loadmore-list li .original_price{
		color: #CBCBCB;
		text-decoration:line-through;
	}
	.to_load{
		text-align: center;
		color: #DADADA;
		font-size: 1.3rem;
		padding: 2rem 0 1rem; 
	}
</style>


