<template>
	<div class="search_view">
		<mt-header class="head" fixed title="商品搜索">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div class="search_goods_list" v-show="is_show">
			<div class="search_goods_title">
				<span>搜索结果</span>
			</div>
			<p class="empty" v-if="list.length==0">暂无商品</p>
			<ul class="search_list_view" v-if="list.length>0">
				<li v-for="(item,i) in list" class="search_goods_item" :key='i'>
					<router-link :to="{path: 'goodsdet', query: {id: item.id}}">
						<div class="img_box">
							<img :src="url+item.img_thumb"/>
						</div>
						<p v-text="item.title"></p>
						<span>租金+押金：<i>¥{{item.price}}</i></span>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>
<script type="text/babel">
	import { Toast,Indicator } from 'mint-ui';
	import {search} from '../api/api';
  	export default {
	    data() {
	        return {
	            key:'',
	            list:[],
	            is_show:false,
                url:''
	        }
	    },
	    methods: {
			
	    },
	    created(){
            this.url=this.host();
	    	Indicator.open();
	    	this.key=this.$route.query.key;
            search({q:this.key}).then((res)=>{
            	Indicator.close();
            	this.is_show=true;
                if(res.code==200){
                	this.list=res.data
                }
            });  
		},
  	}
</script>
<style scoped="scoped">
	.search_view{
		height: 100vh;
		background: #FFFFFF;
		padding: 5rem 0 1rem;
		box-sizing: border-box;
	}
	.search_goods_title{
		width: 100%;
		height: 3.5rem;
		line-height: 3.5rem;
		font-size: 1.8rem;
		border-bottom: 1px solid #f0f0f0;
	}
	.search_goods_title span{
		margin-left: 1.65rem;
		position: relative;
	}
	.search_goods_title span:after{
		content: "";
		position: absolute;
		width: 3rem;
		height: 0.1rem;
		background: #FF6784;
		left: 0;
		bottom: -0.4rem;
	}
	.search_list_view{
		overflow: hidden;
	}
	.search_list_view li{
		width: 50%;
		padding: 1rem;
		box-sizing: border-box;
		text-align: center;
		font-size: 1.4rem;
		line-height: 1.25;
		float: left;
	}
	.search_list_view li a{
		display: block;
		background: #F7F7F7;
		padding-bottom: 1rem;
	}
	.search_list_view div img{
		width: 100%;
	}
	.search_list_view li p{
		font-size: 1.6rem;
	}
	.search_list_view li p,
	.search_list_view span{
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		color: #3C3C3C;
	}
	.search_list_view span i{
		color: #FF3D3D;
	}
</style>


