<template>
	<div class="collection_view">
		<mt-header class="head" fixed title="我的收藏">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span @click="edits" slot="right"><i v-if="!edit">编辑</i><i v-if="edit">完成</i></span>
		</mt-header>
		<p v-if="show" class="empty">您的收藏还是空空哒</p>
		<ul class="collection_list">
			<li v-for="(item,index) in list">
				<i v-show="edit" @click="Sel(index)" :class="item.check ?'sel':'def'"></i> 	
				<router-link :to="{path: 'goodsdet', query: {id: item.gid}}">
					<div class="img_box">
						<img :src="host()+item.img_thumb"/>
					</div>
					<div class="text_box">
						<p v-text="item.title"></p>
						<span>¥{{item.price}}</span>
					</div>
				</router-link>
			</li>
		</ul>
		<footer v-show="edit">
			<label><input style="display: none;" v-model="allCheck" type="checkbox" @click="allSel" /><i></i>全选</label>
			<div class="btn_box">
				<button class="delect" @click="Del">删除</button>
			</div>
		</footer>
	</div>
</template>

<script>
	import { Picker,Toast,Indicator,MessageBox } from 'mint-ui';
	import {collection,delCollection} from '../../api/api';

	export default {
		data(){
			return{
				allCheck:false,
				edit:false,
				show:false,
				ids:[],
				list:[],
			}
		},
		methods:{
			edits(){
				this.edit = !this.edit;
				if(this.edit == false){
					this.allCheck = false;
					this.list.forEach((item)=>{
						item.check = false;
					})
				}
			},
			Sel(i){
				this.list[i].check = !this.list[i].check;
	   			var len=this.list.length;
	   			var selLen=0;
	   			var that=this;
	   			that.ids=[];
	   			for(var i=0;i<this.list.length;i++){
	   				if(this.list[i].check==true){
	   					that.ids.push(this.list[i].gid)
	   					selLen++;
	   				}
	   			}
	   			if(len==selLen){
	   				this.allCheck=true;
	   			}else{
	   				this.allCheck=false;
	   			}
	   		},
			allSel(){
				this.allCheck = !this.allCheck;
				var that=this;
	   			if(this.allCheck){
	   				this.list.forEach((i)=>{
	   					i.check=true;
	   					that.ids.push(i.gid)
	   				});
	   			}else{
	   				this.list.forEach((i)=>{
	   					that.ids=[]
	   					i.check=false
	   				});
	   			}
			},
			Del(){
				if(this.ids.length<1){
					Toast({message:'请选择您要删除的商品',position:'top'});
					return;
				}
				var that = this;
	   			MessageBox({
				  title: '是否删除',
				  message: '是否取消收藏选中的商品？',
				  showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						var id='';
						this.ids.forEach((i)=>{
		   					id+=i+',';
		   				});
		   				id=id.replace(/(^,)|(,$)/g,'');
		   				
						Indicator.open();
						delCollection({id:id,act:'del',token:this.getToken()}).then((res)=>{
			            	Indicator.close();
			            	if(res.code==200){
			            		this.ids=[];
			            		for(var i=0;i<this.list.length;i++){
					   				if(this.list[i].check==true){
					   					this.list.splice(i,1);
					   					i--;
					   				}
					   			}
								if(this.list.length == 0){
									this.edit = false;
									this.show=true;
									this.allCheck=false;
								}
			            	}
			            	console.log(this.ids);
		                })
						

					} 					
				});
				
	   		},
	   		getList(){
				Indicator.open();
				collection({token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.list=res.data;
	            		setTimeout(function(){
							$.each($(".img_box"), function(i,item) {
								var w=0,h=0;
								w=$(item).width();
								h=w;
								$(item).find("img").css("width",w+"px").css("height",h+"px");
							});
						},50)
	            	}else{
	            		this.show=true;
	            	}
                })
	   		},
		},
		created(){
			var re=this.isLogin()
			if(re){
				this.getList()
			}
			
		},
		mounted() {
	    }
	}
</script>

<style scoped="scoped">
	.collection_view{
		padding-top: 5rem;
		padding-bottom: 5rem;
	}
	.collection_list{
		overflow: hidden;
	}
	.collection_list li{
		width: 44%;
		margin: 1.5rem 4% 0;
		float: left;
		border-radius: 1rem;
		overflow: hidden;
		border: 1px solid #E8E8E8;
		box-sizing: border-box;		
		position: relative;
	}
	.collection_list li:nth-child(even){
		margin-left: 0;
	}
	.collection_list li a{
		width: 100%;
		display: block;
		height: 100%;
		font-size: 1.4rem;
		line-height: 1.6rem;
	}
	.collection_list li a img{
		width: 100%;
	}
	.text_box{
		padding:0.5rem 1rem;
	}
	.text_box p{
		color: #3C3C3C;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.text_box span{
		color: #FF3D3D;
	}
	.collection_list li .def{
		width: 1.6rem;
		height: 1.6rem;
		padding: 1.2rem;
		position: absolute;
		right: 0;
		top: 0;
		background:url(../../assets/img/no-selected.png) center center no-repeat;
		background-size: 1.6rem 1.6rem;
	}
	.collection_list li .sel{
		width: 1.6rem;
		height: 1.6rem;
		padding: 1.2rem;
		position: absolute;
		right: 0;
		top: 0;
		background: url(../../assets/img/selected_icon@2x.png) center center no-repeat;
		background-size: 1.6rem 1.6rem;
	}
	footer{
		height: 4.8rem;
		background: #FFFFFF;
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: row;
		align-items: center;
		padding-left: 1.2rem;
	}
	footer label{
		color: #3C3C3C;
		padding-left: 2.5rem;
		font-size: 1.5rem;
		position: relative;
	}
	footer label i{
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 1.6rem;
		height: 1.6rem;
		background: url(../../assets/img/no-selected@2x.png);
		background-size: 100% 100%;
	}
	footer label input:checked + i{
		background: url(../../assets/img/selected_icon@2x.png);
		background-size: 100% 100%;
	}
	.btn_box{
		width: 19rem;
		position: absolute;
		top: 0;
		right: 0;
		height: 4.8rem;
		display: flex;
	}
	.add_shopCart,.delect{
		flex: 1;
		border: none;
		overflow: hidden;
		height: 4.8rem;
		color: #FFFFFF;
		font-size: 1.6rem;
		background: #FFB853;
	}
	.delect{
		background: #FF6784;
	}
</style>


