<script type="text/babel">
	import { Indicator,MessageBox} from 'mint-ui';
	import {home} from '../api/api';
	export default {
	    data() {
	        return {
	        	url:'',
	            is_show: false,
	            selText:'',
	            list:[]
	        };
	    },
	    methods: {
	        getList() {
	        	Indicator.open();
	        	home({}).then((res)=>{
	                Indicator.close();
	                if(res.code==200){
	                    this.list = res.data;
	                    this.list.goods_a.forEach((i)=>{
	                    	i.price = i.price.substr(0,i.price.length-3);
	                    });
	                    this.list.goods_b.forEach((i)=>{
	                    	i.price = i.price.substr(0,i.price.length-3);
	                    });
	                    this.list.goods_new.forEach((i)=>{
	                    	i.price = i.price.substr(0,i.price.length-3);
	                    });
	                }else{
	                    this.$message({
	                        message: '请求失败',
	                        type: 'error'
	                    });
	                }
	                this.is_show=true;
	            })
	        },
	    },
	    created() {
	    	this.url=this.host();
	        this.getList()
	    },
	    mounted() {
	    	var that = this;
	    	$(function(){
	    		$('.select input').on('keypress', function(e){
					var keycode = e.keyCode;  
	                var searchName = $(this).val();  
	                if(keycode=='13') {  
	                    e.preventDefault(); 
	                    //请求搜索接口
	                    that.selText = ''
	                    that.$router.push({
							path: '/search',
							query: { 
				                key: searchName, 
				            }
						});
	                }  
				});
				function imgWH(){
					$.each($(".mint-swipe"), function(i,item) {
						var w=0,h=0;
						w=$(item).width();
						h=parseInt(w*0.6);
						$(item).css("height",h+"px");
					});
					$.each($(".img_box"), function(i,item) {
						var w=0,h=0;
						w=$(item).width();
						h=w;
						$(item).find("img").css("width",w+"px").css("height",h+"px");
					});
				}
				imgWH();   
				$(window).resize(function(){
					imgWH();
				});
			});
	    }
	};
</script>

