<template>
	<div class="changenum_view">
		<mt-header class="head" fixed title="更换手机号">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="submit()">完成</span>  
		</mt-header>
		<ul class="changenum_list">
			<li class="changenum_phone">手机号<input type="text" v-model="phone" value="" placeholder="请输入手机号" maxlength="11" /></li>
			<li class="changenum_code">验证码<input type="text" v-model="code" value="" placeholder="请输入验证码" maxlength="6" /><input @click="send()" type="button" value="获取验证码" v-if="!show"/><button class="custom" v-if="show">重新发送({{time}})秒</button></li>
		</ul>
	</div>
</template>

<script>
	import { Toast,Indicator,MessageBox } from 'mint-ui';
	import {changePhone,sendCode} from '../../api/api';
	export default {
		data(){
			return{
				phone : '',
				code : '',
				time:60,
				show:false,
			}
		},
		methods:{
			send(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					return;
				}
				Indicator.open();
				sendCode({phone:this.phone,type:5}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		MessageBox.alert(res.data.code, '验证码');
	            		this.show=true;
	            		this.custom();
	            	}
                    Toast({message:res.msg,position:'top'});
                })
			},
			custom(){
				var closeTime =""
				closeTime=setInterval(() => {
                	this.time--
                	if(this.time<=0){
						this.time=60;
						this.show=false;
						clearInterval(closeTime);
					}
				}, 1000);
				
			},
			submit(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					return;
				}
				if(this.code==""){
					Toast({message:'请填写验证码',position:'top'});
					$('.verifyCode_input').addClass("on");
					return;

				}
				if(!this.regex('aaa',this.code,/^\d{6}$/)){
					Toast({message:'验证码格式错误',position:'top'});
					return;
				}
				Indicator.open();
	            changePhone({phone:this.phone,code:this.code,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
                    if(res.code==200){
                        setTimeout(() => {
                        	this.$router.replace({
								path: '/bindingsuc',
								query: {phone: this.phone} 
							});
						}, 2000);
                    }
                })
			}
		},
		created(){
			this.isLogin()
		},	
	}
</script>

<style scoped="scoped">
	.changenum_view{
		padding-top: 6rem;
	}
	.changenum_list{
		background: #FFFFFF;
	}
	.changenum_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.changenum_list .changenum_phone input{
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		border: none;
		text-align: center;
		
	}
	.changenum_list .changenum_code input[type="text"]{
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		border: none;
		text-align: center;
	}
	.changenum_list .changenum_code input[type="button"],.changenum_code .custom{
		float: right;
		height: 3rem;
		border: none;
		background: none;
		font-size: 1.5rem;
		color: #FF6784;
	}
</style>


