<template>
	<div class="myrelease_view">
		<mt-header class="head" fixed title="我的发布">
		  	<mt-button @click="back()" class="back" slot="left"></mt-button>
		  	<router-link class="message_btn" slot="right" to="/message"><i v-if="newMsg"></i></router-link>
		</mt-header>
		<div v-show="is_show">
			<p v-if="list.length==0" class="empty">暂无数据</p>
			<mt-loadmore :top-method="loadTop" ref="loadmore" v-if="list.length>0">
				<div class="page-loadmore-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="100">
					<div v-for="item in list" class="community_item">
						<div class="community_item_head">
							<div class="head_name">
								<img :src="url+item.head_img"/>
								<p v-text="item.nickname"></p>
								<span v-text="item.create_time"></span>
							</div>
						</div>
						<router-link :to="{path: 'topicDet', query: {id: item.id}}">
							<div class="community_item_content">
								<p v-text="item.content"></p>
								<div>
									<img v-for="vo in item.image" :src="url+'/v1/thumb?src='+url+vo+'&w=200&h=200'"/>
								</div>
							</div>
						</router-link>
						<div class="community_item_footer">
							<span class="comments_icon" v-text="item.commentcount" ></span>
							<i class="hr f-m"></i>
							<span class="share_icon" v-text="item.likecount"></span>
						</div>
					</div>
				</div>
			</mt-loadmore>	
			<router-link class="release" to="/release">
				<span>发布动态</span>
			</router-link>
		</div>
	</div>
</template>

<script>
import { Indicator,MessageBox} from 'mint-ui';
import {myPushList} from '../../api/api';
	export default {
		data(){
			return{
				loading:false,
				newMsg:false,
				list: [],
	            page:1,
	            totalpage:0,
	            end:0,
	            url:'',
	            top:'',
	            is_show:false,
			}
		},
		methods: {
	        //下拉刷新
	        loadTop() {
	            this.getList();
	            this.$refs.loadmore.onTopLoaded();
	            
	        },
	        getList(){
	            Indicator.open();
	            myPushList({page:1,token:this.getToken()}).then((res)=>{
	                Indicator.close()
	                if(res.code==200){
	                    this.page=1;
	                    this.list = res.data.list;
	                    this.totalpage=res.data.total_page;
	                    this.newMsg=res.data.is_msg;
	                }else{
	                    this.$message({
	                        message: '请求失败',
	                        type: 'error'
	                    });
	                }
	                this.is_show=true;
	            })
	        },
	        loadMore() {
	        	if(this.page>=this.totalpage){
					return;
				}
				this.page++;
	            var that=this;
	            Indicator.open();
	            myPushList({page:this.page,token:this.getToken()}).then((res)=>{
	            	Indicator.close()
	                if(res.code==200){
	                    this.totalpage=res.data.total_page;
	                    res.data.list.forEach(function(item){
	                        that.list.push(item)
	                    })

	                }else{
	                    this.$message({
	                        message: '请求失败',
	                        type: 'error'
	                    });
	                }
	            })
	        },

	    },
		created(){
			this.isLogin();
			this.url=this.host()
			this.getList();
		},	
	}
</script>

<style>
	.more{overflow: scroll;}
	.myrelease_view{
		padding-top: 5rem;
	}
	.message_btn{
		width: 1.7rem;
		height: 2rem;
		display: block;
		background: url(../../assets/img/remind_icon@2x.png);
		background-size: 100% 100%;
		float: right;
		margin-right: 1rem;
		position: relative;
	}
	.message_btn i{
		width: 0.6rem;
		height: 0.6rem;
		position: absolute;
		top: 0;
		right: 0;
		border-radius: 50%;
		background: #FF0000;
	}
	.release{
		position: fixed;
		z-index: 100;
		width: 10rem;
		height: 3rem;
		border-radius: 1.5rem;
		background: rgb(255,80,120);
		color: #FFFFFF;
		line-height: 3rem;
		text-align: center;
		left: 50%;
		transform: translateX(-50%);
		bottom: 4.5rem;
		box-shadow: 0 5px 5px 1px rgba(255,80,120,0.2);
	}
	.release span{
		padding-left: 2rem;
		background: url(../../assets/img/release_icon@2x.png) left center no-repeat;
		background-size: 1.8rem 1.9rem;
	}
	.community_item{
		background: #FFFFFF;
		margin-bottom: 0.9rem;
	}
	.community_item:last-child{
		margin-bottom: 0;
	}
	.community_item_head{
		height: 5rem;
		display: flex;
		padding: 0 1.2rem;
		justify-content: space-between;
		align-items: center;
		color: #3C3C3C;
	}
	.head_name{
		width: 80%;
		height: 4rem;
		padding: 0.5rem 0 0.5rem 5rem;
		position: relative;
	}
	.head_name img{
		width: 4rem;
		height: 4rem;
		position: absolute;
		left: 0;
		top: 0.5rem;
		border-radius: 50%;
	}
	.head_name p{
		font-size: 1.5rem;
		line-height: 2rem;
	}
	.head_name span{
		font-size: 1.3rem;
		line-height: 2rem;
		color: #B1B1B1;
	}
	.collection{
		height: 1.3rem;
		font-size: 1.3rem;
		color: #ff6262;
		padding-left: 1.8rem;
		position: relative;
		line-height: 1.3rem;
	}
	.collection img{
		width: 1.5rem;
		height: 1.3rem;
		position: absolute;
		top: 0;
		left: 0;
	}
	.community_item_content{
		border-top: 1px solid #E8E8E8;
		border-bottom: 1px solid #E8E8E8;
		padding: 1rem 1.2rem 2.5rem;
	}
	.community_item_content p{
		font-size: 1.4rem;
		line-height: 2rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		padding-left: 1rem;
		position: relative;
		color: #3C3C3C;
	}
	.community_item_content p:before{
		content: "";
		position: absolute;
		width: 0.7rem;
		height: 0.7rem;
		border-radius: 50%;
		top: 0.7rem;
		left: 0;
		background: #FF6784;
	}
	.community_item_content div img{
		width: 30%;
		margin-top: 1rem;
		margin-right: 5%;
	}
	.community_item_content div img:nth-child(3n){
		margin-right: 0;
	}
	.community_item_footer{
		border-bottom: 1px solid #E8E8E8;
		height: 4rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		position: relative;
	}
	.community_item_footer .hr{
		width: 1px;
		height: 2rem;
		background: #E8E8E8;
	}
	.community_item_footer span{
		display: block;
		padding-left: 2.2rem;
		height: 1.5rem;
		line-height: 1.5rem;
		position: relative;
		color: #ADADAD;
	}
	.comments_icon{
		background: url(../../assets/img/comment_icon@2x.png) left center no-repeat;
		background-size: 1.6rem 1.5rem;
	}
	.share_icon{
		background: url(../../assets/img/fabulous_default@2x.png) left center no-repeat;
		background-size: 1.6rem 1.5rem;
	}
	/*.share_icon{
		background: url(../../assets/img/forward_icon@2x.png) left center no-repeat;
		background-size: 1.6rem 1.5rem;
	}*/
</style>


