<template>
	<div class="resetA_view">
		<mt-header class="head" fixed title="密码重置">
		  	<router-link to="" slot="left">
		  		<mt-button @click="back()" class="back"></mt-button>
		  	</router-link>
		  	<span slot="right" @click="next()">下一步</span>
		</mt-header>
		<ul class="resetA_list">
			<li class="reset_phone">手机号<input type="text" maxlength="11" v-model="phone" value="" placeholder="请输入手机号" /></li>
			<li class="reset_code">验证码<input type="text" maxlength="6" v-model="code" value="" placeholder="请输入验证码" /><input @click="send()" type="button" value="获取验证码" v-if="!show"/><button class="custom" v-if="show">重新发送({{time}})秒</button></li>
		</ul>
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {sendCode} from '../api/api';
	export default {
		data(){
			return{
				phone : '13512345678',
				code : '',
				time:60,
				show:false,
			}
		},
		methods:{
			send(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				Indicator.open();
				sendCode({phone:this.phone,type:2}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.show=true;
	            		this.custom();
	            	}
	            	
                    Toast({message:res.msg,position:'top'});
                })
			},
			custom(){
				var closeTime =""
				closeTime=setInterval(() => {
                	this.time--
                	if(this.time<=0){
						this.time=60;
						this.show=false;
						clearInterval(closeTime);
					}
				}, 1000);
				
			},
			next(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					$('.reset_phone input').focus();
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					$('.reset_phone input').focus();
					return;
				}
				if(this.code==""){
					Toast({message:'请填写验证码',position:'top'});
					$('.reset_code input').focus();
					return;

				}
				if(!this.regex('aaa',this.code,/^\d{6}$/)){
					Toast({message:'验证码格式错误',position:'top'});
					$('.reset_code input').focus();
					return;
				}
				this.$router.push({
					path: '/resetTwo',
					query: {
            			phone:this.phone,
            			code:this.code
         			}
				});

			}
		}

	}
</script>

<style scoped="scoped">
	.resetA_view{
		padding-top: 5rem;
	}
	.resetA_list{
		background: #FFFFFF;
	}
	.resetA_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.resetA_list .reset_phone input{
		border: none;
		width: 20rem;
		font-size: 1.5rem;
		height: 3rem;
		float: right;
		text-align: right;
	}
	.resetA_list .reset_code input[type="text"]{
		width: 10rem;
		font-size: 1.5rem;
		height: 3rem;
		border: none;
		text-align: center;
	}
	.resetA_list .reset_code input[type="button"], .resetA_list .custom{
		float: right;
		height: 3rem;
		border: none;
		background: none;
		font-size: 1.5rem;
		color: #FF6784;
	}
</style>


