<template>
	<div>
	<div class="reg_view" v-show="is_show">
		<div class="head_tab">
			<a class="active">注册</a>
		</div>
		<ul class="reg_input">
			<li class="phone_input">
				<i></i><input type="text" v-model="phone" maxlength="11" value="" placeholder="输入手机号码" />
			</li>
			<li class="verifyCode_input">
				<i></i><input class="v_code" v-model="code" maxlength="6" type="text" value="" placeholder="输入短信验证码" /><input @click="send()" type="button" value="获取验证码" v-if="!show"/><button class="custom" v-if="show">重新发送({{time}})秒</button>
			</li>
			<li class="login_pwd_input">
				<i></i><input type="password" v-model="password" value="" placeholder="设置登录密码" />
			</li>
		</ul>
		<button class="reg_btn" @click="regist">注册</button>
	</div>
	<div class="user_view" v-show="is_login">
		<header>
			<router-link class="edit" to="/personal"></router-link>
			<router-link class="set" to="/options"></router-link>
			<div class="head_img">
				<i class="man"></i>
				<img src="../assets/img/default_head_image@2x.png"/>
				<p>未登录</p>
			</div>
		</header>
		<!--加上news类名即可显示消息-->
		<div class="order_status">
			<router-link class="no_payment" to="/order?active=1">待付款</router-link><span></span>
			<router-link class="no_delivery" to="/order?active=2">待发货</router-link><span></span>
			<router-link class="no_goods news" to="/order?active=3">待收货</router-link><span></span>
			<router-link class="completed" to="/order?active=4">已完成</router-link>
		</div>
		<ul class="mine_menu">
			<li class="all_order">
				<router-link to="/order?active=0">全部订单<i></i></router-link>
			</li>
			<li class="attestation">
				<router-link to="/authentication">身份认证<i></i></router-link>
			</li>
			<li class="address">
				<router-link to="/address">收货地址<i></i></router-link>
			</li>
			<li class="mine_collect">
				<router-link to="/collection">我的收藏<i></i></router-link>
			</li>
			<li class="record">
				<router-link to="/record">浏览记录<i></i></router-link>
			</li>
			<li class="publish">
				<router-link to="/myrelease">我的发布<i></i></router-link>
			</li>
			<li class="mine_comment">
				<router-link to="/evaluation">我的评价<i></i></router-link>
			</li>
			<li class="feedback">
				<router-link to="/leaveMsg">留言反馈<i></i></router-link>
			</li>
		</ul>
		<mt-tabbar class="footer" v-model="selected" fixed>
		<router-link to="/index">
			<mt-tab-item id="首页"><i class="index"></i>首页</mt-tab-item>
		</router-link>
		<router-link to="/class">
			<mt-tab-item id="分类"><i class="class"></i>分类</mt-tab-item>
		</router-link>
		<router-link to="/user" class="router-link-exact-active router-link-active">
			<mt-tab-item id="会员中心"><i class="user"></i>会员中心</mt-tab-item>
		</router-link>
		<router-link to="/cart">
			<mt-tab-item  id="购物车"><i class="cart"></i>购物车</mt-tab-item>
		</router-link>
		<router-link to="/community">
			<mt-tab-item  id="et妈妈"><i class="community"></i>ET妈妈</mt-tab-item>
		</router-link>
    </mt-tabbar>
	</div>
	</div>
</template>

<script>
	import { Toast,Indicator,MessageBox} from 'mint-ui';
	import {userRegist,sendCode,wxLogin} from '../api/api';
	export default {
		data(){
			return{
				phone:'',
				code:'',
				password:'',
				time:60,
				show:false,
				is_show:false,//页面是否加载
				is_login:false,
				selected:'首页',
			}
		},
		methods:{
			send(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				Indicator.open();
				sendCode({phone:this.phone,type:1}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		MessageBox.alert(res.data.code, '验证码');
	            		this.show=true;
	            		this.custom();
	            	}
                    Toast({message:res.msg,position:'top'});
                })
			},
			custom(){
				var closeTime =""
				closeTime=setInterval(() => {
                	this.time--
                	if(this.time<=0){
						this.time=60;
						this.show=false;
						clearInterval(closeTime);
					}
				}, 1000);
				
			},
			regist(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(this.code==""){
					Toast({message:'请填写验证码',position:'top'});
					$('.verifyCode_input').addClass("on");
					return;

				}
				if(!this.regex('aaa',this.code,/^\d{6}$/)){
					Toast({message:'验证码格式错误',position:'top'});
					$('.phone_input').addClass("on");
					return;
				}
				if(this.password==""){
					Toast({message:'密码不能为空',position:'top'});
					$('.login_pwd_input').addClass("on");
					return;
				}
				Indicator.open();
	            userRegist({phone:this.phone,code:this.code,password:this.password,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
                    if(res.code==200){
                        Toast({message:res.msg,position:'top'});
                        setTimeout(() => {
                        	this.$router.push({
								path: '/'
							});
						}, 2000);
                    }else{
                    	Toast({message:res.msg,position:'top'});
                    }
                })
			},
			headback(){
				var w = $(document).width();
				var h = w * 0.5;
				if(h>260){
					h = 260;
				}
				$(".user_view header").css("height",h+"px");
			}
		},
		mounted() {
			this.headback();
			$(function(){
				$("input").focus(function(){
					$(this).parents("li").addClass("on").siblings().removeClass("on");
				});
				$("input").blur(function(){
					$(this).parents("li").removeClass("on");
				});
			})
			pushHistory(); 
			var that=this;
		    window.addEventListener("popstate", function(e) { 
		        that.$router.replace({
					path: '/'
				});
		    }, false); 
		    function pushHistory() { 
		        var state = { 
		            title: "title", 
		            url: "#"
		        }; 
		        window.history.pushState(state, "title", "#"); 
		    }
			console.log(this.$route.query.code)
			if(this.$route.query.code==200){
				Toast({message:'登录成功',position:'top'});
				this.is_show=true
				Lockr.set('token', encodeURIComponent(this.$route.query.token));
            	this.$router.replace({
					path: '/'
				});
			}else if(this.$route.query.code==400){
				Toast({message:'请注册账号',position:'top'});
				Lockr.set('token', encodeURIComponent(this.$route.query.token));
				this.is_show=true
			}else if(this.$route.query.code==undefined||Lockr.get('token')==undefined){
				this.is_login=true
				Indicator.close();
				setTimeout(function(){
					window.location.href='http://ytapi.hulupi.com//v1/wxlogin';
				},1000)
				
			}
			
		}
	}
</script>

<style>
	html,body{
		width: 100%;
		height: 100%;
	}
	/*登录*/
	.reg_view{
		width: 100%;
		height: 100%;
		overflow: hidden;
		background:#FFFFFF url(../assets/img/login_bg@2x.png) left bottom no-repeat;
		background-size: 100% 30%;
	}
	.head_tab{
		margin: 4rem auto 5rem;
		font-size: 1.7rem;
		line-height: 2.2rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
	.head_tab a{
		color: #3C3C3C;
		position: relative;
	}
	.head_tab a.active{
		color: #FF6784;
	}
	.head_tab a.active:after{
		content: "";
		position: absolute;
		width: 1.9rem;
		height: 0.3rem;
		border-radius: 3px;
		background: #FF6784;
		left: 50%;
		transform: translateX(-50%);
		bottom: -0.6rem;
	}
	.reg_input{
		width: 90%;
		margin: 0 auto;	
	}
	.reg_input li{
		border: 1px solid transparent;
		padding: 1rem 0;	
		margin-bottom: 2rem;
		border-radius: 2rem;
	}
	.reg_input li.on{
		border-color: #E8E8E8;
	}
	.reg_input .phone_input,
	.reg_input .login_pwd_input,
	.reg_input .verifyCode_input{
		position: relative;
		height: 2.2rem;
		line-height: 2.2rem;
		padding-left: 4rem;
	}
	.phone_input i,
	.login_pwd_input i,
	.verifyCode_input i{
		position: absolute;
		left: 1rem;
		top: 1rem;
		width: 2.2rem;
		height: 2.2rem;
		background: url(../assets/img/phone_number@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.login_pwd_input i{
		background: url(../assets/img/password@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.verifyCode_input i{
		background: url(../assets/img/verification_code@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	.phone_input input,
	.login_pwd_input input,
	.verifyCode_input .v_code{
		width: 98%;
		height: 2.2rem;
		font-size: 1.6rem;
		border: none;
	}
	.verifyCode_input{
		padding-right: 8.5rem;
	}
	.verifyCode_input input[type="button"], .verifyCode_input .custom{
		position: absolute;
		top: 1rem;
		right: 1rem;
		height: 2.2rem;
		color: #FF6784;
		border: none;
		background: transparent;
	}
	.reg_btn{
		display: block;
		width: 86%;
		height: 5rem;
		margin: 0 auto 2.1rem;
		color: #FFFFFF;
		font-size: 1.8rem;
		border: none;
		background-color: rgb(255,87,123);
		border-radius: 2.5rem;
		box-shadow: 0 0.5rem 1rem 0.25rem rgba(255,87,123,0.2);
	}
	.user_view{
		padding-bottom: 6rem;
	}
	.user_view header{
		position: relative;
	}
	.user_view header img{
		width: 100%;
	}
	header{
		min-height: 5rem;
		background: #FFFFFF url(../assets/img/mine_bg@2x.jpg) no-repeat;
		background-size: 100% 100%;
	}
	.edit,
	.set{
		position: absolute;
		top: 2rem;
		left: 1.2rem;
		width: 2.4rem;
		height: 2.4rem;
		background: url(../assets/img/information_editing_icon@2x.png);
		background-size: 100% 100%;
	}
	.set{
		left: auto;
		right: 1.2rem;
		background: url(../assets/img/set_up_icon@2x.png);
		background-size: 100% 100%;
	}
	.head_img{
		position: absolute;
		left: 50%;
		top: 20%;
		transform: translateX(-50%);
		width: 7.5rem;
		height: 7.5rem;
		border-radius: 50%;
		color: #FFFFFF;
		font-size: 1.6rem;
		text-align: center;
		white-space: nowrap;
	}
	.head_img img{
		width: 100%;
		height: 100%;
		border-radius: 50%;
		border: 0.2rem solid #F74365;
	}
	.head_img .woman,
	.head_img .man{
		position: absolute;
		left: -0.5rem;
		bottom: 0.5rem;
		width: 1.8rem;
		height: 1.8rem;
		border-radius: 50%;
		overflow: hidden;
		background: #FFFFFF url(../assets/img/women_icon@2x.png) center center no-repeat;
		background-size: 1rem 1rem;
	}
	.head_img .man{
		background: #FFFFFF url(../assets/img/man_icon@2x.png) center center no-repeat;
		background-size: 1.1rem 1.1rem;
	}
	.order_status{
		width: 100%;
		height: 7.5rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
	}
	.order_status a{
		padding-top: 3rem;
		color: #3C3C3C;
		font-size: 1.4rem;
		position: relative;
	}
	.order_status span{
		width: 0.05rem;
		height: 3.8rem;
		background: #E8E8E8;
	}
	.order_status .no_payment{
		background: url(../assets/img/pending_payment_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.order_status .no_delivery{
		background: url(../assets/img/pending_delivery_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.order_status .no_goods{
		background: url(../assets/img/goods_receipt_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.order_status .completed{
		background: url(../assets/img/complete_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.mine_menu li{
		height: 4.5rem;
		padding: 0 1.2rem;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		line-height: 4.5rem;
	}
	.mine_menu li a{
		color: #3C3C3C;
		font-size: 1.5rem;
		padding-left: 3.5rem;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	.mine_menu li a i{
		width: 0.7rem;
		height: 1.3rem;
		background: url(../assets/img/next@2x.png);
		background-size: 100% 100%;
	}
	.mine_menu .all_order{
		margin-bottom: 1rem;
	}
	.mine_menu .all_order a{
		background: url(../assets/img/order_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .attestation a{
		background: url(../assets/img/authentication_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .address a{
		background: url(../assets/img/address_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .mine_collect a{
		background: url(../assets/img/collection_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .record a{
		background: url(../assets/img/browse_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .publish a{
		background: url(../assets/img/release_icon2@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .mine_comment a{
		background: url(../assets/img/evaluate_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .feedback a{
		background: url(../assets/img/feedback_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.footer{
		height: 5rem;
		background: rgba(0,0,0,0.9);
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		background-image: none;
		border-top: 0.05rem solid #E8E8E8;
		z-index: 1000 !important;
	}
	.footer a{
		flex: 1;
		text-align: center;
		text-decoration: none;
		color: #FFFFFF;
	}
	.footer a i{
		display: block;
		width: 2rem;
		height: 1.84rem;
		margin:0 auto 0.5rem;
	}
	.footer a .index{
		background-image: url(../assets/img/home_default@2x.png);
		background-size: 100% 100%;
	}
	.footer a .class{
		background-image: url(../assets/img/classification_default@2x.png);
		background-size: 100% 100%;
	}
	.footer a .user{
		background-image: url(../assets/img/mine_default@2x.png);
		background-size: 100% 100%;
	}
	.footer a .cart{
		background-image: url(../assets/img/cart_default@2x.png);
		background-size: 100% 100%;
	}
	.footer a .community{
		background-image: url(../assets/img/community_default@2x.png);
		background-size: 100% 100%;
	}
	.footer .router-link-active a{
		color: #ff5f5f;
	}
	.footer .router-link-active .index{
		background-image: url(../assets/img/home_selected@2x.png);
		background-size: 100% 100%;
	}
	.footer .router-link-active .class{
		background-image: url(../assets/img/classification_selected@2x.png);
		background-size: 100% 100%;
	}
	.footer .router-link-active .user{
		background-image: url(../assets/img/mine_selected@2x.png);
		background-size: 100% 100%;
	}
	.footer .router-link-active .cart{
		background-image: url(../assets/img/cart_selected@2x.png);
		background-size: 100% 100%;
	}
	.footer .router-link-active .community{
		background-image: url(../assets/img/community_selected@2x.png);
		background-size: 100% 100%;
	}	
</style>


