<template>
	<div class="user_view" v-show="show">
		<header>
			<router-link class="edit" to="/personal"></router-link>
			<router-link class="set" to="/options"></router-link>
			<div class="head_img">
				<i class="man" v-if="sex==1"></i><i class="woman" v-else></i>
				<img :src="head_img"/>
				<p>{{ username }}</p>
			</div>
		</header>
		<!--加上news类名即可显示消息-->
		<div class="order_status">
			<router-link class="no_payment" to="/order?active=1">待付款</router-link><span></span>
			<router-link class="no_delivery" to="/order?active=2">待发货</router-link><span></span>
			<router-link class="no_goods news" to="/order?active=3">待收货</router-link><span></span>
			<router-link class="completed" to="/order?active=4">已完成</router-link>
		</div>
		<ul class="mine_menu">
			<li class="all_order">
				<router-link to="/order?active=0">全部订单<i></i></router-link>
			</li>
			<li class="attestation">
				<router-link to="/authentication">身份认证<i></i></router-link>
			</li>
			<li class="address">
				<router-link to="/address">收货地址<i></i></router-link>
			</li>
			<li class="mine_collect">
				<router-link to="/collection">我的收藏<i></i></router-link>
			</li>
			<li class="record">
				<router-link to="/record">浏览记录<i></i></router-link>
			</li>
			<li class="publish">
				<router-link to="/myrelease">我的发布<i></i></router-link>
			</li>
			<li class="mine_comment">
				<router-link to="/evaluation">我的评价<i></i></router-link>
			</li>
			<li class="feedback">
				<router-link to="/leaveMsg">留言反馈<i></i></router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {getUser} from '../api/api';
	export default {
		data(){
			return{
				username : '',
				head_img : '',
				sex:1,
				show:false,
			}
		},
		methods:{
			getUserInfo(){
				//判断缓存
				var that = this;
				if(Lockr.get('username')==undefined){
					Indicator.open();
		            getUser({token:this.getToken()}).then((res)=>{
		            	Indicator.close();
	                    if(res.code==200&&res.data){
	                    	Lockr.set('username', res.data.nickname);
	                    	Lockr.set('sex', res.data.sex);
	                    	Lockr.set('head_img', res.data.head_img);
	                    	Lockr.set('phone', res.data.phone);
	                        this.username=res.data.nickname
	                        this.head_img=that.host()+res.data.head_img;
	                        this.sex=res.data.sex;
	                        that.show = true;
	                    }else{
	                    	Lockr.flush();
	                    }
	                })
				}else{
					this.username=Lockr.get('username');
					this.head_img=that.host()+Lockr.get('head_img');
                    this.sex=Lockr.get('sex');
                    that.show = true;
				}
				
			},
			headback(){
				var w = $(document).width();
				var h = w * 0.5;
				if(h>260){
					h = 260;
				}
				$(".user_view header").css("height",h+"px");
			}
		},
		created(){
			var isLogin=this.isLogin();
			if(isLogin){
				this.getUserInfo();
			}
		},
		mounted() {
			this.headback();
			var that = this;
			$(window).resize(function(){
				that.headback();
			})
		}
	}
</script>

<style scoped="scoped">
	.user_view{
		padding-bottom: 6rem;
	}
	.user_view header{
		position: relative;
	}
	.user_view header img{
		width: 100%;
	}
	header{
		min-height: 5rem;
		background: #FFFFFF url(../assets/img/mine_bg@2x.jpg) no-repeat;
		background-size: 100% 100%;
	}
	.edit,
	.set{
		position: absolute;
		top: 2rem;
		left: 1.2rem;
		width: 2.4rem;
		height: 2.4rem;
		background: url(../assets/img/information_editing_icon@2x.png);
		background-size: 100% 100%;
	}
	.set{
		left: auto;
		right: 1.2rem;
		background: url(../assets/img/set_up_icon@2x.png);
		background-size: 100% 100%;
	}
	.head_img{
		position: absolute;
		left: 50%;
		top: 20%;
		transform: translateX(-50%);
		width: 7.5rem;
		height: 7.5rem;
		border-radius: 50%;
		color: #FFFFFF;
		font-size: 1.6rem;
		text-align: center;
		white-space: nowrap;
	}
	.head_img img{
		width: 100%;
		height: 100%;
		border-radius: 50%;
		border: 0.2rem solid #F74365;
	}
	.head_img .woman,
	.head_img .man{
		position: absolute;
		left: -0.5rem;
		bottom: 0.5rem;
		width: 1.8rem;
		height: 1.8rem;
		border-radius: 50%;
		overflow: hidden;
		background: #FFFFFF url(../assets/img/women_icon@2x.png) center center no-repeat;
		background-size: 1rem 1rem;
	}
	.head_img .man{
		background: #FFFFFF url(../assets/img/man_icon@2x.png) center center no-repeat;
		background-size: 1.1rem 1.1rem;
	}
	.order_status{
		width: 100%;
		height: 7.5rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
	}
	.order_status a{
		padding-top: 3rem;
		color: #3C3C3C;
		font-size: 1.4rem;
		position: relative;
	}
	.order_status span{
		width: 0.05rem;
		height: 3.8rem;
		background: #E8E8E8;
	}
	.order_status .no_payment{
		background: url(../assets/img/pending_payment_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.order_status .no_delivery{
		background: url(../assets/img/pending_delivery_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.order_status .no_goods{
		background: url(../assets/img/goods_receipt_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.order_status .completed{
		background: url(../assets/img/complete_icon@2x.png) center top no-repeat;
		background-size: 2.6rem 2.1rem;
	}
	.mine_menu li{
		height: 4.5rem;
		padding: 0 1.2rem;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		line-height: 4.5rem;
	}
	.mine_menu li a{
		color: #3C3C3C;
		font-size: 1.5rem;
		padding-left: 3.5rem;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	.mine_menu li a i{
		width: 0.7rem;
		height: 1.3rem;
		background: url(../assets/img/next@2x.png);
		background-size: 100% 100%;
	}
	.mine_menu .all_order{
		margin-bottom: 1rem;
	}
	.mine_menu .all_order a{
		background: url(../assets/img/order_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .attestation a{
		background: url(../assets/img/authentication_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .address a{
		background: url(../assets/img/address_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .mine_collect a{
		background: url(../assets/img/collection_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .record a{
		background: url(../assets/img/browse_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .publish a{
		background: url(../assets/img/release_icon2@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .mine_comment a{
		background: url(../assets/img/evaluate_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
	.mine_menu .feedback a{
		background: url(../assets/img/feedback_icon@2x.png) left center no-repeat;
		background-size: 2.2rem 2.2rem;
	}
</style>


