export default{
    install(Vue,options)
    {
        //全局变量 根域名
        Vue.prototype.host = function () {
            return 'http://ytapi.hulupi.com';
            // return 'http://www.mcyite.com';
        }
        
       
        //设置cookie
        Vue.prototype.setCookie = function (name,value,time) {
            var d = new Date();
            d.setTime(d.getTime()+time);
            var expires = "expires="+d.toGMTString();
            document.cookie = name + "=" + value + "; " + expires;
        }
        //获取cookie;
        Vue.prototype.getCookie = function (name) {
            var name = name + "=";z
            var ca = document.cookie.split(';');
            for(var i=0; i<ca.length; i++) {
                var c = ca[i].trim();
                if (c.indexOf(name)==0) return c.substring(name.length,c.length);
            }
            return "";
        }
        //删除cookie
        Vue.prototype.delCookie = function (name){
            var exp = new Date();
            exp.setTime(exp.getTime() - 1);
            var cval=this.getCookie(name);
            if(cval!=null)
            document.cookie= name + "="+cval+";expires="+exp.toGMTString();
        }

        Vue.prototype.isLogin=function(){
            // if(Lockr.get('phone')==undefined&&Lockr.get('token')){
            //     this.$router.replace({
            //         path: '/login',
            //         query: {redirect: router.currentRoute.fullPath} 
            //     });
            // }
            if(Lockr.get('token')==undefined){
                
                this.$router.replace({
                    path: '/login',
                    query: {redirect: router.currentRoute.fullPath} 
                });
                return false;
            }else{
                return true;
            }
        }

        Vue.prototype.back=function(){
            this.$router.go(-1);
        }
        Vue.prototype.top=function(){
            $(window).scrollTop(0)
        }
        Vue.prototype.getToken=function(){
            // if(Lockr.get('phone')==undefined&&Lockr.get('token')){
            //     this.$router.replace({
            //         path: '/login',
            //         query: {redirect: router.currentRoute.fullPath} 
            //     });
            // }
            if(Lockr.get('token')==undefined){
                this.$router.replace({
                    path: '/login',
                    query: {redirect: router.currentRoute.fullPath} 
                });
            }else{
                return Lockr.get('token');
            }

        }
        Vue.prototype.regex = function (type,string,pattern){
            switch (type) {
                case 'num'://数字
                    pattern = /^[0-9]+$/;
                    break;
                case 'phone'://手机号码
                    pattern = /^1[3456789]\d{9}$/;
                    break;
                case 'password'://密码可以全数字或全字母 特殊字符~!@#$%^&*.
                    pattern = /^[\@A-Za-z0-9\!\#\$\%\^\&\*\.\~]{6,22}$/;
                    break;
                case 'username'://用户名
                    pattern = /^[a-zA-Z0-9_]{3,16}$/;
                    break;
                case 'email'://用户名
                    pattern = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
                    break;
                case 'chinese'://中文
                    pattern=/^[\u4e00-\u9fa5]+$/;
                    break;         
                case 'id_card'://15或18位身份证
                    pattern=/(^\d{15}$)|(^\d{18}$)|(^\d{17}X|x$)/;
                    break;
                case 'bank_card'://银行卡16或19位
                    pattern=/^(\d{16}|\d{19})$/;
                    break;      
                case 'money'://金额  正数2位小数
                    pattern = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
                    break;
                default:
                    break;
            }
            if(!pattern.test(string)){
                return false;
            }else{
                return true;
            }
        }
        
    }
}


