<template>
	<div class="resetB_view">
		<mt-header class="head" fixed title="密码重置">
		  	<router-link to="" slot="left">
		  		<mt-button @click="back()" class="back"></mt-button>
		  	</router-link>
		  	<span slot="right" @click="submit()">完成</span>
		</mt-header>
		<ul class="resetB_list">
			<li class="reset_pwd">设置新密码<input type="password" v-model="password" value="" placeholder="请输入新密码" /></li>
			<li class="reset_pwd_affirm">确认新密码<input type="password" v-model="repassword" value="" placeholder="请再次确认新密码" /></li>
		</ul>
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {forgetPassword} from '../api/api';
	export default {
		data(){
			return{
				password : '',
				repassword : '',
				phone:'',
				code:'',
			}
		},
		methods:{
			submit(){
				console.log(this.phone);
				if(!this.regex('phone',this.phone)||!this.regex('aaa',this.code,/^\d{6}$/)){
					Toast({message:'非法参数',position:'top'});
					setTimeout(() => {
                    	this.$router.push({
							path: '/resetOne'
						});
					}, 2000);
					return;
				}
				if(this.password==""){
					Toast({message:'密码不能为空',position:'top'});
					return;
				}
				if(this.password==""){
					Toast({message:'确定密码不能为空',position:'top'});
					return;
				}
				if(this.password!==this.repassword){
					Toast({message:'两次密码不一致',position:'top'});
					return;
				}
				Indicator.open();
	            forgetPassword({phone:this.phone,code:this.code,password:this.password,repassword:this.repassword}).then((res)=>{
	            	Indicator.close();
                    if(res.code==200){
                        Toast({message:res.msg,position:'top'});
                        Lockr.rm('token');
                        setTimeout(() => {
                        	this.$router.push({
								path: '/login'
							});
						}, 2000);
                    }else{
                    	Toast({message:res.msg,position:'top'});
                    }
                })
			}
		},
		mounted(){
			this.phone=this.$route.query.phone
			this.code=this.$route.query.code
			console.log(this.$route.query.phone,this.$route.query.code)
		}
	}
</script>

<style scoped="scoped">
	.resetB_view{
		padding-top: 5rem;
	}
	.resetB_list{
		background: #FFFFFF;
	}
	.resetB_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.resetB_list li input{
		border: none;
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		float: right;
		text-align: right;
	}
</style>


