<template>
	<div class="community_det_view" v-show="is_show">
		<mt-header class="head" fixed title="话题详情">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<mt-button class="share" slot="right" @click="share"></mt-button>
		</mt-header>
		<div class="community_det" >
			<div class="community_det_head">
				<div class="head_name">
					<img :src="url+head_img"/>
					<p v-text="nickname"></p>
					<span v-text="create_time"></span>
				</div>
				<div @click="Collection" v-if="collection == false" class="collection"><img src="../../assets/img/fabulous_default@2x.png"/></div>
				<div @click="Collection" v-else class="collection"><img src="../../assets/img/fabulous_selected@2x.png"/></div>
			</div>
			<div class="community_det_content">
				<p v-text="content"></p>
				<div>
					<img v-for="(item,i) in img" @click="bigImg(i)" :src="url+'/v1/thumb?src='+url+item+'&w=200&h=200'"/>
				</div>
			</div>
			<div class="community_det_footer">
				<span class="comments_icon" v-text="commentcount"></span><i class="hr f-m"></i>
				<span class="share_icon" v-text="likecount"></span>
			</div>
		</div>
		<p style="text-align:center;padding:2rem 0; color: #AAAAAA;" v-if="comment_list.length==0">消灭零回复~</p>
		<div class="replyBox" v-if="comment_list.length>0">
			<div v-for="(item,i) in comment_list" class="reply_item">
				<img :src="url+item.head_img"/>
				<div class="userReply">
					<p class="userName" v-text="item.nickname"></p>
					<p class="replyTime" v-text="item.create_time"></p>
					<p class="replyContent" v-text="item.content" @click="reply(item)"></p>
					<div v-show="item.son.length>0" class="childReplyBox">
						<i></i>
						<p v-for="(vo,j) in item.son" @click="reply_reply(vo,item.id)">

							<span class="send">{{vo.u1_nickname}}</span> 回复<span class="receive" v-if="vo.u1_id!=vo.u2_id"> {{vo.u2_nickname}}</span>：{{vo.content}}
						</p>
					</div>
				</div>
			</div>
		</div>
		<div class="footer_input" id="send">
			<div><input maxlength="40" v-model="msg" type="text"  value="" placeholder="发表你的想法，参与讨论吧~" /></div>
			<button @click="comment()">发送</button>
		</div>
		<div v-show="shareLayer" class="share_layer" @click.stop="hideLayer"></div>
		<div v-show="bigImgShow" class="img_layer">
			<!--<img :src="original"/>-->
			<!--<img :src="url+'/v1/thumb?src='+original+'&w=400'"/>-->
		</div>
	</div>
</template>
<script src="http://res.wx.qq.com/open/js/jweixin-1.0.0.js"></script>
<script>
	import { Indicator,MessageBox,Toast} from 'mint-ui';
	import {pushInfo,pushComment,pushCollect} from '../../api/api';
	export default {
		data(){
			return{
				msg:'',
				url:'',
				id:'',
				head_img:'',
				bigImgShow:false,
				shareLayer:false,
				collection:false,
				index:0,
				nickname:'',
				create_time:'',
				likecount:'',
				commentcount:'',
				content:'',
				img:[],
				original:'',
				sharecount:24,
				comment_list:[],
				comment_id:0,
				to_uid:0,
				is_show:false
			}
		},
		methods:{
			Collection(){
				this.collection = !this.collection;
				var act=this.collection?'add':'del';
				pushCollect({id:this.id,act:act,token:this.getToken()}).then((res)=>{
	            	Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		if(this.collection){
	            			this.likecount+=1;
	            		}else{
	            			this.likecount-=1;
	            		}
	            	}
                })
			},
			comment(){
				if(this.msg==""){
					Toast({message:'先说点什么呗',position:'top'});
					$('.footer_input input').focus();
					return;
				}
				Indicator.open();
				pushComment({content:this.msg,comment_id:this.comment_id,to_uid:this.to_uid,push_id:this.id,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		this.msg="";
	            		$('.footer_input input').val('');
	            		this.info();
	            		this.top();
	            		this.comment_id=0;
						this.to_uid=0;
	            	}
                })
			},
			bigImg(i){
				if(i<0){
					i=0
				}else if(i>this.img.length-1){
					i = this.img.length-1;
				}
				Indicator.open();
				var bigImg = new Image();
				this.index = i;
//				this.original = this.url+this.img[i];
				this.bigImgShow = true;
				var w = $(document).width();
				bigImg.src = this.url+'/v1/thumb?src='+this.url+this.img[i]+'&w='+w;
				bigImg.onload = function(){
					$(".img_layer").html(bigImg);
					Indicator.close();
				}
				
			},
			reply(item){
				this.msg="";
				$('.footer_input input').focus();
				$('.footer_input input').attr('placeholder','@'+item.nickname);
				this.comment_id=item.id;
				this.to_uid=item.uid;
			},
			reply_reply(item,id){
				this.msg="";
				$('.footer_input input').focus();
				$('.footer_input input').attr('placeholder','@'+item.u1_nickname);
				this.comment_id=id;
				this.to_uid=item.u1_id;
			},
			info(){
				Indicator.open();
				pushInfo({id:this.id,token:this.getToken()}).then((res)=>{
	                Indicator.close()
	                if(res.code==200){
	                    this.id = res.data.id;
						this.head_img = res.data.head_img;
						this.nickname = res.data.nickname;
						this.create_time = res.data.create_time;
						this.likecount = res.data.likecount;
						this.commentcount = res.data.commentcount;
						this.content = res.data.content;
						this.img = res.data.image;
						this.sharecount = res.data.sharecount;
						this.comment_list=res.data.comment;
						this.collection=res.data.is_coll==1?true:false;
	                }else{
	                    this.$message({
	                        message: '请求失败',
	                        type: 'error'
	                    });
	                }
	                this.is_show=true;
	            })
			},
			share(){
				this.shareLayer = true;
			},
			hideLayer(){
				this.shareLayer = false;
				this.bigImgShow = false;
			}
		},
		created(){
			this.url=this.host()
			this.id=this.$route.query.id;
			this.info();
		},	
		mounted(){
			$('.footer_input input').blur(function(){
				this.msg="";
				$('.footer_input input').attr('placeholder','发表你的想法，参与讨论吧~');
			});
			$('.footer_input input').focus(function(){
				$('#send').scrollIntoView();
			});
			
			$(".community_det_view").on("touchmove",".share_layer",function(){
				event.preventDefault();
			});
			var that = this;
			var oldX=0,newX=0;
			$(".community_det_view").on("touchstart",".img_layer",function(){
				event.stopPropagation();
				oldX = event.changedTouches[0].clientX;
				event.preventDefault();
			});
			$(".community_det_view").on("touchend",".img_layer",function(){
				newX = event.changedTouches[0].clientX;
				if(oldX>newX+50){
					that.bigImg(that.index+1);
				}else if(oldX<newX-50){
					that.bigImg(that.index-1);
				}else if(oldX == newX){
					that.hideLayer();
				}
				event.preventDefault();
			})

		}
	}
</script>

<style scoped="scoped">
	body,html{
		width: 100%;
		height: 100%;
		font-family: sans-serif;
		-ms-text-size-adjust: 100%;
		-webkit-text-size-adjust: 100%;
		-webkit-font-smoothing: antialiased;
		-webkit-overflow-scrolling: touch;
	}
	.community_det_view{
		height: 100vh;
		padding-top: 5rem;
		padding-bottom: 5rem;
		background: #FFFFFF;
		box-sizing: border-box;
		overflow: auto;
	}
	.share{
		width: 1.6rem;
		height: 1.5rem;
		display: block;
		background: url(../../assets/img/forward_icon_white@2x.png);
		background-size: 100% 100%;
		position: relative;
		float: right;
	}
	.community_det{
		background: #FFFFFF;
	}
	.community_det_head{
		height: 5rem;
		display: flex;
		padding: 0 1.2rem;
		justify-content: space-between;
		align-items: center;
		color: #3C3C3C;
	}
	.head_name{
		width: 80%;
		height: 4rem;
		padding: 0.5rem 0 0.5rem 5rem;
		position: relative;
	}
	.head_name img{
		width: 4rem;
		height: 4rem;
		position: absolute;
		left: 0;
		top: 0.5rem;
		border-radius: 50%;
	}
	.head_name p{
		font-size: 1.5rem;
		line-height: 2rem;
	}
	.head_name span{
		font-size: 1.3rem;
		line-height: 2rem;
		color: #B1B1B1;
	}
	.collection{
		height: 1.3rem;
		font-size: 1.3rem;
		color: #ff6262;
		padding-left: 1.8rem;
		position: relative;
		line-height: 1.3rem;
	}
	.collection img{
		width: 1.5rem;
		height: 1.3rem;
		position: absolute;
		top: 0;
		left: 0;
	}
	.community_det_content{
		border-top: 1px solid #E8E8E8;
		border-bottom: 1px solid #E8E8E8;
		padding: 1rem 1.2rem 2.5rem;
	}
	.community_det_content p{
		font-size: 1.4rem;
		line-height: 2rem;
		padding-left: 1rem;
		position: relative;
		color: #3C3C3C;
		margin-bottom: 1rem;
	}
	.community_det_content p:before{
		content: "";
		position: absolute;
		width: 0.7rem;
		height: 0.7rem;
		border-radius: 50%;
		top: 0.7rem;
		left: 0;
		background: #FF6784;
	}
	.community_det_content div img{
		width: 30%;
		margin-top: 1rem;
		margin-right: 5%;
	}
	.community_det_content div img:nth-child(3n){
		margin-right: 0;
	}
	.community_det_footer{
		border-bottom: 1px solid #E8E8E8;
		height: 4rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		position: relative;
	}
	.community_det_footer .hr{
		width: 1px;
		height: 2rem;
		background: #E8E8E8;
	}
	.community_det_footer span{
		display: block;
		padding-left: 2.2rem;
		height: 1.5rem;
		line-height: 1.5rem;
		position: relative;
		color: #ADADAD;
	}
	.comments_icon{
		background: url(../../assets/img/comment_icon@2x.png) left center no-repeat;
		background-size: 1.6rem 1.5rem;
	}
	.share_icon{
		background: url(../../assets/img/fabulous_default@2x.png) left center no-repeat;
		background-size: 1.6rem 1.5rem;
	}
	/*.share_icon{
		background: url(../../assets/img/forward_icon@2x.png) left center no-repeat;
		background-size: 1.6rem 1.5rem;
	}*/
	/*评论区*/
	.reply_item{
		padding: 1.75rem 1.4rem 1.8rem 6rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
	}
	.reply_item:last-child{
		border-bottom: none;
	}
	.reply_item img{
		position: absolute;
		top: 1.75rem;
		left: 1.2rem;
		width: 4rem;
		height: 4rem;
		border-radius: 50%;
	}
	.userReply{
		font-size: 1.5rem;
		color: #3C3C3C;
	}
	.userReply .userName{
		margin-bottom: 0.5rem;
	}
	.userReply .replyTime{
		font-size: 1.3rem;
		color: #ADADAD;
		margin-bottom: 0.5rem;
	}
	.userReply .replyContent{
		line-height: 2rem;
		margin-bottom: 0.5rem;
	}
	.userReply .childReplyBox{
		padding: 1rem 1.4rem 1rem;
		background: #F5F5F5;
		position: relative;
	}
	.childReplyBox i{
		position: absolute;
		top: -1.4rem;
		left: 2rem;
		width: 0;
		height: 0;
		border: 0.7rem solid transparent;
		border-bottom-color: #F5F5F5;
	}
	.childReplyBox p{
		margin-bottom: 1rem;
		line-height: 1.8rem;
	}
	.childReplyBox p:last-child{
		margin-bottom: 0;
	}
	.childReplyBox p .send,
	.childReplyBox p .receive{
		color: #FF6784;
	}
	.footer_input{
		overflow: hidden;
	    width: 100%;
	    box-sizing: border-box;
	    padding: 1rem 1.2rem;
	    border-top: 1px solid #e8e8e8;
	    position: fixed;
	    bottom: 0;
	    left: 0;
	    background: #FFFFFF;
	}
	.footer_input div{
		float: left;
		width: 70%;
		height: 3rem;
		box-sizing: border-box;
		border-bottom: 1px solid #E8E8E8;
		padding-bottom: 0.5rem;
		padding-left: 2.8rem;
		background: url(../../assets/img/edit_icon@2x.png) 0.2rem 0.3rem  no-repeat;
		background-size: 1.9rem 1.9rem;
	}
	.footer_input div input{
		width: 100%;
		height: 100%;
		font-size: 1.4rem;
		border: none;
	}
	.footer_input button{
		height: 3rem;
		width: 6rem;
		border: none;
		border-radius: 1.5rem;
		color: #FFFFFF;
		font-size: 1.6rem;
		background: #007DDB;
		float: right;
	}
	.img_layer{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 10rem 0;
		box-sizing: border-box;
		overflow: hidden;
		z-index: 999999;
		background: rgba(0,0,0,0.5);
		display: flex;
		align-items: center;
	}
	.img_layer img{
		width: 100%;
	}
</style>


