<template>
	<div class="evaluation_view" v-show="is_show">
		<mt-header class="head" fixed title="发表评价">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span class="release" @click="submit()" slot="right" :style="{ color: subshow ? '' : '#aaa' }">{{subshow?'发布':'发布中'}}</span>
		</mt-header>
		<div style="margin-bottom:1rem">
			<div class="goods_msg">
				<router-link to="">
					<img :src="url+list.goods_img"/>
					<p v-text="list.title"></p>
					<span v-text="list.spec_key_name"></span>
				</router-link>
			</div>
			<div class="text">
				<textarea maxlength="120" v-model="content" placeholder="商品满意吗？分享给大家使用心得吧~"></textarea>
				<div class="imgbox">
					<img v-for="item in preview" :src="item" />
					<label>
						<input style="display: none;" type="file" value="" accept="image/*" @change='onUpload' />
						<i>添加照片</i>
					</label>
				</div>
				
			</div>
		</div>
	</div>
</template>

<script>
	import EXIF from 'exif-js'; 
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {addComment} from '../../api/api';
	export default {
		data(){
			return{
				url:'',
				is_show:false,
				content:'',
				order_id:'',
				img:[],
				list:[],
				upload:[],
				preview:[],
				subshow:true,//判断发布按钮是否可以点击
			}
		},
		methods:{
			onUpload(e){
				var img_length=e.target.files.length;
				if(this.img.length+img_length>9){
					Toast({message:'最多只能上传9张哦',position:'top'});
					return;
				}
				this.imageLoad(e,0);
			},
			imageLoad(e,i){
				var th = this;
				var url=this.host()+'/v1/base64ToImg';
				var images = new Image();
				var canvas = document.createElement('canvas');
				var context = canvas.getContext('2d');
		        var reader = new FileReader();
		        var file = event.target.files[i];
		        var orientation='';
		        var degree=0;
		        EXIF.getData(file, function() {    
                   	orientation = EXIF.getTag(this, 'Orientation');  
                }); 
		        var images = new Image();
		        reader.readAsDataURL(file);
		        reader.onload = function() {
				    images.src = event.target.result;
				};
				images.onload = function () {
				    // 图片原始尺寸
				    var originWidth = this.width;
				    var originHeight = this.height;
				    // 最大尺寸限制
				    var maxWidth = 800, maxHeight = 800;
				    // 目标尺寸
				    var targetWidth = originWidth, targetHeight = originHeight;
				    // 图片尺寸超过800x800的限制
				    if (originWidth > maxWidth || originHeight > maxHeight) {
				        if (originWidth / originHeight > maxWidth / maxHeight) {
				            // 更宽，按照宽度限定尺寸
				            targetWidth = maxWidth;
				            targetHeight = Math.round(maxWidth * (originHeight / originWidth));
				        } else {
				            targetHeight = maxHeight;
				            targetWidth = Math.round(maxHeight * (originWidth / originHeight));
				        }
				    }
				    var width='';
				    var height='';
				    canvas.width =width= targetWidth;
				    canvas.height =height= targetHeight;
				    // 清除画布
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    //判断图片方向，重置canvas大小，确定旋转角度，iphone默认的是home键在右方的横屏拍摄方式
				    var degree=0;
					switch(orientation){
						//iphone横屏拍摄，此时home键在左侧
						case 3:
							degree=180;
							targetWidth=-width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在下方(正常拿手机的方向)
						case 6:
							canvas.width=height;
							canvas.height=width;
							degree=90;
							targetWidth=width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在上方
						case 8:
							canvas.width=height;
							canvas.height=width;
							degree=270;
							targetWidth=-width;
							targetHeight=height;
							break;
					}
					//使用canvas旋转校正
					context.rotate(degree*Math.PI/180);
				    // 图片压缩
				    context.drawImage(images, 0, 0, targetWidth, targetHeight);
				    var base = canvas.toDataURL();
				    th.img.push(base);
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    canvas.width = 300;//重新设置画布大小
				    canvas.height = 300;
				    var previewImg = new Image();
				    previewImg.src = base;
				    previewImg.onload = function(){
				    	context.drawImage(previewImg, 0, 0, 300, 300);//重新把图片渲染进画布（压缩成300X300）
					    th.preview.push(canvas.toDataURL());
				    }
				    
				}   
			},
			submit(){
				if(this.content==""){
					Toast({message:'先说点什么呗',position:'top'});
					$('.text_box textarea').focus();
					return;
				}
				if(!this.subshow){
					return;
				}
				Indicator.open();
				this.subshow = false;
				var Image='';
				if(this.img.length>0){
					for (var i = 0; i < this.img.length; i++) {
						Image+=this.img[i]+'#####';
					}
				}
				Image = Image.substr(0, Image.length - 5); 
				addComment({order_id:this.order_id,gid:this.list.gid,content:this.content,image:Image,token:this.getToken()}).then((res)=>{
					Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		setTimeout(() => {
	            			this.$router.replace({
								path: '/order',
								query:{
									active:4,
								}
							});
							Indicator.close();
							this.subshow = true;
						}, 1000);
	            	}else{
	            		Indicator.close();
	            		this.subshow = true;
	            	}
	            	
               });
			}
		},
		created(){
	    	this.url=this.host();
	    	this.top();
	    	this.order_id = this.$route.query.order_id;
    		this.list.title=this.$route.query.title;
    		this.list.goods_img=this.$route.query.goods_img;
    		this.list.spec_key_name=this.$route.query.spec_key_name;
    		this.list.gid=this.$route.query.gid;   		
	    	this.is_show=true;
		},	
	}	
</script>

<style scoped="scoped">
	.imgbox{
		overflow: hidden;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}
	.imgbox img{
		width: 30%;
		margin-bottom: 1rem;
		margin-right: 5%;
	}
	.imgbox img:nth-child(3n){
		margin-right: 0;
	}
	.evaluation_view{
		padding-top: 5rem;
	}
	.goods_msg{
		height: 6.5rem;
		padding: 1.4rem 1.2rem 0;
		background: #FFFFFF;	
	}
	.goods_msg a{
		display: block;
		height: 100%;
		position: relative;
		padding-left: 6.5rem;
		font-size: 1.6rem;
	}
	.goods_msg a img{
		width: 5.5rem;
		height: 5.5rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.goods_msg a p{
		color: #3C3C3C;
		line-height: 2rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.goods_msg a span{
		color: #3C3C3C;
		font-size: 1.4rem;
		line-height: 1.8rem;
	}
	.text{
		line-height: 1.5;
		background: #FFFFFF;
		border-top: 1px solid #E8E8E8;
		border-bottom: 1px solid #E8E8E8;
		padding: 2rem 1.2rem;
		position: relative;
	}
	.text textarea{
		font-size: 1.6rem;
		width: 100%;
		height: 8rem;
		border: none;
		background: none;
		box-shadow: 0 0 1px rgba(0,0,0,0.5);
		padding: 1rem;
		box-sizing: border-box;
	}
	.text span{
		position: absolute;
		right: 1.2rem;
		top: 8rem;
		color:#BBBBBB
	}
	.text label{
		display: inline-block;
		width: 8rem;
		height: 8rem;
		border-radius: 0.5rem;
		border: 1px dashed #E8E8E8;
		text-align: center;
	}
	.text label i{
		display: block;
		padding-top: 5rem;
		text-align: center;
		color: #BFBFBF;
		font-size: 1.2rem;
		background: url(../../assets/img/photo_icon@2x.png) center center no-repeat;
		background-size: 3.2rem 2.8rem;
	}
</style>


