<template>
	<div class="address_view">
		<mt-header class="head" fixed title="收货地址">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<p v-if="list.length==0" class="empty">您的收货地址还是空空哒</p>
		<div class="address_item" v-for="(item,index) in list" ref="addr">
			<div class="address_item_top">
				<span class="userName" v-text="item.username"></span><span class="phone" v-text="item.phone"></span>
			</div>
			<div class="address">
				<div v-show="item.is_check" class="default_box">默认</div>
				<div class="address_text" v-text="item.province+item.city+item.area+item.detail"></div>
			</div>
			<div class="address_btn_box">
				<span v-show="!item.is_check" class="set_default" @click="setDef(index,item.id)">设置为默认地址</span>
				<span class="address_btn">
					<router-link class="edit"  :to="{path: 'editaddress', query: {id: item.id}}" replace></router-link>
					<i class="del" @click="Del(index,item.id)"></i>
				</span>
			</div>
		</div>
		<footer>
			<router-link to="/newaddress" replace>新增收货地址</router-link>
		</footer>
		<div v-show="loading" class="layer"></div>
	</div>
</template>

<script>
	import { Toast,Indicator,MessageBox} from 'mint-ui';
	import {addrList,delAddress,addrCheck} from '../../api/api';
	export default {
		data(){
			return{
				loading:false,
				delAlert:false,
				list:[]
			}
		},
		methods:{
			setDef(i,id){
				var index = i;
				this.list.forEach((item,i)=>{
					item.is_check=0;
					if(i==index){
						Indicator.open();
			            addrCheck({id:id,token:this.getToken()}).then((res)=>{
			            	Indicator.close();	
			            	Toast({message:res.msg,position:'top'});
		                    if(res.code==200){
		                    	item.is_check=1;
		                    }
		                })
					}
				})
			},
			Del(i,id){
				var that=this;
				MessageBox({
				  title: '是否删除',
				  message: '确定删除该收货地址吗？',
				  showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						Indicator.open();

			            delAddress({id:id,token:this.getToken()}).then((res)=>{
			            	Indicator.close();	
			            	Toast({message:res.msg,position:'top'});
		                    if(res.code==200){
		                    	that.list.splice(i,1);
		                    }
		                })
					}
				});
			},
			getList(){
				Indicator.open();
				this.loading = true;
				var that = this;
	            addrList({token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	that.loading = false; 	
                    if(res.code==200){
                        this.list=res.data;
                    }
                })
			}
		},
		created(){
			this.isLogin()
			this.getList()
		},	
	}
</script>

<style scoped="scoped">
	.layer{
		position: fixed;
		width: 100%;
		height: 100%;
		background: #f7f7f7;
		top: 0;
		left: 0;
		z-index: 0;
		transition: all .1s;
	}
	.address_item{
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		margin-bottom: 1rem;
		padding-left: 1.2rem;
	}
	.address_item .address_item_top{
		height: 4rem;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding-right: 1.2rem;
		font-size: 1.6rem;
	}
	.address{
		padding-right: 1.2rem;
		padding-left: 5.6rem;
		border-bottom: 1px solid #E8E8E8;
		padding-bottom: 1rem;
		font-size: 1.4rem;
		line-height: 1.8rem;
		position: relative;
	}
	.address .default_box{
		width: 4.6rem;
		height: 2.1rem;
		font-size: 1.2rem;
		border-radius: 1.25rem;
		background: #FFB853;
		color: #FFFFFF;
		text-align: center;
		line-height: 2.1rem;
		position: absolute;
		top: 0;
		left: 0;
	}
	.address .address_text{
		word-break: break-all;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.address_btn_box{
		height: 4.5rem;
		padding-right: 1.2rem;
		line-height: 4.5rem;
	}
	.address_btn_box .set_default{
		color: #FF0000;
		font-size: 1.4rem;
		float: left;
	}
	.address_btn_box .address_btn{
		float: right;
	}
	.address_btn_box .address_btn .edit,
	.address_btn_box .address_btn .del{
		display: inline-block;
		width: 1.5rem;
		height: 1.5rem;
		background: url(../../assets/img/edit_icon2@2x.png);
		background-size: 100% 100%;
	}
	.address_btn_box .address_btn .del{
		margin-left: 1.5rem;
		background: url(../../assets/img/delete_icon@2x.png);
		background-size: 100% 100%;
	}
	.address_view{
		padding-top: 5rem;
		padding-bottom: 5rem;
	}
	footer{
		position: fixed;
		width: 100%;
		height: 5rem;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 10;
		background: #FF0000;
	}
	footer a{
		display: block;
		width: 100%;
		height: 100%;
		line-height: 5rem;
		text-align: center;
		color: #FFFFFF;
		font-size: 1.7rem;
		letter-spacing: 0.2rem;
	}
</style>


