<template>
	<div class="afterservice_view">
		<mt-header class="head" fixed :title="info.days?'退车':'还车'">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="submit" :style="{ color: publish ? '#aaa' : '' }">{{publish ? '提交中':'提交'}}</span>
		</mt-header>
		<div v-show="is_show">
			<div class="afterservice_commodity"  v-for="item in list">
				<div class="img"><img :src="url+item.goods_img" /></div>
				<div class="text_box">
					<p class="title">{{item.goods_name}}</p>
					<p class="argument">{{info.type}}&nbsp;{{item.spec_key_name}}</p>
					<p style="color: #FF0000;" v-show="info.is_lease==1">{{info.day}}</p>
				</div>
			</div>
			<div class="afterservice_list">
				<div class="afterservice_reason" @click="reasonShow">{{info.days?'退车':'还车'}}原因<i class="arrow"></i>
					<p style="color: #909090;" v-if="selReason==''">请选择</p>
					<p v-else>{{selReason}}</p>
				</div>
				<div v-if="info.days==0" class="afterservice_money">已产生租金：<span style="color: #FF0000;">¥{{info.expense}}</span></div>
				<div class="afterservice_money">可退金额：<span v-if="info.days == 0" style="color: #FF0000;">¥{{info.total_price}}</span><span v-else >21天内免费退车（由买家承担运费）</span></div>
				<div v-if="info.days==0" class="invoice_box">
					<span>是否需要发票</span>
					<span class="invoice_radio">
						<label>
							<input type="radio" @click="invoiceShow" name="invoice" value="" style="display: none;"/>
							<i :class="invoice ? 'active':''"></i>是
						</label>
						<label>
							<input type="radio" @click="invoice=0;type=''" name="invoice" value="" style="display: none;"/>
							<i :class="invoice ? '':'active'"></i>否
						</label>
					</span>
				</div>
				<div v-if="invoice" class="address_box" @click="popupAddress=true;">
					<span>发票邮寄地址</span>
					<span class="address">{{address}}</span>
				</div>
				
			</div>
			<p v-if="invoice" style="margin:1rem;color: #FF0000;line-height: 1.8rem;">
				发票邮寄地址默认为该商品的收货地址！发票邮费需自行承担！
			</p>
			<div class="afterservice_explain">
				<textarea type="text" v-model="illustrate" value="" placeholder="请输入退车/还车原因" />
				<div class="imgbox">
					<img v-for="item in preview" :src="item" />
				</div>
				<div class="photo_box">
					<label>
						<input type="file" style="display: none;" accept="image/*" value=""  @change='onUpload'/>
						<img src="../../assets/img/photo_icon@2x.png" />
						<p>上传凭证</p>
					</label>
				</div>
			</div>
			<!--弹出层-->
			<div v-show="reasonBox" id="chooseview" class="shade">
				<div id="choosereason_view" class="choose">
					<ul class="choose_list">
						<li class="choose_title">请选择{{info.days?'退车':'还车'}}原因<img class="closecs" src="../../assets/img/cancel@2x.png" @click="close"/></li>
						<li class="choose_item" v-for="(item,index) in reasonItem[info.days]" v-text="item" @click="chooseRes(index)"></li>
					</ul>
				</div>
			</div>
			<!--发票popup层-->
			<mt-popup v-model="popupshow" position="right" class="mint-popup-3 invoice_view" :modal="false">
				<mt-header class="head" fixed title="发票信息">
				  	<mt-button @click="invoiceClose" slot="left" class="back"></mt-button>
				</mt-header>
				<ul class="invoice_list">
					<li class="invoice_type">类型
						<button :class="type ? 'active':''" @click="type=1;">单位</button>
						<button :class="type ? '':'active'" @click="type=0;invoice_TFN=''">个人</button>
					</li>
					<li class="invoice_title">
						<span>抬头</span><input type="text" v-model="invoice_title" placeholder="请填写准确的抬头名称" />
					</li>
					<li v-show="type" class="invoice_dutynum" id="dutynum">
						<span>税号</span><input type="tel" v-model="invoice_TFN" maxlength="20" value="" placeholder="请填写「税号」或「社会信用代码」" />
					</li>
				</ul>
				<p v-show="type" id="tiptext">可咨询公司财务部门获得「税号」或「社会信用代码」</p>
				<center><button class="saveBtn" @click="invoiceSave">保存</button></center>
		    </mt-popup>
		    <!--选择收货地址popup层-->
		    <mt-popup v-model="popupAddress" position="right" class="mint-popup-3 popupAddress" :modal="false">
				<sel-addr @selAdd="selAdds" @close="addsCloses"></sel-addr>
		    </mt-popup>
		</div>
	</div>
</template>

<script>
	import EXIF from 'exif-js'; 
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {apply,applyDetail,intoApply,address} from '../../api/api';
	import selAddr from '@/views/goods/sel_address.vue'
	export default {
		data(){
			return{
				typeBox:false,
				reasonBox:false,
				reasonItem:[
					['租赁还车'],
					['质量问题','租赁还车','其他']
				],
				invoice:0,//是否需要发票
				popupshow:false,//发票弹出层显示隐藏
				type:'',//税号类型,1=单位，0=个人
				invoice_title:'',//发票抬头
				invoice_TFN:'',//发票税号
				popupAddress:false,//选择地址弹出层显示隐藏
				username:'',//收件人
				phone:'',//联系方式
				address:'',//收货地址
				selType:'',
				selReason:'',
				money:'',
				order_id:'',
				img:[],//保存上传的凭证图片
				apply_money:0,
				illustrate:'',//退车原因
//				is_edit:false,
				is_show:false,
				list:[],
				info:[],
				preview:[],//预览图
				publish:false//发布状态
			}
		},
		methods:{
			reasonShow(){//退车原因弹出层
				this.reasonBox = true;
			},
			close(){//关闭退车原因弹出框
				this.reasonBox = false;
			},
			invoiceShow(){
				if(this.type == ''){
					this.type = 1;
				}
				this.invoice = 1;
				this.popupshow = true;
				this.username=this.info.consignee;
				this.phone = this.info.mobile;
				this.address = this.info.address;
			},
			invoiceClose(){//发票弹出层关闭
				if(this.type){
					if(this.invoice_title == '' || this.invoice_TFN == ''){
						this.invoice = 0;
						this.popupshow = false;
					}else{
						this.popupshow = false;
					}
				}else{
					if(this.invoice_title == ''){
						this.invoice = 0;
						this.popupshow = false;
					}else{
						this.popupshow = false;
					}
				}
			},
			selAdds(data){//如果需要选择地址
				this.popupAddress=false;
				this.username=data.username;
        		this.phone=data.phone;
        		this.address=data.province+data.city+data.area+data.detail;
			},
			addsCloses(){
				this.popupAddress=false;
			},
			
			chooseRes(i){
				this.selReason = this.reasonItem[this.info.days][i];
				this.reasonBox = false;
			},
			onUpload(e){
				var img_length=e.target.files.length;
				if(this.img.length+img_length>9){
					Toast({message:'最多只能上传9张哦',position:'top'});
					return;
				}
				this.imageLoad(e,0);
			},
			imageLoad(e,i){
				var th = this;
				var url=this.host()+'/v1/base64ToImg';
				var images = new Image();
				var canvas = document.createElement('canvas');
				var context = canvas.getContext('2d');
		        var reader = new FileReader();
		        var file = event.target.files[i];
		        var orientation='';
		        var degree=0;
		        EXIF.getData(file, function() {    
                   	orientation = EXIF.getTag(this, 'Orientation');  
                }); 
		        var images = new Image();
		        reader.readAsDataURL(file);
		        reader.onload = function() {
				    images.src = event.target.result;
				};
				images.onload = function () {
				    // 图片原始尺寸
				    var originWidth = this.width;
				    var originHeight = this.height;
				    // 最大尺寸限制
				    var maxWidth = 800, maxHeight = 800;
				    // 目标尺寸
				    var targetWidth = originWidth, targetHeight = originHeight;
				    // 图片尺寸超过1200x1200的限制
				    if (originWidth > maxWidth || originHeight > maxHeight) {
				        if (originWidth / originHeight > maxWidth / maxHeight) {
				            // 更宽，按照宽度限定尺寸
				            targetWidth = maxWidth;
				            targetHeight = Math.round(maxWidth * (originHeight / originWidth));
				        } else {
				            targetHeight = maxHeight;
				            targetWidth = Math.round(maxHeight * (originWidth / originHeight));
				        }
				    }
				    var width='';
				    var height='';
				    canvas.width =width= targetWidth;
				    canvas.height =height= targetHeight;
				    // 清除画布
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    //判断图片方向，重置canvas大小，确定旋转角度，iphone默认的是home键在右方的横屏拍摄方式
				    var degree=0;
					switch(orientation){
						//iphone横屏拍摄，此时home键在左侧
						case 3:
							degree=180;
							targetWidth=-width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在下方(正常拿手机的方向)
						case 6:
							canvas.width=height;
							canvas.height=width;
							degree=90;
							targetWidth=width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在上方
						case 8:
							canvas.width=height;
							canvas.height=width;
							degree=270;
							targetWidth=-width;
							targetHeight=height;
							break;
					}
					//使用canvas旋转校正
					context.rotate(degree*Math.PI/180);
				    // 图片压缩
				    context.drawImage(images, 0, 0, targetWidth, targetHeight);
				    var base = canvas.toDataURL();
				    th.img.push(base);
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    canvas.width = 300;//重新设置画布大小
				    canvas.height = 300;
				    var previewImg = new Image();
				    previewImg.src = base;
				    previewImg.onload = function(){
				    	context.drawImage(previewImg, 0, 0, 300, 300);//重新把图片渲染进画布（压缩成300X300）
					    th.preview.push(canvas.toDataURL());
				    }
				}   
			},
			invoiceSave(){
				if(this.type){
					if(this.invoice_title == ''){
						Toast({message:'请填写发票抬头',position:'top'});
					}else if(this.invoice_TFN == ''){
						Toast({message:'请填写发票税号',position:'top'});
					}else{
						this.popupshow = false;
					}
				}else{
					if(this.invoice_title == ''){
						Toast({message:'请填写发票抬头',position:'top'});
					}else{
						this.popupshow = false;
					}
				}
			},
			submit(){
				if(this.selReason==""){
					Toast({message:'请选择退车/还车原因',position:'top'});
					return;
				}
				if(this.selReason=="质量问题" && this.illustrate==""){
					Toast({message:'请输入退车/还车原因',position:'top'});
					return;
				}
				if(this.publish){
					return;
				}
				Indicator.open('上传中...');
				this.publish = true;
				var image='';
				if(this.img.length>0){
					for (var i = 0; i < this.img.length; i++) {
						image+=this.img[i]+'#####';
					}
				}
				image = image.substr(0, image.length - 5); 
				apply({
					oid:this.order_id,
					type:this.selType,//已去掉
					service_type:this.selReason,//退车原因
					apply_money:this.apply_money,//已去掉
					invoice:this.invoice,//0：不用发票，1要发票
					invoiceType:this.type,//true=单位，false=个人
					invoicetit:this.invoice_title,//发票抬头
					invoiceTFN:this.invoice_TFN,//发票税号
					address:this.address,//地址
					consignee:this.username,//收件人
					mobile:this.phone,//收件人号码
					illustrate:this.illustrate,//退车原因
					image:image,//凭证图片
					token:this.getToken()
					}).then((res)=>{
	            		Indicator.close();
	            		this.publish = false;
	            		Toast({message:res.msg,position:'top'});
		            	if(res.code==200){
		            		this.$router.replace({
								path: 'subsuc',
								query: { 
					                id: this.order_id, 
					            }
							});
		            	}
                })
			},
			applyinfo(){
				Indicator.open();
				intoApply({oid:this.order_id,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
		    			this.info = res.data.info;
		    			this.money = this.info.total_price;
	            		this.list=res.data.list;
	            		
            			if(this.info.type_id == 5){
            				this.info.type = '全新车';
            			}else if(this.info.type_id == 6){
            				this.info.type = '循环车A类';
            			}else{
            				this.info.type = '循环车B类';
            			}
            			
	            		this.username=this.info.consignee;
        				this.phone=this.info.mobile;
        				this.address=this.info.address;
	            	}
	            	this.is_show=true;
               })
			}	
		},
		components:{
			selAddr
		},
		watch:{
	    	popupshow:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".afterservice_view").css("position","fixed");
	    		}else{
	    			$(".afterservice_view").css("position","relative");
	    		}
	    	},
	    	addrBox:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".address_add_layer").on("touchmove",function(event){
	    				event.preventDefault();
	    			});
	    		}
	    	},
	    	invoice:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==false){
	    			this.type=true,//税号类型,true=单位，false=个人
					this.invoice_title='',//发票抬头
					this.invoice_TFN=''//发票税号
	    		}
	    	},
	    },
		created(){
	    	this.url=this.host();
	    	this.top();
	    	
	    	this.order_id = this.$route.query.order_id;
	    	this.applyinfo();
	    	this.is_show=true;
		},	
	}
</script>

