		flex: 1;
		text-align: right;
		word-break: break-all;
	}
	.invoice_view{
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		padding-top: 5rem;
		background: #FFFFFF;
		position: fixed;
		top: 0;
		left: 0;
		transform: translateY(0)!important;
		transition: all 0.2s ease-out;
	}
	.invoice_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.invoice_list .invoice_dutynum,
	.invoice_list .invoice_title{
		padding-left: 5rem;
	}
	.invoice_list .invoice_dutynum span,
	.invoice_list .invoice_title span{
		position: absolute;
		left: 1.2rem;
		top: 50%;
		transform: translateY(-50%);
	}
	.invoice_list .invoice_dutynum input,
	.invoice_list .invoice_title input{
		border: none;
		width: 100%;
		font-size: 1.5rem;
		height: 3rem;
		text-align: left;
	}
	.invoice_type button{
		background: transparent;
		width: 6rem;
		height: 2.6rem;
		border-radius: 0.3rem;
		border: 1px solid #E8E8E8;
		margin-left: 1rem;
		box-sizing: border-box;
	}
	.invoice_type button.active{
		border: 1px solid #5CACEE;
		color: #5CACEE;
	}
	.invoice_view p{
		color: gray;
		padding: 0.8rem;
		line-height: 1.7rem;
	}
	.saveBtn{
		background-color: #5CACEE;
		height: 4rem;
		border-radius: 0.3rem;
		border-width: 0;
		color: #FFFFFF;
		border-style:solid;
		width: 80%;
		font-size: 1.7rem;
		margin-top: 1rem;
	}
	
	.popupAddress{
		width: 100%;
		height: 100%;
	}
</style>


