<template>
	<div class="order_details_view">
		<mt-header class="head" fixed title="订单详情">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button> 
		</mt-header>
		<div v-show="is_show">
			<div class="order_msg">
				<h4>订单信息</h4>
				<span v-if="info.order_status==2" class="state">待付款</span>
				<span v-else-if="info.order_status==3" class="state">待发货</span>
				<span v-else-if="info.order_status==4" class="state">待收货</span>
				<span v-else-if="info.order_status==5" class="state">已完成</span>
				<span v-else-if="info.order_status==6" class="state">已取消</span>
				<ul>
					<li>订单状态
						<span v-if="info.order_status==2" class="order_steta">待付款订单</span>
						<span v-else-if="info.order_status==3" class="order_steta">待发货订单</span>
						<span v-else-if="info.order_status==4" class="order_steta">待收货订单</span>
						<span v-else-if="info.order_status==5" class="order_steta">已完成订单</span>
						<span v-else-if="info.order_status==6" class="order_steta">已取消</span>
					</li>
					<li>创建时间<span>{{info.create_time}}</span></li>
					<li>订单编号<span>{{info.order_sn}}</span></li>
				</ul>
			</div>
			<div class="consignee">
				<h4>收货人：<span>{{info.consignee}}</span><span class="f-r">手机：{{info.mobile}}</span></h4>
				<p>{{info.address}}</p>
			</div>
			<div class="order_details">
				<h4>订单详情</h4>
				<div v-for="(item,index) in info.detail" class="goods_details">
					<router-link :to="{path: 'goodsdet', query: {id: item.goods_id}}">
						<img :src="url+item.goods_img"/>
						<div>
							<p class="goods_name">{{item.goods_name}}</p>
							<p class="goods_arguments">{{info.type}}&nbsp;{{item.spec_key_name}}</p>
							<p class="price_num"><span>¥<i>{{item.goods_price}}</i></span> <span class="f-r">×<i>{{item.goods_num}}</i></span></p>
						</div>
					</router-link>
				</div>
				<div class="other">
					<p>配送方式<span>{{distribution}}</span></p>
				</div>
			</div>
			<div class="order_price">
				<h4>订单金额</h4>
				<ul>
					<li>支付方式<span>微信支付</span></li>
					<li>商品金额<span>¥<i>{{info.total_price}}</i></span></li>
					<li>运费<span>¥<i>0</i></span></li>
					<li>订单总价<span>¥<i>{{info.total_price}}</i></span></li>
				</ul>
				<div class="pay_amount">
					实付款（含运费）：<span>¥<i>{{info.total_price}}</i></span>
				</div>
			</div>
			<div v-if="info.order_status==2" class="goods_btn">
				<a class="close" @click="close()" href="javascript:void(0);">取消订单</a>
				<a class="pay" @click="submit()" href="javascript:void(0);">立即支付</a>
			</div>
			<div v-if="info.order_status==4" class="goods_btn">
				<router-link class="logistics_two" :to="{path: 'logistics', query: {id: info.order_id}}">查看物流</router-link>
				<a class="the_goods" @click="receiptOrder(info.order_id)" href="javascript:void(0);">确认收货</a>
			</div>
			<div v-if="info.order_status==5" class="goods_btn">
				<router-link class="logistics_two" :to="{path: 'logistics', query: {id: info.order_id}}">查看物流</router-link>
				<a v-if="info.server==false"  class="after-sales" @click="service()" href="javascript:void(0);">退车/还车</a>
				<a v-if="info.detail[0].is_comment==0"  class="evaluation" @click="evaluate()" href="javascript:void(0);">立即评价</a>
				<router-link v-if="info.server==true" class="after-sales"  :to="{path: 'subsuc', query: {id: info.order_id}}" replace>退车/还车</router-link>
				<a v-if="info.detail[0].is_comment==1" class="evaluation" @click="evaluateOK()">已评价</a>
				<!--<router-link v-if="info.detail[0].is_comment==1" class="evaluation" to="/evaluation">已评价</router-link>-->
			</div>
		</div>
	</div>
</template>

<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {detail,unOrder,delOrder,continueOrder,receipt} from '../../api/api';
	export default {
		data(){
			return{
				is_show:false,
				order_id:'',
				info:{},
				invoice:false,   //发票
				distribution:'快递 免邮',
			}
		},
		computed:{
			totalPrice(){
				this.orders.forEach((i)=>{
					this.total+= i.price*i.num;
				});
				return `${this.total}`
			},
			totalAmount(){
				return `${this.total+this.freight}`
			}
		},
		methods:{
			close(){
				MessageBox({
					title: '是否删除',
					message: '是否取消该订单？',
					showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						unOrder({order_id:this.order_id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.$router.go(-1);
			            	}
		                })
					} 					
				});
			},
			receiptOrder(order_id){
				MessageBox({
					title: '是否删除',
					message: '是否收到宝贝了么？',
					showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						receipt({id:order_id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.$router.go(0);
			            	}
		                })
					} 					
				});
			},
			getInfo(){
				Indicator.open();
				this.is_show=false
	            detail({order_id:this.order_id,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.info=res.data;
	            		console.log(this.info);
            			if(this.info.type_id == 5){
            				this.info.type = '全新车';
            			}else if(this.info.type_id == 6){
            				this.info.type = '循环车A类';
            			}else{
            				this.info.type = '循环车B类';
            				this.distribution = '快递 邮费自付';
            			}
	            	}
	            	this.is_show=true;
                })
			},
			submit(){
				var that=this
	            continueOrder({id:this.order_id,token:this.getToken()}).then((res)=>{
	            	if(res.code==200){
	            		WeixinJSBridge.invoke(
					       'getBrandWCPayRequest', {
					           "appId":res.data.appId,     //公众号名称，由商户传入     
					           "timeStamp":res.data.timeStamp,         //时间戳，自1970年以来的秒数     
					           "nonceStr":res.data.nonceStr, //随机串     
					           "package":res.data.package,     
					           "signType":res.data.signType,         //微信签名方式：     
					           "paySign":res.data.paySign //微信签名 
					       	},
					       	function(res){ 
					       		console.log(res);
					            if(res.err_msg == "get_brand_wcpay_request:ok" ) {
					            	Toast({message:'支付成功',position:'top'});
				                    that.$router.push({
										path: '/payment',
										query: { 
							                money: that.info.total_price, 
							            }
									});
					            } 
					            if(res.err_msg == "get_brand_wcpay_request:cancel" ) {
					            	MessageBox({
										message: '确定取消支付吗？',
										showCancelButton: true,
										closeOnClickModal:false,
									}).then(action => {
										if(action=='confirm'){
											Toast({message:'支付已取消',position:'top'});
							            	that.$router.push({
												path: '/order',
												query: { 
									                active: 1, 
									            }
											});
										} 					
									});
					            	
					            } 
					            if(res.err_msg == "get_brand_wcpay_request:fail" ) {
					                Toast({message:'支付失败',position:'top'});
					                that.$router.push({
										path: '/order',
										query: { 
							                active: 1, 
							            }
									});
					            } 
					       	}
					    )
	            	}
                })
			},
			service(){
			    var type='';
                if(this.info.type_id == 5){
                    type = '全新车';
                }else if(this.info.type_id == 6){
                    type = '循环车A类';
                }else{
                    type = '循环车B类';
                }
				this.$router.push({
					path: '/service',
					query: { 
		                title: this.info.detail[0].goods_name,
		                spec_key_name:type+'/'+this.info.detail[0].spec_key_name,
		                goods_img:this.info.detail[0].goods_img,
		                money:this.info.total_price,
		                order_id:this.order_id
		            }
				});
			},
			evaluate(){
                var type='';
                if(this.info.type_id == 5){
                    type = '全新车';
                }else if(this.info.type_id == 6){
                    type = '循环车A类';
                }else{
                    type = '循环车B类';
                }
                var title = this.info.detail[0].goods_name;
                var spec_key_name = type +'/'+ this.info.detail[0].spec_key_name;
                var goods_img = this.info.detail[0].goods_img;
                var gid = this.info.detail[0].goods_id;
				this.$router.push({
					path: '/goodsEval',
					query: { 
		                title: title, 
		                spec_key_name:spec_key_name,
		                goods_img:goods_img,
		                gid:gid,
		                order_id:this.order_id
		            }
				});

			},
            evaluateOK(){
                var gid = this.info.detail[0].goods_id;
                this.$router.push({
                    path: '/evaluation',
                    query: {
                        order_id:this.order_id
                    }
                });
            }
		},
		created(){
			this.isLogin()
			this.url=this.host()
	    	this.order_id = this.$route.query.id;
			this.getInfo()
		},	
	}
</script>

<style scoped="scoped">
	.order_details_view{
		padding-top: 5rem;
	}
	/*订单信息*/
	.order_msg{
		padding: 1.5rem 1.2rem 1.75rem;
		background: #FFFFFF;
		margin-bottom: 1rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
	}
	.order_msg h4{
		font-weight: normal;
		font-size: 1.6rem;
		padding-left: 2.15rem;
		background: url(../../assets/img/information_icon@2x.png) left center no-repeat;
		background-size: 1.5rem 1.5rem;
		margin-bottom: 1.8rem;
	}
	.order_msg ul li{
		margin-bottom: 1.2rem;
		color: #979797;
		font-size: 1.5rem;
	}
	.order_msg ul li:last-child{
		margin-bottom: 0;
	}
	.order_msg ul li .order_steta{
		color: #FF6784;
	}
	.order_msg ul li span{
		color: #3C3C3C;
		margin-left: 2rem;
	}
	.order_msg .state{
		position: absolute;
		top: 0;
		right: 1.2rem;
		width: 3.5rem;
		height: 6rem;
		color: #FFFFFF;
		font-size: 1.3rem;
		writing-mode:horizontal-tb;
		background: url(../../assets/img/label_icon@2x.png) no-repeat;
		background-size: 100% 100%;
		box-sizing: border-box;
		padding: 0.3rem 1.2rem;
		line-height: 1.2;
	}
	/*收货人*/
	.consignee{
		padding: 1.9rem 3.25rem 2.25rem 3.25rem;
		background: #FFFFFF;
		margin-bottom: 1rem;
		border-bottom: 1px solid #E8E8E8;
	}
	.consignee h4{
		font-weight: normal;
		font-size: 1.6rem;
		margin-bottom: 1.8rem;
	}
	.consignee p{
		position: relative;
		font-size: 1.4rem;
		line-height: 1.9rem;
	}
	.consignee p:before{
		content: "";
		position: absolute;
		width: 1.1rem;
		height: 1.5rem;
		top: 0;
		left: -2.2rem;
		background: url(../../assets/img/address_icon3@2x.png);
		background-size: 100% 100%;
	}
	/*订单详情*/
	.order_details{
		background: #FFFFFF;
		margin-bottom: 1rem;
		border-bottom: 1px solid #E8E8E8;
		padding: 1.6rem 0 0 1.2rem;
	}
	.order_details h4{
		font-weight: normal;
		font-size: 1.6rem;
		padding-left: 2.15rem;
		background: url(../../assets/img/details_icon@2x.png) left center no-repeat;
		background-size: 1.5rem 1.5rem;
		margin-bottom: 1.5rem;
	}
	.order_details .goods_details{
		height: 7rem;
		padding: 1rem 1.2rem 1rem 0;
	}
	.order_details .goods_details a{
		display: block;
		width: 100%;
		height: 100%;
		position: relative;
	}
	.order_details .goods_details a img{
		width: 7rem;
		height: 7rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.order_details .goods_details a div{
		padding-left: 10.85rem;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		font-size: 1.4rem;
	}
	.goods_details a div .goods_name{
		color: #525252;
		line-height: 1.8rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.goods_details a div .goods_arguments{
		color: #AEAEAE;
	}
	.goods_details a div .price_num{
		color: #3C3C3C;
	}
	.goods_details a div .price_num span:first-child{
		color: #FF3D3D;
		font-size: 1.5rem;
	}
	.other{
		color: #979797;
		font-size: 1.5rem;
		height: 4.5rem;
		line-height: 4.5rem;
		padding-right: 1.2rem;
	}
	.other:first-child{
		border-bottom: 1px solid #E8E8E8;
	}
	.other p span{
		color: #393838;
		float: right;
	}
	.order_price{
		background: #FFFFFF;
		padding: 1.5rem 0 0 1.2rem;
	}
	.order_price h4{
		font-weight: normal;
		font-size: 1.6rem;
		padding-left: 2.15rem;
		background: url(../../assets/img/money_icon@2x.png) left center no-repeat;
		background-size: 1.5rem 1.5rem;
		margin-bottom: 2rem;
	}
	.order_price ul{
		padding-bottom: 0.6rem;
		border-bottom: 1px solid #E8E8E8;
	}
	.order_price ul li{
		font-size: 1.5rem;
		color: #979797;
		margin-bottom: 0.8rem;
		padding-right: 1.2rem;
	}
	.order_price ul li span{
		float: right;
		color: #3C3C3C;
	}
	.pay_amount{
		height: 4rem;
		line-height: 4rem;
		color: #393838;
		text-align: right;
		padding-right: 1.2rem;
	}
	.pay_amount span{
		color: #FF3D3D;
	}
	.goods_btn{
		height: 5.9rem;
		background: #FFFFFF;
		text-align: right;
		padding: 0 1.2rem;
		line-height: 5.9rem;
		margin-bottom: 0;
	}
	.goods_btn a{
		display: inline-block;
		padding: 0 1rem;
		height: 2.5rem;
		line-height: 2.5rem;
		border-radius: 1.25rem;
		text-align: center;
		font-size: 1.5rem;
	}
	.goods_btn .logistics_two{
		color: #A8A8A8;
		border: 1px solid #A8A8A8;
		margin-right: 1rem;
	}
	.goods_btn .close,
	.goods_btn .logistics,
	.goods_btn .after-sales{
		color: #FFB853;
		border: 1px solid #FFB853;
		margin-right: 1rem;
	}
	.goods_btn .pay,
	.goods_btn .the_goods,
	.goods_btn .evaluation{
		color: #FFFFFF;
		background: #FF6784;
	}
</style>


