<template>
	<div class="shop_cart_view">
		<mt-header class="head" fixed title="购物车">
		  	<span @click="allDel" v-if="list.length>0" slot="right">删除</span> 
		</mt-header>
		<div v-show="loading" style="height:100%">
			<div class="shop_cart_list">
				<div v-for="(item,index) in list" class="shop_cart_item">
					<div class="goods_head">
						<i @click="Sel(index)" :class="item.check ?'sel':'def'"></i>
						<span v-if="!item.edit">
							<a class="edit" @click="edits(index,item)" href="javascript:;"></a>
							<a class="del" @click="Del(index,item.id)" href="javascript:;"></a>
						</span>
						<button v-if="item.edit" @click="edits(index,item)">完成</button>
					</div>
					<div class="goods_msg_box">
						<div class="goods_msg_img img_box">
							<img :src="url+item.img_thumb"/>
						</div>
						<transition>
							<div v-if="!item.edit" class="goods_msg_one">
								<p v-text="item.title" class="goods_msg_title"></p>
								<p class="goods_msg_arguments">{{item.type}}&nbsp;{{item.attr_name}}</p>
								<p class="goods_msg_price"><span>¥<i>{{item.price}}</i></span><span class="num">×<i>{{item.num}}</i></span></p>
							</div>
							<div v-if="item.edit" class="goods_msg_two">
								<div class="quantity">
									<button class="del" @click="delNum(index)">-</button>
									<input type="number" v-model="item.num" readonly="readonly" disabled="disabled" style="background: #FFFFFF;" />
									<button class="add" @click="addNum(index)">+</button>
								</div>
								<p class="arguments_sel" @click="argSel(index,item)"><span>{{item.attr_name}}</span><i></i></p>
							</div>
						</transition>
					</div>
				</div>
			</div>
			<div v-show="argBox" class="arguments_layer">
				<div class="arguments_box">
					<div class="arguments_head">
						<img :src="url+img_thumb"/>
						<div>
							<p class="arguments_price">¥<i>{{price}}</i></p>
							<p class="inventory">库存{{argument.inventory}}件</p>
						</div>
						<span @click="argBox=false"></span>
					</div>
					<div class="color">
						<p>颜色</p>
						<ul>
							<li v-for="(col,i) in argument" :class="item.attr_name == col.item_name?'active':''" @click="checkColor(i)" >{{col.item_name}}</li>
						</ul>
					</div>
					<button @click="argChange(item)">确定</button>
				</div>
			</div>
			<p v-if="list.length==0" style="margin: 8rem auto;text-align: center;color: #BFBFBF;font-size: 1.8rem;letter-spacing: 0.2rem;">购物车里空空如也~</p>
			<div v-if="list.length>0" class="settle_accounts">
				<div class="total_box">
					<div class="total">
						合计：<span>¥{{countPrice}}</span>
					</div>
				</div>
				<a href="javascript:;" @click="submit()">结算（{{countNum}}）</a>
			</div>
		</div>
	</div>
</template>

<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {cart,delCart,editCart,attribute} from '../api/api';
	export default {
	    data() {
	        return {
	        	url:'',
	            loading: false,
	            end:0,
	            totalPrice:0,
	            totalNum:0,
	            argBox:false,
	            argument:[],
	            item:[],
	            index:0,
	            img_thumb:'',
	            price:0,
	            inventory:0,
	            list:[],
	            id:[],
				num:[],
				itemid:[],
	        };
	    },
	    methods: {
	   		edits(i,item){
	   			this.list[i].edit=!this.list[i].edit;
	   			if(!this.list[i].edit){
	   				editCart({item_id:item.item_id,id:item.id,num:item.num,token:this.getToken()}).then((res)=>{
		            	if(res.code==200){
		            		// 
		            	}
	                })
	   			}
	   		},
	   		allDel(){//头部删除
	   			var that = this;
	   			MessageBox({
				  title: '是否删除',
				  message: '是否将选中的商品移出购物车？',
				  showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						var id='';
						for(var i=0;i<that.list.length;i++){
			   				if(that.list[i].check==true){
			   					id+=that.list[i].id+',';
			   					that.list.splice(i,1);
			   					i--;
			   				}
			   			}
			   			id = id.substr(0, id.length - 1); 
			   			if(id==""){
			   				Toast({message:'请选择要删除的商品',position:'top'});
			   				return;
			   			}
		   				delCart({id:id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
		                })
					} 					
				});
	   		},
	   		Del(i,id){
	   			MessageBox({
				  title: '是否删除',
				  message: '是否将此商品移出购物车？',
				  showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						delCart({id:id,token:this.getToken()}).then((res)=>{
			            	Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.list.splice(i,1);
			            	}
		               })
					} 					
				});
	   		},
	   		Sel(i){	//单选按钮	
	   			var th = this.list[i];
	   			this.list[i].check = !this.list[i].check;
	   			this.list.forEach((item)=>{
	   				if(item != th){
	   					item.check = false;
	   				}
	   			});
	   		},
	   		addNum(i){
	   			this.list[i].num++;
	   		},
	   		delNum(i){
	   			this.list[i].num--;
	   			if(this.list[i].num==0){
	   				this.list[i].num=1;
	   			}
	   		},
	   		argSel(i,item){
	   			this.item=item;
	   			this.index=i;
	   			this.price = item.price;
	   			attribute({id:item.gid}).then((res)=>{
	            	if(res.code==200){
	            		this.img_thumb=item.img_thumb;
	            		this.argument=res.data;
			   			this.argBox = true;
			        }	
            	})
	   		},
	   		checkColor(i){
	   			this.price = this.argument[i].price;
	   			this.list[this.index].attr_name=this.argument[i].item_name;
	   			this.list[this.index].item_id=this.argument[i].itemid;
	   		},
	   		argChange(){
	   			this.argBox = false;
	   		},
	   		getList(){
				Indicator.open();
	            cart({token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	this.loading=true;
	            	if(res.code==200){
	            		this.list=res.data;
	            		this.list.forEach((i)=>{
	            			if(i.type_id == 5){
	            				i.type = '全新车';
	            			}else if(i.type_id == 6){
	            				i.type = '循环车A类';
	            			}else{
	            				i.type = '循环车B类';
	            			}
	            		})
	            	}
                })
			},
			submit(){
				this.id=[];
				this.num=[];
				this.itemid=[];
				var that = this;
				var buytype=1;
				for(var i=0;i<that.list.length;i++){
	   				if(that.list[i].check==true){
	   					that.id.push(that.list[i].gid);
	   					that.num.push(that.list[i].num);
	   					that.itemid.push(that.list[i].item_id);
	   					buytype=that.list[i].buytype
	   				}
	   			}
	   			if(that.id.length==0){
	   				Toast({message:'请选择要购买的商品',position:'top'});
	   				return;
	   			}
				this.$router.push({
					path: '/confirmOrder',
					query: { 
		                id: this.id, 
		                num: this.num,
		                itemid:this.itemid,
		                act:1,
		                buytype:buytype,
		            }
				});
			}
	    },
	    watch:{
	    	argBox:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".arguments_layer").on("touchmove",function(event){
	    				event.preventDefault();
	    			});
	    		}
	    	}
	    },
	    computed:{
	    	countPrice(){
	    		this.totalPrice=0;
	    		this.list.forEach((i)=>{
	    			if(i.check){
	    				var price = i.num * i.price;
	   					this.totalPrice += price;
	    			}
	   			});
	    		return `${this.totalPrice}`
	    	},
	    	countNum(){
	    		this.totalNum =0;
	    		this.list.forEach((i)=>{
	    			if(i.check){
	   					this.totalNum +=i.num;
	   				}	
	   			});
	    		return `${this.totalNum}`
	    	}
	    },
	    mounted() {
	    	function imgWH(){
				$.each($(".img_box"), function(i,item) {
					var w=0,h=0;
					h=$(item).height();
					w=h;
					$(item).find("img").css("width",w+"px").css("height",h+"px");
				});
			}
			imgWH();
			$(window).resize(function(){
				imgWH();
			});
	    },
	    created(){
	    	this.url=this.host()
			this.getList()
		},	
	};
</script>

<style scoped="scoped">
	/*参数选择*/
	.arguments_layer{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.3);
		z-index: 1100;
	}
	.arguments_box{
		width: 100%;
		box-sizing: border-box;
		background: #FFFFFF;
		position: absolute;
		bottom: 5rem;
		left: 0;
		right: 0;
	}
	.arguments_head{
		height: 6rem;
		padding-top: 1rem;
		position: relative;
	}
	.arguments_head img{
		width: 7rem;
		height: 7rem;
		border-radius: 1rem;
		position: absolute;
		left: 1.2rem;
		top: -1rem;
		border: 1px solid #B2B2B2;
	}
	.arguments_head div{
		padding-left: 10rem;
	}
	.arguments_head div .arguments_price{
		color: #FF3D3D;
		font-size: 1.9rem;
		margin-bottom: 1rem;	
	}
	.arguments_head div .inventory{
		font-size: 1.4rem;
		color: #3C3C3C;	
	}
	.arguments_head span{
		position: absolute;
		top: 1.5rem;
		right: 1.7rem;
		width: 2.2rem;
		height: 2.2rem;
		background: url(../assets/img/delete_icon2@2x.png);
		background-size: 100% 100%;
	}
	.color{
		padding: 0 1.2rem 1.5rem;
		border-bottom: 1px solid #F1F1F1;
	}
	.color p{
		height: 3rem;
		line-height: 3rem;
		font-size: 1.5rem;
		color: #525252;
	}
	.color ul{
		overflow: hidden;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
	}
	.color ul li{
		float: left;
		width: 21%;
		height: 2.5rem;
		border: 1px solid #A1A1A1;
		background: #FFFFFF;
		font-size: 1.4rem;
		line-height: 2.6rem;
		text-align: center;
		margin: 1rem 5% 0 0;
		color: #3C3C3C;
	}
	.color ul li.active{
		border: 1px solid #d53a3a;
		background: url(../assets/img/check.png) right bottom no-repeat;
		background-size: 1.6rem 1.4rem;
	}
	.color ul li:nth-child(4n){
		margin-right: 0;
	}
	.arguments_box button{
		width: 100%;
		height: 4.5rem;
		border: none;
		background: url(../assets/img/determine_button@2x.png);
		background-size: 100% 100%;
		color: #FFFFFF;
		font-size: 1.7rem;
	}
	
	.shop_cart_view{
		background: #F7F7F7;
		padding-top: 5rem;
		padding-bottom: 10rem;
	}
	.shop_cart_item{
		margin-bottom: 0.9rem;
		background: #FFFFFF;
	}
	.goods_head{
		padding: 0 1.2rem;
		height: 3.5rem;
		display: flex;
		justify-content:space-between;
		align-items: center;
	}
	.goods_head .def{
		display: inline-block;
		width: 1.6rem;
		height: 1.6rem;
		background: url(../assets/img/no-selected@2x.png);
		background-size: 100% 100%;
	}
	.goods_head .sel{
		display: inline-block;
		width: 1.6rem;
		height: 1.6rem;
		background: url(../assets/img/selected_icon@2x.png);
		background-size: 100% 100%;
	}
	.goods_head .edit,
	.goods_head .del{
		display: inline-block;
		width: 1.5rem;
		height: 1.5rem;
		margin-right: 1.5rem;
		background: url(../assets/img/edit_icon2@2x.png);
		background-size: 100% 100%;
	}
	.goods_head .del{
		margin-right: 0;
		background: url(../assets/img/delete_icon@2x.png);
		background-size: 100% 100%;
	}
	.goods_head button{
		border: none;
		background: none;
		color: #3C3C3C;
		font-size: 1.5rem;
	}
	.goods_msg_box{
		height: 10rem;
		box-sizing: border-box;
		padding: 1rem 1.2rem;
		border-top: 1px solid #E8E8E8;
		border-bottom: 1px solid #E8E8E8;
		display: flex;
	}
	.goods_msg_img{
		height: 100%;
		margin-right: 1.5rem;
	}
	.goods_msg_img img{
		height: 100%;
	}
	.goods_msg_one{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.goods_msg_one .goods_msg_title{
		font-size: 1.4rem;
		color: #525252;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		line-height: 1.8rem;
	}
	.goods_msg_one .goods_msg_arguments{
		color: #AEAEAE;
		font-size: 1.4rem;
		line-height: 1.6rem;
	}
	.goods_msg_one .goods_msg_price{
		color: #FF3D3D;
		font-size: 1.5rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.goods_msg_price .num{
		color: #3C3C3C;
		font-size: 1.4rem;
	}
	.goods_msg_two{
		width: 100%;
		padding: 0 1rem;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.quantity,
	.arguments_sel{
		width: 12rem;
		height: 3rem;
		line-height: 3rem;
		border-radius: 0.1rem;
		border: 1px solid #E8E8E8;
		display: flex;
		justify-content: space-between;
	}
	.quantity button{
		width: 30%;
		height: 100%;
		border: none;
		background: none;
		font-size: 2rem;
		font-weight: bold;
		color: #C6C6C6;
	}
	.quantity .del{
		border-right: 1px solid #E8E8E8;
	}
	.quantity .add{
		border-left: 1px solid #E8E8E8;
	}
	.quantity input{
		width: 30%;
		height: 100%;
		text-align: center;
		border: none;
		font-size: 1.5rem;
	}
	.arguments_sel{
		box-sizing: border-box;
		padding: 0 0.9rem;
		font-size: 1.3rem;
		position: relative;
	}
	.arguments_sel i{
		position: absolute;
		width: 0.7rem;
		height: 1.3rem;
		top: 50%;
		right: 0.9rem;
		transform: translateY(-50%);
		background: url(../assets/img/next@2x.png);
		background-size: 100% 100%;
	}
	.settle_accounts{
		width: 100%;
		height: 4.5rem;
		background: #FFFFFF;
		position: fixed;
		left: 0;
		right: 0;
		bottom: 5rem;
		z-index: 100;
	}
	.total_box{
		height: 100%;
		display: flex;
		flex-direction: row;
		padding:0 11.2rem 0 1.2rem;
		justify-content: space-between;
		align-items: center;
		font-size: 1.5rem;
		color: #3C3C3C;
		box-sizing: border-box;
	}
	.total_box .allSelBtn i.alldef{
		display: inline-block;
		width: 1.6rem;
		height: 1.6rem;
		background: url(../assets/img/no-selected@2x.png);
		background-size: 100% 100%;
		float: left;
		margin-right: 0.5rem;
		position: relative;
		top: -2px;
	}
	.total_box .allSelBtn i.allsel{
		display: inline-block;
		width: 1.6rem;
		height: 1.6rem;
		float: left;
		margin-right: 0.5rem;
		position: relative;
		top: -2px;
		background: url(../assets/img/selected_icon@2x.png);
		background-size: 100% 100%;
	}
	.total_box .total span{
		color: #FF3D3D;
	}
	.settle_accounts a{
		position: absolute;
		right: 0;
		bottom: 0;
		width: 10rem;
		height: 4.5rem;
		line-height: 4.5rem;
		text-align: center;
		color: #FFFFFF;
		background: #FF0000;
		font-size: 1.6rem;
	}
</style>


