<template>
	<div class="leave_msg_view">
		<mt-header class="head" fixed title="留言反馈">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="submit()">提交</span>
		</mt-header>
		<div class="text">
			<textarea maxlength="130" v-model="msgText" placeholder="您有什么意见或者建议，快来告诉我吧！"></textarea><span>{{length}}字</span>
		</div>
		
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {feedback} from '../../api/api';
	export default {
		data(){
			return{
				msgText:'',
				len : 130
			}
		},
		computed:{
			length(){
				return `${this.len - this.msgText.length}`
			}
		},
		methods:{
			submit(){
				if(this.msgText==""){
					Toast({message:'您还没说点什么呢',position:'top'});
					return;
				}
				Indicator.open();
				feedback({detail:this.msgText,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		setTimeout(() => {
                        	this.$router.replace({
								path: '/user'
							});
						}, 2000);
	            	}
                })
			},
			
		},
		created(){
			this.isLogin()
		},

	}
</script>
<style scoped="scoped">
	.leave_msg_view{
		padding-top: 5rem;
	}
	.text{
		height: 14rem;
		line-height: 1.5;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		padding: 2rem 1.2rem;
		position: relative;
	}
	.text textarea{
		font-size: 1.6rem;
		width: 100%;
		height: 100%;
		color: #3C3C3C;
		border: none;
		background: none;
	}
	.text span{
		position: absolute;
		right: 1.2rem;
		bottom: 2rem;
		color:#BBBBBB
	}
</style>


