import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Index from '@/views/index.vue'
import Class from '@/views/class.vue'
import Search from '@/views/search.vue'
import User from '@/views/user.vue'
import ShopCart from '@/views/shop_cart.vue'
import Community from '@/views/community.vue'
import Release from '@/views/community/release.vue'
import communityDet from '@/views/community/community_details.vue'
import Login from '@/views/wxlogin.vue'
import Register from '@/views/register.vue'
import PwdresetOne from '@/views/pwdresetA.vue'
import PwdresetTwo from '@/views/pwdresetB.vue'
import MyOrder from '@/views/order/myOrder.vue'
import paySuccess from '@/views/order/pay_success.vue'
import logistics from '@/views/order/logistics.vue'
import goodsEvaluation from '@/views/order/goods_evaluation.vue'
import orderDetails from '@/views/order/order_details.vue' 
import Record from '@/views/user/record.vue'
import authentication from '@/views/user/authentication.vue'
import leaveMsg from '@/views/user/leave_msg.vue'
import Collection from '@/views/user/collection.vue'
import Evaluation from '@/views/user/evaluation.vue'
import myRelease from '@/views/user/myrelease.vue'
import message from '@/views/user/message.vue'
import Address from '@/views/user/address.vue'
import AddressNew from '@/views/user/address_new.vue'
import AddressEdit from '@/views/user/address_edit.vue'
import bindingNum from '@/views/options/bindingnum.vue'
import bindingSuc from '@/views/options/bindingsuc.vue'
import changeNum from '@/views/options/changenum.vue'
import options from '@/views/options/options.vue'
import pwdreSet from '@/views/options/pwdresetC.vue'
import personalData from '@/views/options/personaldata.vue'
import Service from '@/views/afterservice/afterservice.vue'
import Subsuc from '@/views/afterservice/subsuccessful.vue'
import goodsDet from '@/views/goods/goods_details.vue'
import confOrder from '@/views/goods/confirm_order.vue'
import invoice from '@/views/order/invoice.vue'
import NotFound from '@/views/404.vue'
import about from '@/views/about.vue'
import contact from '@/views/contact.vue'
Vue.use(Router)

export default new Router({
	mode: 'history',
  routes: [
        { path: '/', redirect: '/index'},
        {
            path: '/',
            component: Home,
            children: [
                { path: '/index', component: Index, name: '首页'},
                { path: '/class',component: Class,name: '分类'},
                { path: '/user', component: User, name: '会员中心'},
                { path: '/cart', component: ShopCart, name: '购物车' },
                { path: '/community', component: Community, name: 'ET妈妈' }
            ]
        },
        {
            path: '/about',
            component: about,
            name:'关于亦特'
        },
        {
            path: '/contact',
            component: contact,
            name:'联系我们'
        },
        {
            path: '/invoice',
            component: invoice,
            name:'发票'
        },
        {
            path: '/search',
            component: Search,
            name:'搜索'
        },
        {
            path: '/order',
            component: MyOrder,
            name:'我的订单'
        },
        {
        	path: '/service',
            component: Service,
            name:'售后服务'
        },
        {
        	path: '/subsuc',
            component: Subsuc,
            name:'售后详情'
        },
        {
        	path: '/orderDetails',
            component: orderDetails,
            name:'订单详情'
        },
        {
        	path:'/payment',
        	component:paySuccess,
        	name:'支付成功'
        },
        {
        	path:'/logistics',
        	component:logistics,
        	name:'查看物流'
        },
        {
        	path:'/goodsEval',
        	component:goodsEvaluation,
        	name:'商品评价'
        },
		{
			path:'/login',
			component: Login,
			name:'登录'
		},
		{
			path:'/register',
			component: Register,
			name:'注册'
		},
		{
			path:'/resetOne',
			component: PwdresetOne,
			name:'修改密码'
		},
		{
			path:'/resetTwo',
			component: PwdresetTwo,
			name:'修改密码二'
		},
		{
			path:'/release',
			component: Release,
			name:'发布动态'
		},
		{
			path:'/topicDet',
			component: communityDet,
			name:'话题详情'
		},
		{
			path:'/authentication',
			component: authentication,
			name:'身份认证'
		},
		{
			path:'/record',
			component: Record,
			name:'浏览记录'
		},
		{
			path:'/leaveMsg',
			component: leaveMsg,
			name:'留言'
		},
		{
			path:'/collection',
			component: Collection,
			name:'收藏'
		},
		{
			path:'/evaluation',
			component: Evaluation,
			name:'评价'
		},
		{
			path:'/myrelease',
			component: myRelease,
			name:'我的发布'
		},
		{
			path:'/message',
			component: message,
			name:'消息'
		},
		{
			path:'/address',
			component: Address,
			name:'收货地址'
		},
		{
			path:'/newaddress',
			component: AddressNew,
			name:'新增地址'
		},
		{
			path:'/editaddress',
			component: AddressEdit,
			name:'编辑地址'
		},
		{
			path:'/bindingnum',
			component: bindingNum,
			name:'绑定手机号码'
		},
		{
			path:'/bindingsuc',
			component: bindingSuc,
			name:'绑定手机号码完成'
		},
		{
			path:'/changenum',
			component: changeNum,
			name:'更换手机号'
		},
		{
			path:'/options',
			component: options,
			name:'设置'
		},
		{
			path:'/pwdreset',
			component: pwdreSet,
			name:'密码重置'
		},
		{
			path:'/personal',
			component: personalData,
			name:'修改资料'
		},
		{
			path:'/goodsdet',
			component: goodsDet,
			name:'商品详情'
		},
		{
			path:'/confirmOrder',
			component: confOrder,
			name:'确认订单'
		},
		{ 
			path: '/404', 
			component: NotFound, 
			name: '404' 
		},
		{
	        path: '*',
	        hidden: true,
	        redirect: { path: '/404' }
	    }
    ]
})



