<template>
	<div class="subsuccessful_view">
		<mt-header class="head" fixed title="退还申请">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div v-show="is_show">
			<div class="subsuccessful_img">
				<div class="img_content"><img src="../../assets/img/submission_icon@2x.png" style="width: 6.2rem;" /></div>
				<div class="text_box" v-if="info.isagree_goods==0">
					<p class="title">已提交</p>
					<p class="argument">您的申请已提交，请耐心接受审核</p>
				</div>
				<div class="text_box" v-else-if="info.isagree_goods==1">
					<p class="title">已同意</p>
					<p class="argument">您的申请已同意</p>
				</div>
				<div class="text_box" v-else>
					<p class="title">已拒绝</p>
					<p class="argument">您的申请已拒绝</p>
				</div>
			</div>
			<div class="subsuccessful_img" style="margin-top: 0.9rem;" v-for="item in list">
				<div class="img_content"><img :src="url+item.goods_img" /></div>
				<div class="text_box">
					<p class="title" v-text="item.goods_name"></p>
					<p class="argument">
						<i v-if="mess.type_id==5">全新车</i>
						<i v-else-if="mess.type_id==6">循环车A类</i>
						<i v-else-if="mess.type_id==7">循环车B类</i>
						{{item.spec_key_name}}
					</p>
				</div>
			</div>
			<div class="subsuccessful_list">
				<div class="subsuccessful_reason"><i>退款原因</i><span id="reasontext">{{info.service_type}}</span></div>
				<div class="afterservice_money"><i>已产生租金</i><span style="color: #FF0000;">¥{{mess.expense}}</span></div>
				<div class="afterservice_money"><i>可退款金额</i><span style="color: #FF0000;">¥{{mess.total_price}}</span></div>
				<div v-if="mess.days==0" class="hr"></div>
				<div v-if="mess.days==0" class="afterservice_money"><i>是否需要发票</i><span>{{info.is_invoice?'是':'否'}}</span></div>
				<div v-if="info.is_invoice==1" class="afterservice_money"><i>发票类型</i><span>{{mess.is_person?'单位':'个人'}}</span></div>
				<div v-if="info.is_invoice==1" class="afterservice_money"><i>发票抬头</i><span>{{mess.name}}</span></div>
				<div v-if="mess.is_person==1" class="afterservice_money"><i>发票税号</i><span>{{mess.tax_num}}</span></div>
				<div v-if="mess.days==0" class="hr"></div>
				<div v-if="info.is_invoice==1" class="afterservice_money"><i>发票收件人</i><span>{{mess.consignee}}</span></div>
				<div v-if="info.is_invoice==1" class="afterservice_money"><i>手机号</i><span>{{mess.mobile}}</span></div>
				<div v-if="info.is_invoice==1" class="afterservice_money"><i>发票邮寄地址</i><span>{{mess.address}}</span></div>
			
			</div>
			<div class="subsuccessful_explain">
				<p style="color: #A0A2A5; padding: 1rem 1.2rem 0;line-height: 2rem;">退车/还车原因:</p>
				<p v-text="info.illustrate"></p>
				<div class="photo_content">
					<div class="img_box" v-for="item in info.images"><img :src="url+item" /></div>
				</div>
			</div>
			<div class="subsuccessful_btn" v-if="info.isagree_goods==0">
				<a class="pay" @click="cancelApply()" href="javascript:void(0);">取消申请</a>
			</div>
		</div>
	</div>
</template>

<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {applyDetail,cancel} from '../../api/api';
	export default {
		data(){
			return{
				is_show:false,
				url:'',
				order_id:'',
				info:[],
				list:[],
				mess:{},
			}
		},
		methods:{
			getInfo(){
				Indicator.open();
				applyDetail({oid:this.order_id,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.info=res.data.list;
	            		this.list=res.data.info;
	            		this.mess=res.data.mess;
	            	}
	            	this.is_show=true;
                })
			},
			cancelApply(){
				MessageBox({
				  title: '是否取消申请',
				  message: '是否取消申请？取消申请后可以重新申请！',
				  showCancelButton: true,
				}).then(action => {
					if(action=='confirm'){
						cancel({id:this.info.id,token:this.getToken()}).then((res)=>{
							Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.$router.replace({
									path: 'order',
									query: { 
						                active: 4, 
						            }
								});
			            	}
		                });
					} 					
				});
				
			}
		},
		mounted() {
	    	$(function(){
				function imgWH(){
					$.each($(".img_box"), function(i,item) {
						var w=0,h=0;
						w=$(item).width();
						h=w;
						$(item).find("img").css("width",w+"px").css("height",h+"px");
					});
				}
				imgWH();
				$(window).resize(function(){
					imgWH();
				});
			});
	    },
	    created(){
	    	this.url=this.host()
	    	this.order_id = this.$route.query.id;
	    	this.getInfo();
		},	
	}
</script>

<style scoped="scoped">
	
	.subsuccessful_view{
		padding-top: 5rem;
	}
	.subsuccessful_img{
		width: 100%;
		height: 9.4rem;
		background: #ffffff;
		margin: 0 auto;
	}
	.subsuccessful_img .img_content{
		width: 9.5rem;
		height: 9.4rem;
		float: left;
		position: relative;
	}
	.subsuccessful_img .img_content img{
		width: 6.5rem;
		height: 6.5rem;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 1.2rem;
	}
	.subsuccessful_img .text_box{
		padding-left: 1rem;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	.text_box .title{
		color: #525252;
		font-size: 1.4rem;
		line-height: 1.8rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.text_box .argument{
		color: #AEAEAE;
		font-size: 1.4rem;
		line-height: 1.6rem;
	}
	.subsuccessful_img p{
		color: #525252;
		font-size: 1.4rem;
		line-height: 1.8rem;
		overflow: hidden;
	}
	.subsuccessful_list{
		margin-top: 0.9rem;
	}
	.subsuccessful_list div{
		line-height: 3rem;
		position: relative;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
		display: flex;
		justify-content: space-between;
	}
	.subsuccessful_list div.hr{
		height: 1rem;
		line-height: 1rem;
		background: #F7F7F7;
		border: none;
		padding: 0;
	}
	.subsuccessful_list div{
		color: #3C3C3C;
	}
	.subsuccessful_list i{
		white-space: nowrap;
		flex: 1;
	}
	.subsuccessful_list span{
		flex: 1;
		text-align: right;
		word-break: break-all;
	}
	.subsuccessful_explain{
		width: 100%;
		background: #FFFFFF;
		margin: 0.9rem 0 0.2rem 0;
		overflow: hidden;
	}
	.subsuccessful_explain p{
		font-size: 1.5rem;
		padding: 1.4rem 1.2rem;
		line-height: 2rem;
	}
	.photo_content{
		padding:0 1.2rem;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		overflow: hidden;
	}
	.photo_content .img_box{
		width: 28vw;
		height: 28vw;
		margin:0 0 1rem 2%;
	}
	.photo_content img{
		/*display: block;
		width: 8rem;
		height: 8rem;*/
		width: 100%;
		height: 100%;
		border-radius: 0.2rem;
	}
	.subsuccessful_btn{
		height: 6.65rem;
		text-align: right;
		padding: 0 1.2rem;
		background: #FFFFFF;
	}
	.subsuccessful_btn a{
		margin-top: 1.7rem;
		display: inline-block;
		padding: 0.3rem 1rem;
		height: 2rem;
		line-height: 2rem;
		border-radius: 1.6rem;
		text-align: center;
		font-size: 1.5rem;
	}
	.subsuccessful_btn .close{
		color: #FFB853;
		border: 1px solid #FFB853;
		margin-right: 1rem;
	}
	.subsuccessful_btn .pay{
		color: #FFFFFF;
		background: #FF6784;
	}
	
</style>


