<template>
	<div class="record_view">
		<mt-header class="head" fixed title="浏览记录">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="deletes" v-if="list.length>0">删除</span>
		</mt-header>
		<div v-show="is_show">
			<p v-if="list.length==0" class="empty">暂无数据</p>
			<div v-if="list.length>0">
				<ul class="record_list">
					<li class="recird_item" v-for="(item,index) in list">
						<div class="item_title">
							<label><input style="display: none;" v-model="item.check" :checked="item.check" type="checkbox" /><i></i>{{item.date}}</label>
						</div>
						<div class="item_content">
							<router-link v-for="goods in item.list" :key="goods.id" :to="{path: 'goodsdet', query: {id: goods.gid}}">
								<div class="img_box">
									<img :src="url+goods.img"/>
								</div>
								<p v-text="goods.title"></p>
								<span v-text="goods.price"></span>
							</router-link>
						</div>
					</li>
				</ul>
				<p style="margin: 2rem 0;text-align: center;color: #A0A2A5;font-size: 1.4rem;">
					已经到底了~
				</p>
			</div>
		</div>
		
	</div>
</template>

<script>
	import { Toast,Indicator,MessageBox } from 'mint-ui';
	import {history,delHistory} from '../../api/api';
	export default {
		data(){
			return{
				url:'',
				list:[],
				is_show:false,
				
			}
		},
		methods:{
			deletes(){
				var ids=''
				for(var i=0;i<this.list.length;i++){
					if(this.list[i].check == true){
						for (var j=0;j<this.list[i].list.length;j++) {
							ids+=this.list[i].list[j].id+','
							
						}
					}
				}
				ids = ids.substr(0, ids.length - 1); 
				if(ids==""){
					Toast({message:'请选择您要删除的记录',position:'top'});
					return;
				}
				MessageBox({
				  	title: '是否删除',
				  	message: '是否删除选中的浏览记录？',
				  	showCancelButton: true
				}).then(action => {
					if(action=='confirm'){
						delHistory({ids:ids,token:this.getToken()}).then((res)=>{
							Toast({message:res.msg,position:'top'});
			            	if(res.code==200){
			            		this.getList();
			            	}
			            	console.log(this.ids);
		                })
						

					} 					
				});
				console.log(ids);
			},
			getList(){
				Indicator.open();
				history({token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.list=res.data;
	            	}
	            	this.is_show=true;
                })
	   		},
		},
		created(){
			var re=this.isLogin()
			this.url=this.host();
			if(re){
				this.getList()
			}
			
		},

	}
</script>

<style scoped="scoped">
	.record_view{
		padding-top: 5rem;
	}
	.record_list{
		background: #FFFFFF;
	}
	.recird_item{
		padding: 0 1.2rem 1.5rem;
		box-sizing: border-box;
	}
	.recird_item .item_title{
		width: 100%;
		height: 3.25rem;
		line-height: 3.25rem;
	}
	.recird_item .item_title label{
		color: #9C9C9C;
		padding-left: 2.5rem;
		font-size: 1.4rem;
		position: relative;
	}
	.item_title label i{
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 1.6rem;
		height: 1.6rem;
		background: url(../../assets/img/no-selected@2x.png);
		background-size: 100% 100%;
	}
	.item_title label input:checked + i{
		background: url(../../assets/img/selected_icon@2x.png);
		background-size: 100% 100%;
	}
	.item_content{
		box-sizing: border-box;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
	}
	.item_content a{
		width: 30%;
		margin-right: 5%;
		overflow: hidden;
	}
	.item_content a:nth-child(3n){
		margin-right: 0;
	}
	.item_content a .img_box{
		width: 100%;
	}
	.item_content a .img_box img{
		width: 100%;
	}
	.item_content a p{
		color: #3C3C3C;
		font-size: 1.4rem;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		line-height: 1.8rem;
	}
	.item_content a span{
		color: #FF3D3D;
		font-size: 1.4rem;
		line-height: 1.6rem;
	}
</style>


