<template>
	<div class="logistics_view">
		<mt-header class="head" fixed title="物流详情">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div v-show="is_show">
			<div v-for="(item,index) in goods" :class="index == goods.length-1?'goods_msg last':'goods_msg'">
				<router-link to="">
					<img :src="url+item.goods_img"/>
					<p v-text="item.goods_name"></p>
					<i class="argument">
						<i v-if="info.type_id==5">全新车</i>
						<i v-else-if="info.type_id==6">循环车A类</i>
						<i v-else-if="info.type_id==7">循环车B类</i>
						{{item.spec_key_name}}
					</i>
					<span>共{{item.goods_num}}件</span>
				</router-link>
			</div>
			<div class="logistics_msg_box">
				<div class="order_det">
					<div>
						<img src="../../assets/img/logistics_icon@2x.png"/>
						<p style="margin-bottom: 1rem;">物流公司：<span>{{info.express_company}}</span></p>
						<p>运单号：<span>{{info.logistics_code}}</span></p>
					</div>
				</div>
				<div class="address_box">
					[收货地址]：<span>{{info.address}}</span>
				</div>
				<ul class="logistics_msg">
					<li class="item" v-for="(item,index) in list">
						<div class="date"><p class="day">{{item.date}}</p><span class="time">{{item.time}}</span></div>
						<p class="logistics" v-text="item.AcceptStation"></p>
					</li>
					<li class="logistics_msg_hr"></li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {logistic} from '../../api/api';
	export default {
		data(){
			return{
				len:0,
				url:'',
				order_id:'',
				info:[],
				list:[],
				goods:[],
				is_show:false,
			}
		},
		methods:{
			getInfo(){
				Indicator.open();
				this.is_show=false
	            logistic({id:this.order_id,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.info=res.data.info
	            		this.list=res.data.express
	            		this.goods=res.data.goods
	            		this.len = this.list.length;
						if(this.len == 0 || this.len == 1){
							$(".logistics_msg_hr").hide();
						}else if(this.len == 2){
							$(".logistics_msg_hr").css("height","80%");
						}else{
							$(".logistics_msg_hr").css("height","95%");
						}
	            	}
	            	this.is_show=true;
                })

			},
		},
		created(){
			this.url=this.host()
	    	this.order_id = this.$route.query.id;
			this.getInfo()
			
		},	
	}
</script>

<style scoped="scoped">
	.argument {
	    color: #AEAEAE;
	    font-size: 1.4rem;
	    line-height: 1.6rem;
	}
	.logistics_view{
		padding-top: 5rem;
	}
	.goods_msg{
		height: 6.5rem;
		padding: 1.4rem 1.2rem;
		background: #FFFFFF;
	}
	.goods_msg.last{
		margin-bottom: 5.5rem;
	}
	.goods_msg a{
		display: block;
		height: 100%;
		position: relative;
		padding-left: 7.5rem;
		font-size: 1.4rem;
	}
	.goods_msg a img{
		width: 6.5rem;
		height: 6.5rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.goods_msg a p{
		color: #3C3C3C;
		line-height: 1.8rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.goods_msg a span{
		color: #AEAEAE;
		line-height: 1.8rem;
		position: absolute;
		left: 7.5rem;
		bottom: 0.1rem;
	}
	.logistics_msg_box{
		background: #FFFFFF;
		border:1px solid transparent;
	}
	.order_det{
		width: 90%;
		height: 12.5rem;
		margin: -5.5rem auto 0;
		background: url(../../assets/img/logistics_bg@2x.png);
		background-size: 100% 100%;
		position: relative;
	}
	.order_det div{
		width: 80%;
		height: 4.6rem;
		position: absolute;
		top: 3rem;
		left: 50%;
		transform: translateX(-50%);
		box-sizing: border-box;
		padding-left: 6rem;
		font-size: 1.5rem;
	}
	.order_det div img{
		width: 5.1rem;
		height: 4.6rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.order_det div p{
		color: #AEAEAE;
	}
	.order_det div p span{
		color: #3C3C3C;
	}
	.address_box{
		padding: 0 1.2rem 0 5.8rem;
		color: #949494;
		font-size: 1.4rem;
		line-height: 1.6rem;
		position: relative;
		margin-bottom: 3rem;
	}
	.address_box:before{
		content: "";
		position: absolute;
		top: 0;
		left: 3.8rem;
		width: 1.1rem;
		height: 1.5rem;
		background: url(../../assets/img/address_icon2@2x.png);
		background-size: 100% 100%;
	}
	.logistics_msg{
		padding-left: 1.2rem;
		position: relative;
		padding-right: 1.2rem;
	}
	.logistics_msg .item{
		margin-bottom: 4rem;
		color: #949494;
		font-size: 1.3rem;
		line-height: 1.6rem;
		position: relative;
	}
	.logistics_msg .item:first-child{
		color: #FF6784;
	}
	.logistics_msg .item:last-child{
		margin-bottom: 0;
	}
	.logistics_msg .item .date{
		position: absolute;
		left: 0;
		top: 0;
	}
	.logistics_msg .item .logistics{
		width: 100%;
		min-height: 2.5rem;
		box-sizing: border-box;
		padding-left: 10rem;
		position: relative;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}
	.logistics_msg .item .logistics:before{
		content: "";
		position: absolute;
		width: 2.2rem;
		height: 2.2rem;
		border-radius: 50%;
		top: -3px;
		left: 7rem;
		background: #FFFFFF url(../../assets/img/icon.png) center center no-repeat;
		background-size: 0.9rem 0.9rem;
		z-index: 10;
	}
	.logistics_msg .item:first-child .logistics:before{
		content: "";
		position: absolute;
		width: 2.2rem;
		height: 2.2rem;
		border-radius: 50%;
		top: 0;
		left: 7rem;
		background: #FFFFFF url(../../assets/img/node_icon@2x.png) center center no-repeat;
		background-size: 1.8rem 1.8rem;
		z-index: 10;
	}
	.logistics_msg .logistics_msg_hr{
		position: absolute;
		top: 50%;
		left: 9.25rem;
		transform: translateY(-50%);
		width: 1px;
		height: 95%;
		background-color: #E8E8E8;
	}
</style>


