<template>
	<div class="address_view">
		<mt-header class="head" fixed title="选择收货地址">
		  	<mt-button @click="Close" slot="left" class="back"></mt-button>
		</mt-header>
		<p v-if="list.length==0" class="empty">您的收货地址还是空空哒</p>
		<div class="address_item" v-for="(item,index) in list" ref="addr">
			<div class="address_item_top">
				<span class="userName">收货人：{{item.username}}</span><span class="phone" v-text="item.phone"></span>
			</div>
			<div class="address">
				<div v-show="item.is_check" class="default_box">默认</div>
				<div class="address_text" v-text="item.province+item.city+item.area+item.detail"></div>
			</div>
			<label><input :checked="selAddrId == index?true : false" style="display: none;" type="radio" name="radio" @click="SelAddr(index)" /><i></i></label>
		</div>
		<footer>
			<router-link to="/newaddress">添加新地址</router-link>
		</footer>
	</div>
</template>

<script>
	import { Toast,Indicator,MessageBox} from 'mint-ui';
	import {addrList} from '../../api/api';
	export default {
		data(){
			return{
				delAlert:false,
				selAddrId:0,
				list:[],
			}
		},
		methods:{
			SelAddr(i){
				this.selAddrId = i;
				var dz = this.list[this.selAddrId];
				this.$emit("selAdd",dz);
			},
			Close(){
				this.$emit("close");
			}
		},
		created(){
			var isLogin=this.isLogin();
			if(isLogin){
				Indicator.open();
	            addrList({token:this.getToken()}).then((res)=>{
	            	Indicator.close();
                    if(res.code==200){
                        this.list=res.data;
                    }
                })
			}
			
		},
	}
</script>

<style scoped="scoped">
	.address_item{
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		margin-bottom: 1rem;
		padding-left: 1.2rem;
		padding-right: 3rem;
		position: relative;
	}
	.address_item label{
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 1.5rem;
		width: 1.5rem;
		height: 1.5rem;
	}
	.address_item label i{
		display: block;
		width: 1.5rem;
		height: 1.5rem;
		background: url(../../assets/img/no-selected@2x.png);
		background-size: 100% 100%;
	}
	.address_item label input:checked + i{
		background: url(../../assets/img/selected_icon@2x.png);
		background-size: 100% 100%;
	}
	.address_item .address_item_top{
		height: 4.5rem;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding-right: 1.2rem;
		font-size: 1.6rem;
	}
	.address{
		display: flex;
		flex-direction: row;
		padding-right: 1.2rem;
		padding-bottom: 0.5rem;
		font-size: 1.4rem;
		line-height: 1.8rem;
		overflow: hidden;
	}
	.address .default_box{
		width: 4.6rem;
		height: 2.1rem;
		font-size: 1.2rem;
		margin-right: 1rem;
		border-radius: 1.25rem;
		background: #FFB853;
		color: #FFFFFF;
		text-align: center;
		line-height: 2.1rem;
	}
	.address .address_text{
		width: 85%;
		word-break: break-all;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.address_btn_box{
		height: 4.5rem;
		padding-right: 1.2rem;
		line-height: 4.5rem;
	}
	.address_btn_box .set_default{
		color: #FF6784;
		font-size: 1.4rem;
		float: left;
	}
	.address_btn_box .address_btn{
		float: right;
	}
	.address_btn_box .address_btn .edit,
	.address_btn_box .address_btn .del{
		display: inline-block;
		width: 1.5rem;
		height: 1.5rem;
		background: url(../../assets/img/edit_icon2@2x.png);
		background-size: 100% 100%;
	}
	.address_btn_box .address_btn .del{
		margin-left: 1.5rem;
		background: url(../../assets/img/delete_icon@2x.png);
		background-size: 100% 100%;
	}
	.address_view{
		padding-top: 5rem;
		padding-bottom: 5rem;
	}
	footer{
		position: fixed;
		width: 100%;
		height: 5rem;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 10;
		background: #bf0412;
	}
	footer a{
		display: block;
		width: 100%;
		height: 100%;
		line-height: 5rem;
		text-align: center;
		color: #FFFFFF;
		font-size: 1.7rem;
		letter-spacing: 0.2rem;
	}
</style>


