<template>
	<div class="bindingnum_view">
		<mt-header class="head" fixed title="绑定手机号码">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="submit()">完成</span>  
		</mt-header>
		<div class="bindingnum_img">
			<img src="../../assets/img/binding_icon@2x.png" />
			<p>完成绑定后，可以使用手机号登录，<br/>也可以通过手机号找回密码~</p>
		</div>
		<ul class="bindingnum_list">
			<li class="bindingnum_phone">手机号<input type="text" v-model="phone" value="" placeholder="请输入手机号" maxlength="11"/></li>
			<li class="bindingnum_code">验证码<input type="text" v-model="code" value="" placeholder="请输入验证码" maxlength="6"/><input @click="send()" type="button" value="获取验证码" v-if="!show"/><button class="custom" v-if="show">重新发送({{time}})秒</button></li>
			<li class="bindingnum_newpw">设置登录密码<input type="password" v-model="password" value="" placeholder="请设置登录密码" maxlength="20" /></li>
		</ul>
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {bindingPhone,sendCode} from '../../api/api';
	export default {
		data(){
			return{
				phone:'',
				code:'',
				password:'',
				time:60,
				show:false,
			}
		},
		methods:{
			send(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					return;
				}
				Indicator.open();
				sendCode({phone:this.phone,type:3}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.show=true;
	            		this.custom();
	            	}
                    Toast({message:res.msg,position:'top'});
                })
			},
			custom(){
				var closeTime =""
				closeTime=setInterval(() => {
                	this.time--
                	if(this.time<=0){
						this.time=60;
						this.show=false;
						clearInterval(closeTime);
					}
				}, 1000);
				
			},
			submit(){
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					return;
				}
				if(this.code==""){
					Toast({message:'请填写验证码',position:'top'});
					$('.verifyCode_input').addClass("on");
					return;

				}
				if(!this.regex('aaa',this.code,/^\d{6}$/)){
					Toast({message:'验证码格式错误',position:'top'});
					return;
				}
				if(this.password==""){
					Toast({message:'密码不能为空',position:'top'});
					$('.login_pwd_input').addClass("on");
					return;
				}
				Indicator.open();
	            bindingPhone({phone:this.phone,code:this.code,password:this.password,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
                    if(res.code==200){
                        setTimeout(() => {
                        	this.$router.replace({
								path: '/bindingsuc',
								query: {phone: this.phone} 
							});
						}, 2000);
                    }
                })
			}
		},
		created(){
			this.isLogin()
		},
	}
</script>

<style scoped="scoped">
	.bindingnum_view{
		padding-top: 5rem;
	}
	.bindingnum_list{
		background: #FFFFFF;
	}
	.bindingnum_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.bindingnum_list .bindingnum_phone input{
		margin-left: 1rem;
		border: none;
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		text-align: left;
	}
	.bindingnum_list .bindingnum_code input[type="text"]{
		margin-left: 1rem;
		width: 10rem;
		font-size: 1.5rem;
		height: 3rem;
		border: none;
		text-align: left;
	}
	.bindingnum_list .bindingnum_code input[type="button"],.bindingnum_list .custom{
		float: right;
		height: 3rem;
		border: none;
		background: none;
		font-size: 1.5rem;
		color: #FF6784;
	}
	.bindingnum_img{
		width: 100%;
		height: 20.85rem;
		text-align: center;
	}
	.bindingnum_img img{
		width: 8.3rem;
		height: 9.1rem;
		vertical-align: middle;
		margin-top: 3.05rem;	
	}
	.bindingnum_img p{
		margin-top: 2.15rem;
		font-size: 1.4rem;
		line-height: 1.8rem;
		color: #B4B4B4;
	}
	.bindingnum_list .bindingnum_newpw input{
		margin-left: 1rem;
		border: none;
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		text-align: left;
	}
</style>


