<template>
	<div class="order_details_view">
		<mt-header class="head" fixed title="确认订单">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div v-show="is_show">
			<div class="consignee" v-show="is_address">
				<a @click="popupVisible3 = true">
					<p class="consignee_msg">
						<span class="consignee_name">{{username}}</span>
						<span class="consignee_phone">{{phone}}</span>
					</p>
					<p class="consignee_address">{{address}}</p>
				</a>
				<i></i>
			</div>
			<div class="consignee" v-show="!is_address">
				<a @click="popupVisible3 = true">
					选择收货地址
				</a>
			</div>
			<div class="hr"></div>
			<div class="order_details">
				<h4>订单详情</h4>
				<div class="goods_details">
					<router-link :to="{path: 'goodsdet', query: {id: orders.id}}">
						<img :src="url+orders.img_thumb"/>
						<div>
							<p class="goods_name">{{orders.title}}</p>
							<p class="goods_arguments">{{orders.type}}&nbsp;{{orders.attr_name}}</p>
							<p class="price_num"><span>¥<i>{{orders.price}}</i></span> <span class="f-r">×<i>{{orders.num}}</i></span></p>
						</div>
					</router-link>
				</div>
			</div>
			<div class="other">
				<p>配送方式<span>{{distribution}}</span></p>
			</div>
			<div class="other">
				<p>支付方式<span>微信支付</span></p>
			</div>
			<div class="order_price">
				<ul>
					<li>商品金额<span>¥<i>{{totalPrice}}</i></span></li>
					<li>运费<span>¥<i>0</i></span></li>
					<li>订单总价<span>¥<i>{{Totals}}</i></span></li>
				</ul>
				<div class="pay_amount">
					共<span>{{totalNumber}}</span>件商品 小计：<span>¥<i>{{Totals}}</i></span>
				</div>
			</div>
			<div class="compact_box">
				<div class="compact" @click="popupVisible=true">
					<i :class="is_invoice?'active':''"></i>
					<div>
						<p>租赁合同</p>
					</div>
				</div>
			</div>
			<div class="good_btn">
				<div>实付款：<span style="color: #FF3D3D;">¥{{Totals}}</span></div>
				<button @click="submit()">{{orders.iscan_buy}}</button>
			</div>
			<mt-popup v-model="popupVisible3" position="right" class="mint-popup-3" :modal="false">
				<sel-addr @selAdd="selAdds" @close="Closes"></sel-addr>
		    </mt-popup>
		    <mt-popup v-model="popupVisible" position="right" class="mint-popupaa" :modal="false">
		    	<mt-header class="head" fixed title="租赁协议">
				  	<mt-button @click.native="QX" slot="left" class="back"></mt-button>
				</mt-header>
		    	<div class="agreement" v-html="agreement">
		    		<!--租赁协议内容后台传回-->
		    	</div>
		    	<div class="btnground">
		    		<mt-button @click.native="QR" size="small" type="primary">同意</mt-button>
	      			<mt-button @click.native="QX" size="small" type="default">不同意</mt-button>
		    	</div>
		    </mt-popup>
	    </div>
	</div>
</template>
<script src="http://res.wx.qq.com/open/js/jweixin-1.0.0.js"></script>
<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {confirmation,submitOrder} from '../../api/api';
	import selAddr from '@/views/goods/sel_address.vue'
	export default {
		data(){
			return{
				is_show:false,
				popupVisible: false,
				popupVisible3: false,
				serialNum:'22375688854',
				username:'',
				phone:'',
				address:'',
				orders:[],
				invoice:0,   //发票
				is_invoice:0,
				agreement:'',
				distribution:'快递 免邮',
				total:0,
				freight:0,        //运费
				id:[],
				num:[],
				itemid:[],
				url:'',
				address_id:'',
				is_address:false,//是否有收货地址
				act:0,
			}
		},
		components:{
			selAddr
		},
		computed:{
			totalPrice(){
				this.total = this.orders.price*this.orders.num;
				return `${this.total}`
			},
			Totals(){
				return `${this.total + this.freight}`
			},
			totalNumber(){
				var num = 0;
				num = parseInt(this.orders.num);
				return `${num}`
			}
		},
		methods:{
			QR(){
				this.popupVisible=false;
				this.is_invoice=1;
			},
			QX(){
				this.popupVisible=false;
				this.is_invoice = 0;
			},
			info(){
				Indicator.open();
	            confirmation({id:this.id,itemid:this.itemid,num:this.num,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		if(res.address){
	            			this.is_address=true;
	            			this.username=res.address.username;
		            		this.phone=res.address.phone;
		            		this.address_id=res.address.id;
		            		this.address=res.address.province+res.address.city+res.address.area+res.address.detail;
		            		this.agreement=res.agreement;
	            		}
	            		this.orders=res.data[0];
            			if(this.orders.type_id == 5){
            				this.orders.type = '全新车';
            			}else if(this.orders.type_id == 6){
            				this.orders.type = '循环车A类';
            			}else{
            				this.orders.type = '循环车B类';
            				this.distribution = '快递 邮费自付';
            			}

	            	}else{
                        Toast({message:res.msg,position:'top'});
                        return;
					}
	            	this.is_show = true;
                })
			},
			selAdds(data){
				this.is_address=true;
				this.popupVisible3=false;
				this.address_id=data.id;
				this.username=data.username;
        		this.phone=data.phone;
        		this.address=data.province+data.city+data.area+data.detail;
			},
			Closes(){
				this.popupVisible3=false;
			},
			submit(){
				var that=this
				if(!this.address_id){
					Toast({message:'请选择收货地址',position:'top'});
					return;
				}
				if(!this.invoice){
					Toast({message:'请选择购买方式',position:'top'});
					return;
				}
				if(this.invoice==1&&this.is_invoice==0){
					Toast({message:'请同意租赁协议',position:'top'});
					return;
				}
				Indicator.open();

	            submitOrder({id:this.id,type:this.invoice,itemid:this.itemid,num:this.num,address_id:this.address_id,act:this.act,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		WeixinJSBridge.invoke(
					       'getBrandWCPayRequest', {
					           "appId":res.data.appId,     //公众号名称，由商户传入
					           "timeStamp":res.data.timeStamp,         //时间戳，自1970年以来的秒数
					           "nonceStr":res.data.nonceStr, //随机串
					           "package":res.data.package,
					           "signType":res.data.signType,         //微信签名方式：
					           "paySign":res.data.paySign //微信签名
					       	},
					       	function(re){
					       		console.log(res.data.order_id);
					            if(re.err_msg == "get_brand_wcpay_request:ok" ) {
					            	Toast({message:'支付成功',position:'top'});
				                    that.$router.push({
										path: '/payment',
										query: {
							                id: res.data.order_id,
							            }
									});
					            }
					            if(re.err_msg == "get_brand_wcpay_request:cancel" ) {
					            	MessageBox({
										message: '确定取消支付吗？',
										showCancelButton: true,
										closeOnClickModal:false,
									}).then(action => {
										if(action=='confirm'){
											Toast({message:'支付已取消',position:'top'});
							            	that.$router.push({
												path: '/order',
												query: {
									                active: 1,
									            }
											});
										}
									});
					            }
					            if(re.err_msg == "get_brand_wcpay_request:fail" ) {
					                Toast({message:'支付失败',position:'top'});
					                that.$router.push({
										path: '/order',
										query: {
							                active: 1,
							            }
									});
					            }
					       	}
					    )
	            	}
                })
			},
		},
		watch:{
	    	popupVisible:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".order_details_view").css("position","fixed");
	    		}else{
	    			$(".order_details_view").css("position","relative");
	    		}
	    	},
	    	popupVisible3:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".order_details_view").css("position","fixed");
	    		}else{
	    			$(".order_details_view").css("position","relative");
	    		}
	    	}
	    },
		created(){
			var isLogin=this.isLogin();
			if(isLogin){
				if(typeof(this.$route.query.id)=='object'){
					this.id=this.$route.query.id;
					this.num=this.$route.query.num;
					this.itemid=this.$route.query.itemid;
					this.invoice=this.$route.query.buytype;
					console.log(this.$route.query.buytype);
				}else{
					this.id=[this.$route.query.id];
					this.num=[this.$route.query.num];
					this.itemid=[this.$route.query.itemid];
					this.invoice=this.$route.query.buytype;
				}
				if(this.$route.query.act){
					this.act=this.$route.query.act
				}
				this.url=this.host();
				this.info();
			}
		},
	}
</script>

