<template>
	<div class="class_view">
		<header class="head">
			<div class="class_select">
				<input type="search" v-model="keywords" maxlength="12" placeholder="请输入关键词" value="" />
			</div>
		</header>
		<section class="class_content" v-show="is_show">
			<ul  class="class_list">
				<li v-for="(item,index) in list" @click="selClass(index,item.id)" :class="selId == index?'class_item active':'class_item'"><i></i><a href="javascript:;">{{item.title}}</a></li>
			</ul>
			<div class="class_goods_list">
				<div style="background: #FFFFFF;height: 100%;position: relative;overflow: hidden;">
					<div class="class_goods_title">
						<span>全部商品</span>
					</div>
					<p class="empty" v-if="goodlist.length==0">暂无商品</p>
					<ul class="class_list_view" v-if="goodlist.length>0">
						<li v-for="item in goodlist" class="class_goods_item">
							<router-link :to="{path: 'goodsdet', query: {id: item.id}}">
								<div class="img_box">
                  					<img :src="url+item.img"/>
								</div>
								<p v-text="item.title"></p>
							</router-link>
						</li>
					</ul>
				</div>
			</div>

		</section>
	</div>
</template>
<script type="text/babel">
	import { Toast,Indicator } from 'mint-ui';
	import {catlist,catshops} from '../api/api';
  	export default {
	    data() {
	        return {
	            keywords:'',
	            list:[],
	            goodlist:[],
	            selId:0,
	            id:'',
	            is_show:false,
              	url:''
	        }
	    },
	    methods: {
			info(){
				$(".class_item").eq(this.selId).addClass("active").siblings().removeClass("active");
			},
			selClass(i,id){
				this.selId = i;
				this.info();
				this.id=id;
				this.getshops(id);
			},
			getshops(id){
				catshops({cid:id}).then((res)=>{
                	if(res.code==200){
                		this.goodlist=res.data
                	}
            	})
			}
	    },
	    mounted() {
//	    	搜索
	    	var that = this;
	    	$('.class_select input').on('keypress', function(e){
				var keycode = e.keyCode;
                var searchName = $(this).val();
                if(keycode=='13') {
                	e.preventDefault();
                	//搜索接口
                	that.keywords = '';
                	that.$router.push({
						path: '/search',
						query: {
			                key: searchName,
			            }
					});
                }
			});
	    },
	    created(){
        this.url=this.host();
	    	Indicator.open();
            catlist({}).then((res)=>{
            	Indicator.close();
            	
                if(res.code==200){
                	this.list=res.data
                	this.getshops(res.data[0].id);
                }

            });
            this.is_show=true;
		},
  	}
</script>
<style scoped="scoped">
	.class_view{
		height: 100vh;
		overflow: hidden;
		box-sizing: border-box;
		background: #F7F7F7;
		padding: 5rem 0 5rem;
	}
	.class_select{
		width: 80%;
		height: 3rem;
		box-sizing: border-box;
		padding-left: 5rem;
		padding-right: 1rem;
		border-radius: 1.4rem;
		margin: 1rem auto;
		position: relative;
		background:rgb(238,238,238) url(../assets/img/search@2x.png) 2rem center no-repeat;
		background-size: 2rem 2rem;
	}
	.class_select input{
		height: 3rem;
		width: 100%;
		box-sizing: border-box;
		border: none;
		background: transparent;
		float: left;
		font-size: 1.5rem;
	}
	.class_content{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: row;
	}
	.class_list{
		width: 26%;
		height: 100%;
		overflow: auto;
		background: #FFFFFF;
	}
	.class_list li{
		height: 5.5rem;
		line-height: 5.5rem;
		text-align: center;
		font-size: 1.5rem;
		position: relative;
	}
	.class_list li a{
		display: block;
		width: 100%;
		height: 100%;
		color: #3C3C3C;
	}
	.class_list li i{
		position: absolute;
		width: 0.2rem;
		height: 1.9rem;
		border-radius: 0.1rem;
		background: #FF6784;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		display: none;
	}
	.class_list li.active{
		background: #FFE6EB;
	}
	.class_list li.active a{
		color: #FF6784;
	}
	.class_list li.active i{
		display: block;
	}
	.class_goods_list{
		width: 74%;
		height: 100%;
		box-sizing: border-box;
		padding: 1.2rem;
	}
	.class_goods_title{
		width: 100%;
		height: 3.5rem;
		line-height: 3.5rem;
		font-size: 1.5rem;
		background: #FFFFFF;
		position: absolute;
		z-index: 10;
		top: -2px;
		left: 0;
	}
	.class_goods_title span{
		margin-left: 1.65rem;
		position: relative;
	}
	.class_goods_title span:after{
		content: "";
		position: absolute;
		width: 2rem;
		height: 0.1rem;
		background: #FF6784;
		left: 0;
		bottom: -0.4rem;
	}
	.class_list_view{
		width: 100%;
		height: 100%;
		background: #FFFFFF;
		box-sizing: border-box;
		padding-top: 3.5rem;
		position: absolute;
		top: 0;
		left: 0;
		overflow: auto;
		z-index: 9;
	}
	.class_list_view li{
		width: 50%;
		padding:0.75rem;
		box-sizing: border-box;
		text-align: center;
		font-size: 1.4rem;
		line-height: 1.25;
		float: left;
	}
	.class_list_view li:nth-child(even){
		border-right-color: transparent;
	}
	.class_list_view div img{
		width: 100%;
	}
	.class_list_view li p,
	.class_list_view span{
		color: #3C3C3C;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		display: block;
	}
	.class_list_view span{
		color: #FF3D3D;
	}
</style>



