<template>
	<div class="release_view">
		<mt-header class="head" fixed title="发布动态">
		  	<mt-button slot="left" @click="back()" class="back"></mt-button>
		  	<span slot="right" @click="submit" :style="{ color: subshow ? '' : '#aaa' }">{{subshow?'发布':'发布中'}}</span>
		</mt-header>
		<div class="user">
			<img :src="head_img"/>
			<span v-text="username"></span>
		</div>
		<div class="text_box">
			<textarea v-model="content" maxlength="120" placeholder="分享你的购物经历和经验吧~"></textarea>
			<div class="img">
				<img v-for="item in preview" :src="item" />
				<div class="uploadImg">
					<div class="uploadBtn">
						<label><input style="display: none;" accept="image/*" type="file" id="file" @change='onUpload' /></label>
					</div>
				</div>
			</div>			
		</div>
	</div>
</template>

<script>
	import EXIF from 'exif-js'; 
	import { Toast,Indicator } from 'mint-ui';
	import {push} from '../../api/api';
	export default {
		data(){
			return{
				head_img:'',
				username:'',
				content:'',
				img:[],
				url:'',
				upload:[],
				preview:[],
				subshow:true,//判断发布按钮是否可以点击
			}
		},
		computed:{
			textLength(){
				return `${this.content.length}`
			}
		},
		methods:{
			onUpload(e){
				var img_length=e.target.files.length;
				if(this.img.length+img_length>9){
					Toast({message:'最多只能上传9张哦',position:'top'});
					return;
				}
				this.imageLoad(e,0);
			},
			imageLoad(e,i){
				var th = this;
				var url=this.host()+'/v1/base64ToImg';
				var images = new Image();
				var canvas = document.createElement('canvas');
				var context = canvas.getContext('2d');
		        var reader = new FileReader();
		        var file = event.target.files[i];
		        var orientation='';
		        var degree=0;
		        EXIF.getData(file, function() {    
                   	orientation = EXIF.getTag(this, 'Orientation');  
                }); 
		        var images = new Image();
		        reader.readAsDataURL(file);
		        reader.onload = function() {
				    images.src = event.target.result;
				};
				images.onload = function () {
				    // 图片原始尺寸
				    var originWidth = this.width;
				    var originHeight = this.height;
				    // 最大尺寸限制
				    var maxWidth = 800, maxHeight = 800;
				    // 目标尺寸
				    var targetWidth = originWidth, targetHeight = originHeight;
				    // 图片尺寸超过800x800的限制
				    if (originWidth > maxWidth || originHeight > maxHeight) {
				        if (originWidth / originHeight > maxWidth / maxHeight) {
				            // 更宽，按照宽度限定尺寸
				            targetWidth = maxWidth;
				            targetHeight = Math.round(maxWidth * (originHeight / originWidth));
				        } else {
				            targetHeight = maxHeight;
				            targetWidth = Math.round(maxHeight * (originWidth / originHeight));
				        }
				    }
				    var width='';
				    var height='';
				    canvas.width =width= targetWidth;
				    canvas.height =height= targetHeight;
				    // 清除画布
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    //判断图片方向，重置canvas大小，确定旋转角度，iphone默认的是home键在右方的横屏拍摄方式
				    var degree=0;
					switch(orientation){
						//iphone横屏拍摄，此时home键在左侧
						case 3:
							degree=180;
							targetWidth=-width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在下方(正常拿手机的方向)
						case 6:
							canvas.width=height;
							canvas.height=width;
							degree=90;
							targetWidth=width;
							targetHeight=-height;
							break;
						//iphone竖屏拍摄，此时home键在上方
						case 8:
							canvas.width=height;
							canvas.height=width;
							degree=270;
							targetWidth=-width;
							targetHeight=height;
							break;
					}
					//使用canvas旋转校正
					context.rotate(degree*Math.PI/180);
				    // 图片压缩
				    context.drawImage(images, 0, 0, targetWidth, targetHeight);
				    var base = canvas.toDataURL();
				    th.img.push(base);
				    context.clearRect(0, 0, targetWidth, targetHeight);
				    canvas.width = 300;//重新设置画布大小
				    canvas.height = 300;
				    var previewImg = new Image();
				    previewImg.src = base;
				    previewImg.onload = function(){
				    	context.drawImage(previewImg, 0, 0, 300, 300);//重新把图片渲染进画布（压缩成300X300）
					    th.preview.push(canvas.toDataURL());
				    }
				    
				}   
			},
			submit(){
				if(this.content==""){
					Toast({message:'先说点什么呗',position:'top'});
					$('.text_box textarea').focus();
					return;
				}
				if(!this.subshow){
					return;
				}
				Indicator.open();
				this.subshow = false;
				var image='';
				if(this.img.length>0){
					for (var i = 0; i < this.img.length; i++) {
						image+=this.img[i]+'#####';
					}
				}
				image = image.substr(0, image.length - 1); 
				push({content:this.content,image:image,token:this.getToken()}).then((res)=>{
	            	if(res.code==200){
	            		Indicator.close();
	            		Toast({message:res.msg,position:'top'});
	            		setTimeout(() => {
	            			this.subshow = true;
                        	this.$router.go(-1);
						}, 2000);
	            	}
                })
			}	
		},
		created(){
			this.url=this.host()
			var isLogin=this.isLogin();
			if(isLogin){
				this.head_img=this.host()+Lockr.get('head_img');
				this.username=Lockr.get('username');
			}
		},
	}
</script>

<style scoped="scoped">
	.release_view{
		padding-top: 5rem;
	}
	.user{
		padding: 0.8rem 1.2rem;
		height: 4rem;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
	}
	.user img{
		width: 4rem;
		height: 4rem;
		border-radius: 50%;
		margin-right: 1rem;
		float: left;
	}
	.user span{
		display: inline-block;
		height: 4rem;
		line-height: 4rem;
	}
	.text_box{
		padding: 2rem 1.2rem 1.5rem;
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
	}
	.text_box textarea{
		display: block;
		border: none;
		box-sizing: border-box;
		width: 100%;
		height: 8rem;
		margin-bottom: 0.5rem;
		padding: 0 0.8rem;
		font-size: 1.5rem;
		border-bottom: 1px solid #E8E8E8;
	}
	.uploadImg{
		min-height: 7.5rem;
		position: relative;
	}
	.img{
		margin: 1rem 0.4rem 1rem 0;
		overflow: hidden;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
	}
	.img img{
		width: 30%;
		margin-bottom: 1rem;
		margin-right: 5%;
	}
	.img img:nth-child(3n){
		margin-right: 0;
	}
	.uploadImg .len{
		color: #B2B2B2;
		font-size: 1.5rem;
		position: absolute;
		bottom: 0;
		right: 0;
	}
	.uploadBtn{
		width: 7.5rem;
		height: 7.5rem;
		background: #EEEEEE;
		margin-bottom: 1rem;
	}
	.uploadBtn label{
		display: block;
		width: 7.5rem;
		height: 7.5rem;
		text-align: center;
		background: url(../../assets/img/photo_icon2@2x.png) center center no-repeat;
		background-size: 3.6rem 3.5rem;
	}
	.len{
		
	}
</style>


