<template>
	<div class="address_new_view">
		<mt-header class="address_head" title="收货地址">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="submit()">确定</span>
		</mt-header>
		<ul class="new_addr_msg">
			<li>
				收货人<input type="text" v-model="username" maxlength="10" placeholder="请输入名称" />
			</li>
			<li>
				联系电话<input type="number" v-model="phone" maxlength="11" placeholder="请输入手机号码" />
			</li>
			<li @click="selAddrShow">
				所在地区 <span class="addr_text"><i v-if="Addr !=''">{{Addr}}</i><i v-if="Addr ==''" style="color: #909090;">请选择</i><i class="next"></i></span>
			</li>
		</ul>
		<div class="detailed_address">
			<textarea v-model="detailed" placeholder="详细地址" maxlength="120"></textarea>
		</div>
		<div class="def">
			<label><input style="display: none;" v-model="defCheck" :checked="defCheck" type="checkbox" /><i></i>设置为默认地址</label>
		</div>
		
		<div v-show="addrBox" class="address_add_layer">
			<div class="choose_address">
				<div class="content">
					<div class="sel_address">请选择所在地区<i @click="close"></i></div>
					<mt-picker :slots="slots" @change="onValuesChange" :visibleItemCount="7"></mt-picker>
					<button @click="selAddress">确定</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { Toast,Indicator,Picker } from 'mint-ui';
	import {address} from '../../api/api';
	import { addr_arrs } from '../../../static/address.js'
	export default {
		data(){
			return{
				username:'',
				phone:'',
				Addr:'',
				detailed:'',
				address:'',//不是最终地址
				addrBox:false,
				defCheck:false,
				city:[],
				district:[],
				slots: [
			        {
			          flex: 1,
			          values: ["北京市", "天津市", "河北省", "河南省", "山西省", "山东省", "内蒙古自治区", "辽宁省", "吉林省", "黑龙江省", "上海市", "江苏省", "浙江省", "福建省", "江西省", "安徽省", "湖北省", "湖南省", "广东省", "广西壮族自治区", "海南省", "重庆市", "四川省", "贵州省", "云南省", "西藏自治区", "陕西省", "甘肃省", "青海省", "宁夏回族自治区", "新疆维吾尔自治区", "台湾省", "香港特别行政区", "澳门特别行政区"],
			          className: 'slot1',
			          textAlign: 'left'
			        },{
			          flex: 1,
			          values: [],
			          className: 'slot2',
			          textAlign: 'center'
			        },{
			          flex: 1,
			          values: [],
			          className: 'slot3',
			          textAlign: 'right'
			        }
			    ]
			}
		},
		methods:{
			info(){
				this.slots[1].values = this.city[0];
				this.slots[2].values = this.district[0][0];
			},
			onValuesChange(picker, values) {
				if(picker.getSlotValues(0) == null){
					return false;
				}
				var shen = values[0];
				var that = this;
				var shenInd = 0;
				picker.getSlotValues(0).forEach((item,i)=>{
					if(shen == item){
						shenInd = i;
						picker.setSlotValues(1,that.city[i]);
					}
				});
				if(picker.getSlotValues(1) == null){
					return false;
				}
				var shi = values[1];
				picker.getSlotValues(1).forEach((item,i)=>{
					if(shi == item){
						picker.setSlotValues(2,that.district[shenInd][i]);
					}
				})
				this.address = [picker.getSlotValue(0),picker.getSlotValue(1),picker.getSlotValue(2)];
		    },
		    close(){
				this.addrBox = false;
			},
			selAddrShow(){
		    	this.addrBox = true;
		    },
			selAddress(){
				this.Addr = "";
				this.address.forEach((item)=>{
					this.Addr+=item+' ';
				})
				this.Addr = this.Addr.substr(0,this.Addr.length-1);
				this.addrBox = false;
			},
			submit(){
				if(this.username==""){
					Toast({message:'收货人姓名不能为空',position:'top'});
					return;
				}
				if(this.phone==""){
					Toast({message:'手机号码不能为空',position:'top'});
					return;
				}
				if(!this.regex('phone',this.phone)){
					Toast({message:'手机号码格式错误',position:'top'});
					return;
				}
				if(this.Addr==""){
					Toast({message:'请选择所在地区',position:'top'});
					return;
				}
				if(this.detailed==""){
					Toast({message:'详细地址不能为空',position:'top'});
					return;
				}
				this.defCheck=this.defCheck?1:0;
				Indicator.open();
				address({username:this.username,phone:this.phone,detail:this.detail,province:this.address[0],city:this.address[1],area:this.address[2],detail:this.detailed,check:this.defCheck,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		setTimeout(() => {
                        	this.$router.go(-1);
						}, 2000);
	            	}
                })
			}
		},
		watch:{
	    	addrBox:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".address_add_layer").on("touchmove",function(event){
	    				event.preventDefault();
	    			});
	    		}
	    	}
	    },
		mounted(){
			this.info();
		},
		created(){
			this.isLogin();
			var addr = addr_arrs()
            this.city = addr.city;
            this.district = addr.district;
		},	
	}
</script>



