<template>
	<div id="view" class="goods_detail_view" v-show="is_show">
		<header class="head">
		  	<span @click="back()" class="back"></span>
		  	<div class="goods_head_tab">
		  		<span @click="Tabs(0)" class="on">商品</span>
		  		<span @click="Tabs(1)">参数</span>
		  		<span @click="popupshow = true">评价({{commentCount}})</span>
		  	</div>
		  	<span class="share" @click="share"></span>
		</header>
		<div style="overflow: hidden;perspective:1000px">
			<mt-swipe :auto="3000" @change="handleChange" style="height:22.3rem;">
		        <mt-swipe-item class="slide" v-for="(item,index) in imglist" :key="index"><img :src="url+'/v1/thumb?src='+url+item+'&w=414&h=248'"/></mt-swipe-item>
		   </mt-swipe>
		</div>
		<div class="goods_msg_box">
			<div class="goods_msg">
				<p class="goods_name" style="font-size: 2rem;">{{title}}&nbsp;<span v-if="type_id==7" style="color: #FF0000;">（免费）</span></p>
				<p v-if="type_id==5" class="goods_name" style="font-size: 1.4rem;">全新车</p>
				<p v-if="type_id==6" class="goods_name" style="font-size: 1.4rem;">整套全新布料+八成新循环车架+物理消毒</p>
				<p v-if="type_id==7" class="goods_name" style="font-size: 1.4rem;">八成新循环布料+八成新循环车架+物理消毒</p>
				<p class="goods_price">¥<span>{{price}}</span>
					<i :class="collection==0 ? 'no_collection':'sel_collection'" @click="Collection"></i>
				</p>
				<p class="market_price">市场价¥{{original_price}}</p>
				<ul>
					<li class="sales">租量:{{count}}</li>
					<li class="courie">快递:{{type_id==7?'邮费自付':'免运费'}}</li>
					<li class="inventory">21天无理由退换</li>
				</ul>
			</div>
			<div class="sel_arguments" @click="argBox = true">
				<p v-if="argColor!=''&&argNumber!=0&&argModel!=''">{{argModel}}  {{argColor}} 数量{{argNumber}}件</p>
				<span v-else>选择颜色分类</span><i></i>
			</div>
		</div>
		<div class="detail_box">
			<div v-if="Tab==0" class="detail" v-html="detail">
				<!--商品图片-->
			</div>
			<div id="arg" v-if="Tab==1" class="arguments_msg">
				<!--商品参数-->
				<ul>
					<li v-show="parameter.brands!=''">
						<span class="arg_msg_tit">品牌：</span>
						<span class="arg_msg_content">{{parameter.brands}}</span>
					</li>
					<li v-show="parameter.model!=''">
						<span class="arg_msg_tit">产品型号：</span>
						<span class="arg_msg_content">{{parameter.model}}</span>
					</li>
					<li v-show="parameter.color!=''">
						<span class="arg_msg_tit">颜色分类：</span>
						<span class="arg_msg_content">{{parameter.color}}</span>
					</li>
					<li v-show="parameter.use_age!=''">
						<span class="arg_msg_tit">适用年龄：</span>
						<span class="arg_msg_content">{{parameter.use_age}}</span>
					</li>
					<li v-show="parameter.bar_code!=''">
						<span class="arg_msg_tit">商品条形码：</span>
						<span class="arg_msg_content">{{parameter.bar_code}}</span>
					</li>
					<li v-show="parameter.parts!=''">
						<span class="arg_msg_tit">配件：</span>
						<span class="arg_msg_content">{{parameter.parts}}</span>
					</li>
					<li v-show="parameter.weight!=''">
						<span class="arg_msg_tit">重量：</span>
						<span class="arg_msg_content">{{parameter.weight}}</span>
					</li>
					<li v-show="parameter.bearing!=''">
						<span class="arg_msg_tit">承重：</span>
						<span class="arg_msg_content">{{parameter.bearing}}</span>
					</li>
				</ul>
			</div>
		</div>
		<mt-popup v-model="popupshow" position="right" class="mint-popup-3 comments" :modal="false">
			<p style="text-align: center;padding: 2rem;" v-if="evaluation.length == 0">暂无评论</p>
			<ul class="page-loadmore-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="100" v-else>
            	<li v-for="item in evaluation" class="page-loadmore-listitem evaluation_item">
            		<img class="headImg" :src="url+item.head_img"/>
            		<p class="user">{{item.nickname}} <i>{{item.time}}</i></p>
            		<p class="content" v-text="item.content"></p>
            		<div v-if="item.image.length != 0" class="img">
            			<div v-for="imgs in item.image" class="img_box">
            				<img :src="url+'/v1/thumb?src='+url+imgs+'&w=200&h=200'"/>
            			</div>
            		</div>
            	</li>
            </ul>
			<p v-show="loading" class="page-infinite-loading">
				<mt-spinner type="snake"></mt-spinner>加载中...
			</p>
	    </mt-popup>
		<!--参数选择框-->
		<div v-show="argBox" class="arguments_layer">
			<div class="arguments_box">
				<div class="arguments_head">
					<img :src="url+img_thumb"/>
					<div>
						<p class="arguments_price">¥<i>{{price}}</i></p>
						<p>库存：{{inventory > 0 ? inventory : '库存不足'}}</p>
						<p>请选择 颜色 数量</p>
					</div>
					<span @click="argBox=false;addShopCart=false;toLink=false"></span>
				</div>
				<div class="model">
					<p>交易类型</p>
					<ul>
						<li @click="choosebuytype">租赁</li>
					</ul>
				</div>
				<div class="color">
					<p>颜色</p>
					<ul>
						<li v-for="col in color" :itemid="col.itemid" @click="choose(col)">{{col.item_name}}</li>
					</ul>
				</div>
				<div class="number">
					<span>租赁数量</span>
					<div class="quantity">
						<button class="del" @click="delNum">-</button>
						<input type="number" v-model="argNumber" readonly="readonly" disabled="disabled" style="background: #FFFFFF;" />
						<button class="add" @click="addNum">+</button>
					</div>
				</div>
			</div>
		</div>
		<footer v-if="!addShopCart && !toLink" v-show="popupshow==false">
			<button class="shopcart" @click="Shopcart">加入购物车</button>
			<button v-if="inventory > 0" class="buy" @click="Submit">立即租赁</button>
			<button v-else class="buy" @click="Submit">立即预定</button>
		</footer>
		<footer v-else>
			<button class="affirm" @click="argChange">确认</button>
		</footer>
		<div v-show="shareLayer" class="share_layer" @click.stop="hideLayer"></div>
		<!--找不到商品-->
		<div v-if="id == ''" class="sold-out">
			该商品已下架
		</div>
	</div>
</template>
<script>
	import { Toast,MessageBox,Indicator} from 'mint-ui';
	import {show,attribute,shopComment,delCollection,addcart,addHistory} from '../../api/api';
	export default {
	    data() {
	        return {
	        	is_show:false,
	        	url:'',
	        	id:'',
	            loadingBox: false,
	            loading:false,
	            shareLayer:false,
	            title:'',
	            img_thumb:'',
	            detail:'',
	            imglist:[],
	            price:0,
	            buytype:1,
	            original_price:0,
	            collection:0, //是否收藏
	            count:0,
	            type_id:0,//商品类型 5:全新，6:A类，7:B类
	            inventory:16886, //库存
	            argBox:false, //参数框显示隐藏
	            Tab:0,  //控制tab切换
	            popupshow:false,//评论显示隐藏
	            argColor:'',
	            argModel:'',
	            itemid:'',
				argNumber:1,
	            color:[],
	            toLink:false,
	            addShopCart:false,//是否添加购物车
	            commentCount:0,//评价条数
	            page:1,//评价页码
	            totalpage:0,//评价总页数
	            evaluation:[], //评价 
	            parameter:'',
	            inventory:0,
	        };
	    },
	    methods: {
	    	Tabs(i){
	    		this.Tab = i;
	    	},
	    	handleChange(index){
				this.current = index+1;
			},
			Collection(){//收藏
				var act=this.collection?'del':'add';
				delCollection({id:this.id,act:act,token:this.getToken()}).then((res)=>{
					Toast({message:res.msg,position:'top'});
					if(res.code==200){
						this.collection = !this.collection;
					}
				})
			},
	    	choosebuytype(){//选择交易联系
    			this.buytype = 1;
    			this.argModel = '租赁'
	    		
	    	},
	    	choose(item){//选择颜色
    			this.price = item.price;
	    		this.argColor = item.item_name;
	    		this.itemid = item.itemid;
	    		this.inventory = item.stock;
				if(item.stock > 0 && item.stock < this.argNumber){
                    this.argNumber = item.stock;
				}else if(item.stock <= 0){
					this.argNumber = 1;
				}
	    	},
			addNum(){
				if(this.argNumber < this.inventory){
					this.argNumber++;
				}else if(this.inventory <= 0){
                    this.argNumber++;
				}else{
					Toast({message:'库存不足',position:'top'});
				}
	   			
	   		},
	   		delNum(){
	   			this.argNumber--;
	   			if(this.argNumber <= 0){
	   				this.argNumber=1;
	   			}
	   		},
			Shopcart(){//加入购物车
				if(this.argBox){
					if(this.argModel == ''){
		   				Toast({message:'请选择商品交易类型',position:'top'});
		   				return
		   			}else if(this.argColor == ''){
		   				Toast({message:'请选择商品颜色',position:'top'});
		   				return
		   			}else{
		   				addcart({id:this.id,buytype:this.buytype,num: this.argNumber,item_id:this.itemid,token:this.getToken()}).then((res)=>{
		   					Toast({message:res.msg,position:'top'});
		   					if(res.code==200){
		   						this.argBox = false;
		   					}
		               });
		   			}
				}else{
					if(this.argColor == '' || this.argModel == ''){
						this.argBox = true;
		   				this.addShopCart = true;
					}else{
						addcart({id:this.id,buytype:this.buytype,num: this.argNumber,item_id:this.itemid,token:this.getToken()}).then((res)=>{
		   					Toast({message:res.msg,position:'top'});
		   					if(res.code==200){
		   						this.addShopCart = false;
		   					}
		               });
					}
				}
			},
			Submit(){//立即购买
				if(this.argBox){
					if(this.argModel == ''){
		   				Toast({message:'请选择商品交易类型',position:'top'});
		   				return
		   			}else if(this.argColor == ''){
		   				Toast({message:'请选择商品颜色',position:'top'});
		   				return
		   			}else{
		   				this.$router.push({
							path: '/confirmOrder',
							query: {
				                id: this.id,
				                num: this.argNumber,
				                itemid:this.itemid,
				                buytype:this.buytype,
				            }
						});
		   			}
				}else{
					if(this.argColor == '' || this.argModel == ''){
		   				this.argBox = true;
		   				this.toLink = true;
		   			}else{
		   				this.$router.push({
							path: '/confirmOrder',
							query: {
				                id: this.id,
				                num: this.argNumber,
				                itemid:this.itemid,
				                buytype:this.buytype,
				            }
						});
		   			}
				}
	   		},
	   		argChange(){//选择类型确认按钮
	   			if(this.argColor != '' && this.argModel != '' && this.addShopCart){
	   				addcart({id:this.id,buytype:this.buytype,num: this.argNumber,item_id:this.itemid,token:this.getToken()}).then((res)=>{
	   					Toast({message:res.msg,position:'top'});
	   					if(res.code==200){
	   						this.shopcart=true;
	   					}
	               });
	               	this.argBox = false;
	   				this.addShopCart = false;
	   				return;
	   			}
	   			if(this.argColor != '' && this.argModel != '' && this.toLink){
	   				this.argBox = false;
	   				this.toLink = false;
   					this.$router.push({
						path: '/confirmOrder',
						query: {
			                id: this.id,
			                num: this.argNumber,
			                itemid:this.itemid,
			                buytype:this.buytype,
			            }
					});
					return;
	   			}else if(this.argModel == ''){
	   				Toast({message:'请选择商品交易类型',position:'top'});
	   			}else if(this.argColor == ''){
	   				Toast({message:'请选择商品颜色',position:'top'});
	   			}
	   		},
			loadMore() {
				if(this.page>=this.totalpage){
					return;
				}
				if(this.id == null){
					return
				}
				this.page++;
				this.loading = true;
				var that=this;
				shopComment({id:this.id,page:this.page}).then((res)=>{
	            	if(res.code==200){
	            		res.data.forEach(function(item){
	                        that.evaluation.push(item)
	                    })
	            	}
	            	this.loading = false;
            	})
	        },
	        share(){//分享
				this.shareLayer = true;
			},
			hideLayer(){
				this.shareLayer = false;
			},
			getComment(page){
				shopComment({id:this.id,page:page}).then((res)=>{
	            	if(res.code==200){
	            		this.commentCount=res.commentCount;
	            		this.evaluation=res.data;
	            		this.totalpage=res.total_page;
	            	}
            	})
			},
			history(){
				addHistory({id:this.id,token:this.getToken()}).then((res)=>{
	            	
                })
			},
			getInfo(){
				Indicator.open();
				if(Lockr.get('token')==undefined){
					var token='';
				}else{
					token=Lockr.get('token')
					this.history();
				}
	            show({id:this.id,token:token}).then((res)=>{
	            	Indicator.close();
	            	if(res.code==200){
	            		this.title=res.data.title;
		            	this.price=res.data.price;
		            	this.img_thumb=res.data.img_thumb;
		            	this.original_price=res.data.original_price;
		            	this.detail=res.data.description;
		            	this.imglist=res.data.imglist;
		            	this.total=res.data.imglist.length;
		            	this.collection=res.data.collect;
		            	this.count=res.data.count;
		            	this.type_id = res.data.type_id;
		            	this.parameter = res.data.parameter;
		            	if(res.data.is_exist == 2){
		            		this.id = '';
		            	}
	            	}
                })
                attribute({id:this.id}).then((res)=>{
	            	if(res.code==200){
	            		this.color=res.data;
	            		if(this.color.length>0){
	            			this.inventory = this.color[0].stock;
	            		}else{
	            			this.inventory = 0;
	            		}
	            	}
            	})
            	this.getComment(1);
            	this.is_show = true;
            	
			}
	    },
	    watch:{
	    	argBox:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".arguments_layer").on("touchmove",function(event){
	    				event.preventDefault();
	    			});
	    		}
	    	},
	    	popupshow:function(val,old){ //参数选择框弹出时阻止滚动事件
	    		if(val==true){
	    			$(".goods_detail_view").css("position","fixed");
	    		}else{
	    			$(".goods_detail_view").css("position","relative");
	    		}
	    	}
	    },
	    mounted() {
	    	var that = this;
			function imgWH(){
				$.each($(".mint-swipe"), function(i,item) {
					var w=0,h=0;
					w=$(item).width();
					h=parseInt(w*0.6);
					$(item).css("height",h+"px");
				});
				$.each($(".img_box"), function(i,item) {
					var w=0,h=0;
					w=$(item).width();
					h=w;
					$(item).find("img").css("width",w+"px").css("height",h+"px");
				});
			}
			imgWH();
			$(window).resize(function(){
				imgWH();
			});
			
			$(".goods_head_tab").on("click",".goods_head_tab span",function(e){//头部tab切换
				$(this).addClass("on").siblings().removeClass("on");
				if($(this).index() != 2){
					that.popupshow = false;
				}
			});
			$(".goods_detail_view").on("click",".model ul li",function(e){//交易类型
				$(this).addClass("active");
			});
			$(".goods_detail_view").on("click",".color ul li",function(e){//颜色
				$(this).addClass("active").siblings().removeClass("active");
			});
			
			//分享指示层
			$(".goods_detail_view").on("touchmove",".share_layer",function(){
				event.preventDefault();
			});
			//找不到商品
			$(".goods_detail_view").on("touchmove",".sold-out",function(){
				event.preventDefault();
			});
			$(".goods_detail_view").on("touchstart",".sold-out",function(){
				that.$router.push({
					path: '/index',
				});
			});
	    },
	    created(){
	    	// this.isLogin()
	    	this.url=this.host()
			this.id=this.$route.query.id;
			this.getInfo();
		},
	}
</script>

<style scoped="scoped">
