		width: 100%;
		padding-top: 5rem;
	}
	.goods_head_tab{
		color: #000000;
		height: 5rem;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		display: flex;
	}
	.goods_head_tab span{
		padding: 0 1rem;
		height: 5rem;
		white-space: nowrap;
		box-sizing: border-box;
		border-bottom: 2px solid #FFFFFF;
	}
	.goods_head_tab span.on{
		color: #FF0000;
		border-bottom: 2px solid #FF0000;
	}
	.share{
		position: absolute;
		top: 50%;
		right: 1.2rem;
		transform: translateY(-50%);
		width: 1.6rem;
		height: 1.6rem;
		background: url(../../assets/img/forward_icon_white@2x.png);
		background-size: 100% 100%;
	}
	.page-infinite-loading {
	    text-align: center;
	    height: 50px;
	    line-height: 50px;
	}
	.page-infinite-loading div{
	    display: inline-block;
	    vertical-align: middle;
	    margin-right: 5px;
	}  
	.evaluation_item{
		padding: 1.5rem 1.2rem 2rem 6.4rem;
		position: relative;
	}
	.evaluation_item .headImg{
		width: 4rem;
		height: 4rem;
		border-radius: 50%;
		position: absolute;
		left: 1.2rem;
		top: 1.8rem;
	}
	.evaluation_item .user{
		color: #3C3C3C;
		font-size: 1.5rem;
	}
	.evaluation_item .user i{
		color: #ADADAD;
		font-size: 1.3rem;
		float: right;
	}
	.evaluation_item .content{
		color: #717171;
		font-size: 1.5rem;
		line-height: 2.5rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
		overflow: hidden;
	}
	.evaluation_item .img {
		overflow: hidden;
	}
	.evaluation_item .img div{
		width: 30%;
		float: left;
		margin-right: 3%;
	}
	.evaluation_item .img div img{
		width: 100%;
		height: 100%;
	}

	.slide img{
		width: 100%;
		height: 100%;
	}
	.goods_detail_view{
		padding-bottom: 5.5rem;
	}
	.goods_msg_box{
		background: #FFFFFF;
		margin-bottom: 1rem;
		border-bottom: 1px solid #E8E8E8;
	}
	.goods_msg{
		padding: 1.2rem;
		border-bottom: 1px solid #E8E8E8;
	}
	.goods_msg .goods_name{
		font-size: 1.6rem;
		color: #393838;
		line-height: 3rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.goods_msg .goods_price{
		color: #FF3D3D;
		font-size: 1.3rem;
		position: relative;
		margin-bottom: 0.6rem;
	}
	.goods_msg .goods_price span{
		font-size: 2.2rem;
	}
	.goods_msg .goods_price i{
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
		width: 2.3rem;
		height: 2.1rem;
	}
	.goods_msg .goods_price .sel_collection{
		background: url(../../assets/img/collection_selected@2x.png) no-repeat;
		background-size: 2.3rem 2.1rem;
	}
	.goods_msg .goods_price .no_collection{
		background: url(../../assets/img/collection_default1@2x.png) no-repeat;
		background-size: 2.3rem 2.1rem;
	}
	.goods_msg .market_price{
		color: #A1A1A1;
		font-size: 1.4rem;
		margin-bottom: 1.2rem;
