	.goods_msg ul{
		overflow: hidden;
	}
	.goods_msg ul li{
		float: left;
		width: 50%;
		box-sizing: border-box;
		height: 2.1rem;
		line-height: 2.1rem;
		padding-left: 2.9rem;
		color: #A1A1A1;
		font-size: 1.4rem;
		margin-bottom: 1rem;
	}
	.goods_msg ul li:last-child{
		margin-bottom: 0;
	}
	.goods_msg ul li.sales{
		background: url(../../assets/img/sales_volume_icon@2x.png) left center no-repeat;
		background-size: 2.1rem 2.1rem;
	}
	.goods_msg ul li.courie{
		background: url(../../assets/img/express_icon@2x.png) left center no-repeat;
		background-size: 2.1rem 2.1rem;
	}
	.goods_msg ul li.inventory{
		background: url(../../assets/img/stock_icon@2x.png) left center no-repeat;
		background-size: 2.1rem 2.1rem;
	}
	.sel_arguments{
		height: 4rem;
		padding: 0 1.2rem;
		color: #393838;
		font-size: 1.6rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.sel_arguments i{
		display: inline-block;
		width: 0.7rem;
		height: 1.3rem;
		background: url(../../assets/img/next@2x.png);
		background-size: 100% 100%;
	}
	/*参数选择*/
	.arguments_layer{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.3);
		z-index: 1001;
	}
	.arguments_box{
		width: 100%;
		box-sizing: border-box;
		background: #FFFFFF;
		position: absolute;
		bottom: 5rem;
		left: 0;
		right: 0;
	}
	.arguments_head{
		height: 8rem;
		padding-top: 1rem;
		position: relative;
	}
	.arguments_head img{
		width: 8rem;
		height: 8rem;
		border-radius: 1rem;
		position: absolute;
		left: 1.2rem;
		top: -1rem;
		border: 1px solid #B2B2B2;
		background: #FFFFFF;
		overflow: hidden;
	}
	.arguments_head div{
		padding-left: 12rem;
		height: 100%;
		box-sizing: border-box;
		padding-bottom: 2rem;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.arguments_head div .arguments_price{
		color: #FF3D3D;
		font-size: 1.9rem;
	}
	.arguments_head div .inventory{
		font-size: 1.4rem;
		color: #3C3C3C;
	}
	.arguments_head span{
		position: absolute;
		top: 1.5rem;
		right: 1.7rem;
		width: 2.2rem;
		height: 2.2rem;
		background: url(../../assets/img/delete_icon2@2x.png);
		background-size: 100% 100%;
	}
	.color,
	.model{
		padding: 0 1.2rem 1.5rem;
		border-bottom: 1px solid #F1F1F1;
	}
	.color p,
	.model p{
		height: 3rem;
		line-height: 3rem;
		font-size: 1.5rem;
		color: #525252;
	}
	.color ul,
	.model ul{
		overflow: hidden;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
	}
	.color ul li,
	.model ul li{
		float: left;
		width: 21%;
		height: 2.5rem;
		border: 1px solid #A1A1A1;
		font-size: 1.4rem;
		line-height: 2.6rem;
		text-align: center;
		margin: 1rem 5% 0 0;
		color: #3C3C3C;
	}
	.color ul li.active,
	.model ul li.active{
		border: 1px solid #d53a3a;
		background: url(../../assets/img/check.png) right bottom no-repeat;
		background-size: 1.6rem 1.4rem;
	}
	.color ul li:nth-child(4n),
	.model ul li:nth-child(4n){
		margin-right: 0;
	}
	.number{
		height: 4rem;
		line-height: 4rem;
		padding:1.2rem;
		display: flex;
		justify-content: space-between;
	}
	.quantity{
		width: 12rem;
		height: 3rem;
		line-height: 3rem;
		border-radius: 0.1rem;
		border: 1px solid #E8E8E8;
		display: flex;
		justify-content: space-between;
	}
	.quantity button{
		width: 30%;
		height: 100%;
		border: none;
		background: none;
		font-size: 2rem;
		font-weight: bold;
		color: #C6C6C6;
	}
	.quantity .del{
		border-right: 1px solid #E8E8E8;
	}
	.quantity .add{
		border-left: 1px solid #E8E8E8;
	}
	.quantity input{
		width: 30%;
		height: 100%;
		text-align: center;
		border: none;
		font-size: 1.5rem;
	}
	.arguments_box .submit{
		width: 100%;
		height: 4.5rem;
		border: none;
		background: url(../../assets/img/determine_button@2x.png);
		background-size: 100% 100%;
		color: #FFFFFF;
		font-size: 1.7rem;
	}
	/*详情*/
	.detail_box{
		background: #FFFFFF;
	}
	.detail{
		padding:1.5rem 1.2rem;
		background: #FFFFFF;
		width: 100%;
		box-sizing: border-box;
		overflow: hidden;
	}
	.detail_box .detail img{
		max-width: 100%;
	}
	.arguments_msg{
		background: #FFFFFF;
		padding: 1rem 1.2rem 0;
	}
	.arguments_msg ul li{
		line-height: 3rem;
		border-bottom: 1px solid #E8E8E8;
		display: flex;
	}
	.arguments_msg ul li:last-child{
		border-bottom: none;
	}
	.arguments_msg ul li .arg_msg_tit{
		flex: 1;
		padding-left: 2rem;
		color: #A1A1A1;
	}
	.arguments_msg ul li .arg_msg_content{
		flex: 2;
	}
	footer{
		position: fixed;
		z-index: 1002;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 5rem;
		background: #FFFFFF;
		display: flex;
	}
	footer .shopcart{
		width: 50%;
		border: none;
		height: 100%;
		color: #FFFFFF;
		background: #fea03b;
		font-size: 1.7rem;
	}
	footer .buy{
		width: 50%;
		border: none;
		height: 100%;
		color: #FFFFFF;
		background: #bf0412;
		font-size: 1.7rem;
	}
	footer .affirm{
		width: 100%;
		border: none;
		height: 100%;
		color: #FFFFFF;
		background: #fea03b;
		font-size: 1.7rem;
	}
	/*评论窗口*/
	.mint-popup-3{
		width: 100%;
		height: 100%;
		background: #FFFFFF;
		box-sizing: border-box;
		padding-top: 5rem;
		position: fixed;
		z-index: 999!important;
		overflow: hidden;
	}
	.page-loadmore-list{
		height: 100%;
		overflow: auto;
	}
	.sold-out{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		line-height: 20rem;
		background: rgba(255,255,255,0.8);
		z-index: 1100;
		font-size: 3rem;
		color: #A1A1A1;
		font-weight: bold;
		white-space: nowrap;
		text-align: center;
	}
</style>



