<template>
	<div class="resetC_view">
		<mt-header class="head" fixed title="密码重置">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		  	<span slot="right" @click="submit()">完成</span>  
		</mt-header>
		<ul class="resetC_list">
			<li class="reset_oldpw">旧密码<input type="password" v-model="oldpassword" value="" placeholder="请输入旧登录密码" /></li>
			<li class="reset_newpw">设置新密码<input type="password" v-model="password" value="" placeholder="请输入新密码" /></li>
			<li class="reset_verifypw">确认新密码<input type="password" v-model="repassword" value="" placeholder="请再次确认新密码" /></li>
		</ul>
	</div>
</template>

<script>
	import { Toast,Indicator } from 'mint-ui';
	import {resetPassword} from '../../api/api';
	export default {
		data(){
			return{
				oldpassword : '',
				password : '',
				repassword : ''
			}
		},
		methods:{
			submit(){
				if(this.oldpassword==""){
					Toast({message:'请输入旧登录密码',position:'top'});
					return;
				}
				if(this.password==""){
					Toast({message:'请输入新密码',position:'top'});
					return;
				}
				if(this.repassword==""){
					Toast({message:'请输入确认密码',position:'top'});
					return;
				}
				if(this.password!==this.repassword){
					Toast({message:'2次密码不一致',position:'top'});
					return;
				}
				Indicator.open();
				resetPassword({oldpassword:this.oldpassword,password:this.password,repassword:this.repassword,token:this.getToken()}).then((res)=>{
	            	Indicator.close();
	            	Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		setTimeout(() => {
                        	this.$router.replace({
								path: '/options'
							});
						}, 2000);
	            	}
	            	
                    
                })
			}
		},
		created(){
			this.isLogin()
		},	
	}
</script>

<style>
	.resetC_view{
		padding-top: 6rem;
	}
	.resetC_list{
		background: #FFFFFF;
	}
	.resetC_list li{
		height: 3rem;
		line-height: 3rem;
		position: relative;
		border-bottom: 1px solid #E8E8E8;
		color: #3C3C3C;
		font-size: 1.5rem;
		padding: 1rem 1.2rem;
	}
	.resetC_list .reset_oldpw input{
		border: none;
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		float: right;
		text-align: right;
	}
	.resetC_list .reset_newpw input{
		border: none;
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		float: right;
		text-align: right;
	}
	.resetC_list .reset_verifypw input{
		border: none;
		width: 15rem;
		font-size: 1.5rem;
		height: 3rem;
		float: right;
		text-align: right;
		
	}
</style>


