<template>
	<div class="bindingsuc_view">
		<mt-header class="head" fixed title="绑定手机号码">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<div class="bindingsuc_img">
			<img src="../../assets/img/bindings_icon@2x.png" />
			<p style="margin-top: 2.15rem; font-size: 1.4rem;color: #B4B4B4;">已完成手机绑定</p>
			<p style="margin-top: 1.45rem; font-size: 1.4rem;">{{phone}}</p>
			<div class="changenumbtn"><router-link to="/changenum" slot="left"><p>更换绑定手机号</p></router-link></div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				phone : '',
			}
		},
		methods:{
			
		},
		mounted(){
			this.phone=this.$route.query.phone
			Lockr.set('phone',this.phone);
		},
		created(){
			this.isLogin()
		}
	}
</script>

<style scoped="scoped">
	.bindingsuc_view{
		padding-top: 6rem;
	}
	.bindingsuc_img{
		width: 100%;
		height:100%;
		text-align: center;
	}
	.bindingsuc_img img{
		width: 8.3rem;
		height: 9.1rem;
		vertical-align: middle;
		margin-top: 7.05rem;	
	}
	.changenumbtn{
		width:80%;
		height:4.8rem;
		border-radius:2.4rem;
		border: 0.05rem solid #FF6784;
		margin: 8rem auto;
		text-align: center;
		line-height: 4.8rem;
	}
	.changenumbtn p{
		color: #FF6784;
		font-size: 1.7rem;
		text-align: center;
	}
</style>


