<template>
	<div class="message_view">
		<mt-header class="head" fixed title="消息">
		  	<mt-button @click="back()" class="back" slot="left"></mt-button>
		  	<span class="emptyMsg" slot="right" @click="empty">清空</span>
		</mt-header>
		<ul class="massage_list" v-show="is_show">
			<p class="empty" v-if="list.length==0">暂无新消息</p>
			<li class="massage_item" v-for="(item,index) in list" v-if="list.length>0">
				<router-link :to="{path: 'topicDet', query: {id: item.push_id}}">
					<div class="massage_content">
						<img class="headImg" :src="url+item.head_img"/>
						<p class="name" v-text="item.nickname"></p>
						<p class="content" v-text="item.content"></p>
						<p class="time" v-text="item.create_time"></p>
						<img class="cp_img" v-if="item.image" :src="url+'/v1/thumb?src='+url+item.image+'&w=200&h=200'"/>
					</div>
				</router-link>
			</li>
		</ul>
	</div>	
</template>

<script>
	import { Indicator,MessageBox,Toast} from 'mint-ui';
	import {pushMsg,pushMsgClean} from '../../api/api';
	export default {
		data(){
			return{
				message:[],
				is_show:false,
				url:'',
				list:[],
			}
		},
		methods:{
			empty(){
				pushMsgClean({token:this.getToken()}).then((res)=>{
	                Toast({message:res.msg,position:'top'});
	            	if(res.code==200){
	            		this.list = [];
	            	}
	            })
			},
			getList(){
	            Indicator.open();
	            pushMsg({token:this.getToken()}).then((res)=>{
	                Indicator.close()
	                this.is_show=true;
	                if(res.code==200){
	                    this.list = res.data;
	                }else{
	                    this.$message({
	                        message: '请求失败',
	                        type: 'error'
	                    });
	                }
	            })
	        },
		},
		created(){
			this.isLogin();
			this.url=this.host()
			this.getList();
		},	
	}
</script>

<style scoped="scoped">
	.message_view{
		padding-top: 5rem;
	}
	.massage_item{
		background: #FFFFFF;
		border-bottom: 1px solid #E8E8E8;
		padding: 1rem 1.2rem;
	}
	.massage_item a{
		display: block;
		width: 100%;
		height: 100%;
	}
	.massage_item a .massage_content{
		padding-left: 5rem;
		padding-right: 6.5rem;
		min-height: 6rem;
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.massage_item a .massage_content .headImg{
		width: 4rem;
		height: 4rem;
		position: absolute;
		left: 0;
		top: 0;
		border-radius: 50%;
	}
	.massage_item a .massage_content .name{
		color: #3C3C3C;
		font-size: 1.5rem;
	}
	.massage_item a .massage_content .content{
		color: #FF6784;
		font-size: 1.3rem;
		line-height: 1.6rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.massage_item a .massage_content .time{
		color: #B1B1B1;
		font-size: 1.3rem;
	}
	.massage_item a .massage_content .cp_img{
		position: absolute;
		width: 6rem;
		height: 6rem;
		top: 0;
		right: 0;
	}
</style>


