<template>
	<div class="evaluation_view">
		<mt-header class="head" fixed title="我的评价">
		  	<mt-button @click="back()" slot="left" class="back"></mt-button>
		</mt-header>
		<p v-if="list.lenght==0" class="empty">暂无数据</p>
		<div class="evaluation_item" v-for="(item,i) in list" v-if="item.gid != null" v-show="orderID == 0 || item.order_id == orderID">
			<div class="user_msg">
				<dl>
					<dt><img :src="url+item.head_img"/></dt>
					<dd>
						<p v-text="item.nickname"></p>
						<span v-text="item.time"></span>
					</dd>
				</dl>
			</div>
			<div class="evaluation_content" v-text="item.content"></div>
			<div class="evaluation_img" v-show="item.image !='' || item.image > 0">
				<img v-for="img in item.image" :src="url+img"/>
			</div>
			<div class="evaluation_goods">
				<router-link :to="{path: 'goodsdet', query: {id: item.gid}}">
					<div class="goods_img">
						<img :src="url+item.img"/>
					</div>
					<div class="goods_msg">
						<p v-text="item.title"></p>
						<p v-text="item.type"></p>
						<span v-text="item.spec_key_name"></span>
					</div>
				</router-link>
			</div>
		</div>

			
	</div>
</template>

<script>
import { Indicator,MessageBox} from 'mint-ui';
import {comment} from '../../api/api';
	export default {
		data(){
			return{
				url:'',
				list: [],
				orderID: 0,
			}
		},
	    methods: {
	        getList(){
	            Indicator.open();
	            comment({page:1,token:this.getToken()}).then((res)=>{
	                Indicator.close()
	                if(res.code==200){
	                    this.list = res.data;
	                }else{
	                    this.$message({
	                        message: '请求失败',
	                        type: 'error'
	                    });
	                }
	            })
	        },

	    },
		created(){
			this.url=this.host();
			this.isLogin();
			this.getList();
			if(this.$route.query.order_id != undefined){
			    this.orderID = this.$route.query.order_id;
                console.log('查看订单ID为'+this.orderID+'的评价');
			}else{
                console.log('查看全部评价')
			    this.orderID = 0;
			}
		},
	}
</script>

<style scoped="scoped">
	.more{overflow: scroll;}
	.evaluation_view{
		padding-top:5rem
	}
	.evaluation_item{
		background: #FFFFFF;
		padding: 1.2rem 1.2rem 2.5rem;
		margin-bottom: 1rem;
		border-bottom: 1px solid #E8E8E8;
	}
	.user_msg dl{
		padding-bottom: 1.2rem;
	}
	.user_msg dl dt{
		float: left;
		width: 3.7rem;
		height: 3.7rem;
		margin-right: 1.1rem;
		border-radius: 50%;
		overflow: hidden;
	}
	.user_msg dl dt img{
		width: 100%;
		height: 100%;
	}
	.user_msg dl dd p{
		color: #3C3C3C;
		font-size: 1.5rem;
		line-height: 2;
	}
	.user_msg dl dd span{
		color: #b4b4b4;
		font-size: 1.3rem;
	}
	.evaluation_content{
		font-size: 1.5rem;
		color: #3C3C3C;
		line-height: 2rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		margin-bottom: 0.5rem;
	}
	.evaluation_img{
		display: flex;
		padding: 1rem 0;
		flex-wrap: wrap;
	}
	.evaluation_img img{
		width: 28vw;
		height: 28vw;
		margin-bottom: 1rem;
		margin-right: 4%;
	}
	.evaluation_img img:nth-child(3n){
		margin-right: 0;
	}
	.evaluation_goods{
		background: #F2F2F2;
		position: relative;
	}
	.evaluation_goods .goods_img{
		width: 8rem;
		height: 8rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.evaluation_goods .goods_img img{
		width: 100%;
		height: 100%;
	}
	.evaluation_goods .goods_msg{
		width: 100%;
		height: 8rem;
		box-sizing: border-box;
		padding: 1rem 1.2rem 1rem 9.2rem;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}
	.evaluation_goods .goods_msg p{
		color: #3C3C3C;
		font-size: 1.4rem;
		line-height: 1.8rem;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	.evaluation_goods .goods_msg span{
		color: #AEAEAE;
		font-size: 1.4rem;
	}

</style>
<style>
	.page-infinite-loading {
	    text-align: center;
	    height: 50px;
	    line-height: 50px;
	}
	.page-infinite-loading div{
	    display: inline-block;
	    vertical-align: middle;
	    margin-right: 5px;
	}
</style>


